import React from 'react';
import { backendUrl } from '../../../../../../../config/config';

import './addBlog.scss';

class AddBlog extends React.Component {
    render() {
        return (
            <>
            {this.props.show && 
            <>
                <div 
                    className="modal-block-backdrop" 
                    onClick={this.props.modalClosed}
                />
                <div className="add-good-in-order" style={{
                    display: 'flex',
                    alignItems: 'center',
                    flexDirection: 'column'
                }}>
                    <h2>
                        {'   :'}
                    </h2>
                    <div className="add-blog-images">
                        {this.props.posts.map(el => 
                            <img
                                onClick={() => this.props.onSelectImage(el)}
                                className='add-blog-img'
                                width={150} 
                                src={backendUrl + '/' + el.image} 
                            />
                        )}
                    </div>
                </div>
            </>
            }
            </>
        );
    }
}

export default AddBlog;