import { CircularProgress } from '@material-ui/core';
import axios from 'axios';
import React from 'react';
import Button from '@material-ui/core/Button';
import { connect } from 'react-redux';
import { backendUrl } from '../../../../config/config';
import ErrorComponent from '../../../Errors/Error/Error';

const inputStyles = {
    width: '90%',
    minHeight: '200px',
    height: '25px',
    border: '1px solid #d1d6da',
    borderRadius: '4px',
    color: '#555555',
    backgroundColor: '#fff',
    lineHeight: 1.428571429,
    fontSize: '16px',
    overflow: 'auto',
    boxShadow: 'inset 0 1px 1px rgba(0,0,0,0.075)',
    resize: 'vertical',
    padding: '8px'
};

class PageText extends React.Component {

    state = {
        loading: false,
        error: false,
        textArr: null,
        pageId: null
    }

    componentDidMount() {
        this.setState({
            loading: true,
            error: false
        });

            axios.get(backendUrl + `/admin/text/name/${this.props.location.state.fetch}`, {
                headers: {
                    'Authorization': `Bearer ${this.props.token}`
                }
            })
            .then(res => {
                console.log(res);
                let array = [];
                for (let i = 1; i <= 6; i++ ) {
                    array.push(res.data[`abz${i}`]);
                }
                this.setState({
                    loading: false,
                    error: false,
                    textArr: array,
                    pageId: res.data._id
                });
            })
            .catch(err => {
                console.log(err, err.response);
                this.setState({
                    loading: false,
                    error: true
                });
            });
    }

    onChangeTextArea = (event, index) => {
        let newTextArr = [ ...this.state.textArr ];
        newTextArr[index] = event.target.value;
        this.setState({
            textArr: newTextArr
        });
    }

    onSaveText = () => {
        this.setState({ loading: true });

        const newObj = {};
        const textArr = this.state.textArr;
        textArr.forEach((el, index) => {
            newObj[`abz${index + 1}`] = el;
        });

        axios.put(backendUrl + `/admin/text/id/${this.state.pageId}`, newObj, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res);
            window.location.reload();
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({ error: true, loading: false });
        });
    }

    render() {

        const {
            title
        } = this.props.location.state;

        const {
            loading,
            error,
            textArr
        } = this.state;

        return (
            <div style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                padding: '40px 0'
            }}>
                {loading && !error && <CircularProgress />}
                {!loading && error && <ErrorComponent />}
                {!loading && !error && textArr &&
                    <>
                        <h3>{`   ${title}`}</h3>
                        {textArr.map((el, index) => (
                            <>
                                <p>{`   ${index + 1}`}</p>
                                <textarea 
                                value={el}
                                onChange={(e) => this.onChangeTextArea(e, index)}
                                style={inputStyles} 
                                />
                            </>
                        ))}
                        <Button
                            color='primary'
                            variant="contained"
                            style={{
                                margin: '20px 0'
                            }}
                            onClick={this.onSaveText}
                        >
                            {''}
                        </Button>
                    </>
                }
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    token: state.auth.token
})

export default connect(mapStateToProps)(PageText);