/*
array = [
    {
        ...
        field: field value
    }, 
    {
        ...
        field
    },
    ...
]
*/

export const sortStrings = (field, array, method) => {
    let result = array;
    result = result.sort((a, b) => a[field] > b[field] ? 1 : -1);
    if (method === 'asc') {
        return result;
    } else {
        return result.reverse();
    }
};

export const sortNumbers = (field, array, method) => {
    let result = array;
    result = result.sort((a, b) => Number(a[field]) - Number(b[field]))
    if (method === 'asc') {
        return result;
    } else {
        return result.reverse();
    }
}