import React from 'react';
import axios from 'axios';
import { connect } from 'react-redux';
import { backendUrl } from '../../../../config/config';
import { withStyles } from '@material-ui/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

import Seo from '../../../SEO/SEO';

import './EditCategory.scss';

const inputStyles = {
    width: '90%',
    minHeight: '40px',
    height: '25px',
    border: '1px solid #d1d6da',
    borderRadius: '4px',
    color: '#555555',
    backgroundColor: '#fff',
    lineHeight: 1.428571429,
    fontSize: '16px',
    overflow: 'auto',
    boxShadow: 'inset 0 1px 1px rgba(0,0,0,0.075)',
    resize: 'vertical',
    padding: '8px'
};

const style = theme => ({
    element: {
        margin: '20px 0'
    },
    button: {
        marginTop: '25px'
    },
    cancelButton: {
        marginTop: '10px'
    }
});

class EditCategory extends React.Component {

    state = {
        error: false,
        loading: false,
        form: {
            nameRU: {
                value: '',
                valid: true,
                touched: false
              },
              nameUA: {
                value: '',
                valid: true,
                touched: false
              },
              route: {
                value: '',
                valid: true,
                touched: false
              },
              alt: {
                  value: '',
                  valid: true,
                  touched: false
              },
              article: {
                value: '',
                valid: true,
                touched: false,
             },
        },
        seo: {
            title: '',
            description: '',
            keywords: '',
            image: ''
        },    
        seo_dp:{
            title: '',
            description: '',
            keywords: ''
        },
        seo_zp:{
            title: '',
            description: '',
            keywords: ''
        }, 
        seo_image: '',
        seoFile: null,
        seoImagePreviewUrl: null,
        formIsValid: true,
        file: null,
        imagePreviewUrl: null,
        categoryId: null
    }

    componentDidMount() {
        this.setState({ loading: true });

        const categoryId = this.props.match.params.id;
        this.setState({ categoryId: categoryId });
        axios.get(backendUrl + `/admin/product/category/id/${categoryId}`, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res.data);
            this.setState({
                loading: false,
                error: false,
                form: {
                    ...this.state.form,
                    nameRU: {
                        value: res.data.name_ru,
                        valid: true,
                        touched: false
                    },
                    nameUA: {
                        value: res.data.name_ua,
                        valid: true,
                        touched: false
                    },
                    route: {
                        value: res.data.route,
                        valid: true,
                        touched: false
                      },
                    alt: {
                        value: res.data.alt,
                        valid: true,
                        touched: false
                    },
                    article: {
                        value: res.data.article,
                        valid: true,
                        touched: false,
                    },
                },
                seo: {
                    title: res.data.seo_title,
                    description: res.data.seo_description,
                    keywords: res.data.seo_keywords,
                },
                seo_dp:{
                    title: res.data.seo_title_dp,
                    description: res.data.seo_description_dp,
                    keywords: res.data.seo_keywords_dp
                },
                seo_zp:{
                    title: res.data.seo_title_zp,
                    description: res.data.seo_description_zp,
                    keywords: res.data.seo_keywords_zp
                },
                seo_image: res.data.seo_image
            });
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({
                loading: false,
                error: true
            });
        });
    }

    onSelectIconHandler = (event, type) => {
        if (type === 'seo') {
            const reader = new FileReader();
            reader.onloadend = () => {
                    this.setState({
                        seoFile: file,
                        seoImagePreviewUrl: reader.result
                    });
            };
            const file = event.target.files[0];
            if (file) { reader.readAsDataURL(file); }
            else {
                this.setState({ seoFile: null, seoImagePreviewUrl: null }) 
            }
        } else {
            const reader = new FileReader();
            reader.onloadend = () => {
                    this.setState({
                        file: file,
                        imagePreviewUrl: reader.result
                    });
            };
            const file = event.target.files[0];
            if (file) { reader.readAsDataURL(file); }
            else {
                this.setState({ file: null, imagePreviewUrl: null }) 
            }
        }
    }

    onExportData = (obj) => {
        this.setState({
            seo: {
                title: obj.title,
                description: obj.description,
                keywords: obj.keywords
            }
        });
    }

    onExportData_zp = (obj) => {
        this.setState({
            seo_zp: {
                title: obj.title,
                description: obj.description,
                keywords: obj.keywords
            }
        });
    }
    onExportData_dp = (obj) => {
        this.setState({
            seo_dp: {
                title: obj.title,
                description: obj.description,
                keywords: obj.keywords
            }
        });
    }

    onInputChangedHandler = (event, name) => {
        const updatedForm = {
            ...this.state.form
        };
        updatedForm[name].value = event.target.value;
        updatedForm[name].touched = true;
        updatedForm[name].valid = event.target.value !== '';

        let formIsValid = true;
        for (let elem in updatedForm) {
            if (!updatedForm[elem].valid && updatedForm[elem].valid !== undefined) {
              formIsValid = false;
            }
          };
        this.setState({ form: updatedForm, formIsValid: formIsValid });
    }

    onSubmitForm = () => {
        this.setState({ loading: true });

        if (this.state.file) {
            let formData = new FormData();
            formData.append('name_ru', this.state.form.nameRU.value);
            formData.append('name_ua', this.state.form.nameUA.value);
            formData.append('route', this.state.form.route.value);
            formData.append('image', this.state.file);
            formData.append('alt', this.state.form.alt.value);
            formData.append('article', this.state.form.article.value);
            formData.append('seo_title', this.state.seo.title);
            formData.append('seo_description', this.state.seo.description);
            formData.append('seo_keywords', this.state.seo.keywords);
            formData.append('seo_title_zp', this.state.seo_zp.title);
            formData.append('seo_description_zp', this.state.seo_zp.description);
            formData.append('seo_keywords_zp', this.state.seo_zp.keywords);
            formData.append('seo_title_dp', this.state.seo_dp.title);
            formData.append('seo_description_dp', this.state.seo_dp.description);
            formData.append('seo_keywords_dp', this.state.seo_dp.keywords);

            axios.put(backendUrl + `/admin/product/category/${this.state.categoryId}`, formData, {
                headers: {
                    'Authorization': `Bearer ${this.props.token}`
                }
            })
            .then(res => {
                if (this.state.seoFile) {
                    let formData = new FormData();
                    formData.append('seoImage', this.state.seoFile);
                    axios.put(backendUrl + `/admin/product/category/seo/Image/${this.state.categoryId}`, formData, {
                        headers: {
                            'Authorization': `Bearer ${this.props.token}`
                        }
                    })
                    .then(res => {
                        this.props.history.push('/storage/categories');
                    })
                    .catch(err => {
                        this.setState({ error: true, loading: false });
                    });
                } else {
                    this.props.history.push('/storage/categories');
                }
            })
            .catch(err => {
                this.setState({ error: true, loading: false });
            });

        } else {

            axios.put(backendUrl + `/admin/product/category/noImage/${this.state.categoryId}`, {
                'name_ru': this.state.form.nameRU.value,
                'name_ua': this.state.form.nameUA.value,
                route: this.state.form.route.value,
                alt: this.state.form.alt.value,
                article: this.state.form.article.value,
                'seo_title': this.state.seo.title,
                'seo_description': this.state.seo.description,
                'seo_keywords': this.state.seo.keywords,
                'seo_title_dp': this.state.seo_dp.title,
                'seo_description_dp': this.state.seo_dp.description,
                'seo_keywords_dp': this.state.seo_dp.keywords,
                'seo_title_zp': this.state.seo_zp.title,
                'seo_description_zp': this.state.seo_zp.description,
                'seo_keywords_zp': this.state.seo_zp.keywords
            }, {
                headers: {
                    'Authorization': `Bearer ${this.props.token}`
                }
            })
            .then(res => {
                if (this.state.seoFile) {
                    let formData = new FormData();
                    formData.append('seoImage', this.state.seoFile);
                    axios.put(backendUrl + `/admin/product/category/seo/Image/${this.state.categoryId}`, formData, {
                        headers: {
                            'Authorization': `Bearer ${this.props.token}`
                        }
                    })
                    .then(res => {
                        this.props.history.push('/storage/categories');
                    })
                    .catch(err => {
                        this.setState({ error: true, loading: false });
                    });
                    
                } else {
                    this.props.history.push('/storage/categories');
                }
            })
            .catch(err => {
                this.setState({ error: true, loading: false });
            });

        }
    }

    render() {

        const { classes } = this.props;
        console.log(this.state);

        return (
            <div className="edit-category">
                <h1>{' '}</h1>
                { this.state.error && <p className="log-in_error-field" >{this.state.error}</p> }
                    <TextField 
                    onChange={(event) => this.onInputChangedHandler(event, 'nameRU')}
                    value={this.state.form.nameRU.value}
                    className={classes.element}
                    label="(RU)" 
                    variant="outlined"
                     />
                    <TextField 
                    onChange={(event) => this.onInputChangedHandler(event, 'nameUA')}
                    value={this.state.form.nameUA.value}
                    className={classes.element}
                    label="(UA)" 
                    variant="outlined"
                     />
                    <TextField 
                    onChange={(event) => this.onInputChangedHandler(event, 'route')}
                    value={this.state.form.route.value}
                    className={classes.element}
                    label="route" 
                    variant="outlined" 
                    />
                    <TextField 
                    onChange={(event) => this.onInputChangedHandler(event, 'alt')}
                    value={this.state.form.alt.value}
                    className={classes.element}
                    label="alt " 
                    variant="outlined" 
                    />
                    <p>{' html :'}</p>
                        <textarea 
                        value={this.state.form.article.value}
                        onChange={(e) => this.onInputChangedHandler(e, 'article')}
                        style={{
                            ...inputStyles,
                            height: '600px'
                        }} 
                        />
                    <label style={{margin: '10px 0'}}>
                             
                    </label>
                    <input 
                        onChange={(event) => this.onSelectIconHandler(event, 'image')}
                        type="file"
                        accept="image/*"
                    />
                    {this.state.imagePreviewUrl &&
                    <>
                        <Button
                        onClick={() => this.setState({ file: null, imagePreviewUrl: null })}
                        className={classes.cancelButton}
                        size="small"
                        variant="contained"
                        color="secondary">
                            {''}
                        </Button>
                        <img 
                        style={{ marginTop: '20px', maxWidth: '100%' }}
                        alt="icon"
                        src={this.state.imagePreviewUrl}
                        />
                        <p>    :</p>
                        <img 
                        style={{ 
                            marginTop: '10px', 
                            maxWidth: '60px',
                            maxHeight: '35px' 
                        }}
                        alt="icon"
                        src={this.state.imagePreviewUrl}
                        />
                    </>}
                    <p>{'     SEO        !'}</p>
                    <h1></h1>
                    {this.state.seo &&
                    <Seo 
                        seo={this.state.seo}
                        onExportData={this.onExportData}
                    />}
                     <h1></h1>
                    {this.state.seo_dp &&
                    <Seo 
                        seo={this.state.seo_dp}
                        onExportData={this.onExportData_dp}
                    />}
                    <h1></h1>
                    {this.state.seo_zp &&
                    <Seo 
                        seo={this.state.seo_zp}
                        onExportData={this.onExportData_zp}
                    />}
                    <p>SEO :</p>
                    {(this.state.seo_image || this.state.seoImagePreviewUrl) ?
                    <img 
                        style={{ marginTop: '20px', maxWidth: '100%' }}
                        src={
                            this.state.seoImagePreviewUrl ?
                            this.state.seoImagePreviewUrl :
                            (backendUrl + `/${this.state.seo_image}`)
                        }
                    /> : <p> ...</p>}
                    <label style={{margin: '10px 0'}}>
                         SEO :
                    </label>
                    <input 
                        onChange={(event) => this.onSelectIconHandler(event, 'seo')}
                        type="file"
                        accept="image/*"
                    />
                    {this.state.seoFile &&
                        <button
                            style={{backgroundColor:'brown', marginTop: '20px'}}
                            onClick={() => this.setState({seoFile: null, seoImagePreviewUrl: null})}
                        >
                            {''}
                        </button>
                    }
                    {this.state.loading ?
                    <CircularProgress />
                    :
                    <Button
                    onClick={this.onSubmitForm}
                    disabled={!this.state.formIsValid}
                    variant="contained" 
                    color="primary"
                    className={classes.button}
                    >
                        {''}
                    </Button>}  
            </div>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(withStyles(style)(EditCategory));