import React from 'react';
import { Switch, Route, NavLink } from 'react-router-dom';

import Main from './components/Main/Main';
import SeoEdit from './components/SeoEdit/SeoEdit';

const Pages = ( props ) => {

    const routes = (
        <Switch>
            <Route path="/pages/main" render={(props) => <Main {...props} />} />
            <Route path="/pages/shares" render={(props) => <SeoEdit name="Actions" {...props} />} />
            <Route path="/pages/delivery" render={(props) => <SeoEdit name="Delivery" {...props} />} />
            <Route path="/pages/contacts" render={(props) => <SeoEdit name="Contacts" {...props} />} />
            <Route path="/pages/blog" render={(props) => <SeoEdit name="Blog" {...props} />} />
        </Switch>
    );

    const path = props.location.pathname;

    return (
        <>
            {path === '/pages' ?
            (
                <div className="editor-content">
                    <ul>
                        <NavLink to="/pages/main" >
                            <li>{''}</li>
                        </NavLink>
                        <NavLink to="/pages/shares">
                            <li>{''}</li>
                        </NavLink>
                        <NavLink to="/pages/delivery">
                            <li>{'/'}</li>
                        </NavLink>
                        <NavLink to="/pages/contacts">
                            <li>{''}</li>
                        </NavLink>
                        <NavLink to="/pages/blog">
                            <li>{''}</li>
                        </NavLink>
                    </ul>
                </div>
            ) :
            (
              routes
            )}
        </>
    )
};

export default Pages;