import React from 'react';
import Slider from "react-slick";
import {Link} from "react-router-dom";
import Truncate from 'react-truncate';

import './index.scss';
import { backendUrl } from '../../../../../../../config/config';

const MainBlog = (props) => {

    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
    };

    return (
        <div className={'main-blog'}>
            <Slider {...settings}
                className={'slide-block'}
            >
                {
                    props.slider.map((post, index) =>
                        <div
                            key={index}
                            className={'list'}
                        >
                            <div className={'list_left'}>
                                {post.post ?
                                <img src={backendUrl + '/' + post.post.image} alt="" /> :
                                <img src={post.image} alt=""/>
                                }
                            </div>
                            <div className={'list_right'}>
                                <div className={'top'}>
                                    {post.label ? <h3>{post.label}</h3> : <h3> </h3>}
                                    <Truncate
                                        lines={8}
                                        ellipsis={<span>...</span>}
                                    >
                                        {post.description ? <p>{post.description}</p> : <p> </p> }
                                    </Truncate>
                                </div>
                                <Link
                                    className={'bottom'}
                                    to={'/'}
                                >
                                    <button>
                                        
                                    </button>
                                </Link>
                            </div>

                        </div>
                    )
                }
            </Slider>
        </div>
    );
}

export default MainBlog;