import React, { useEffect, useState } from 'react';
import { backendUrl } from '../../config/config';

import './seo.scss';

const Seo = (props) => {

    const [ title, setTitle ] = useState('');
    const [ keywords, setKeywords ] = useState('');
    const [ description, setDescription ] = useState('');
    const [ alt, setAlt ] = useState('');
    const [ img, setImg ] = useState('');
    const [ uploadedFile, setUploadedFile ] = useState({
        file: null,
        imagePreviewUrl: null
    });

    useEffect(() => {
        const seo = props.seo;
        setTitle(seo.title);
        setDescription(seo.description);
        setKeywords(seo.keywords);
        setAlt(seo.alt || '');
        setImg(seo.image || '');
    }, [props]);

    const onChangeTitle = (e) => {
        setTitle(e.target.value);
    };
    const onChangeDescription = (e) => {
        setDescription(e.target.value);
    };
    const onChangeKeywords = (e) => {
        setKeywords(e.target.value);
    };

    const onChangeAlt = (e) =>{
        setAlt(e.target.value);
    }

    const onSelectIconHandler = (e) => {
        const reader = new FileReader();
        reader.onloadend = () => {
            setUploadedFile({
                file: file,
                imagePreviewUrl: reader.result
            })
        };

        const file = e.target.files[0];
        if (file) { reader.readAsDataURL(file) }
        else { setUploadedFile({ file: null, imagePreviewUrl: null }) }
    };

    const onExportData = () => {
        props.onExportData({
            title, description, keywords, alt, img, uploadedFile
        });
    };

    return (
        <div className="seo">
            <h5>SEO    {props.title} </h5>
            <p className="warning">  ,    Google!</p>
            <div className="textarea-cont">
                <p>  Title:</p>
                <textarea 
                    value={title}
                    onChange={onChangeTitle}
                />
            </div>
            <div className="textarea-cont">
                <p>  Keywords:</p>
                <textarea 
                    value={keywords}
                    onChange={onChangeKeywords}
                />
            </div>
            <div className="textarea-cont">
                <p>  Description:</p>
                <textarea
                    className="textarea-cont_descr" 
                    value={description}
                    onChange={onChangeDescription}
                />
            </div>
            {props.image && <>
                <div className="textarea-cont">
                <p> alt:</p>
                <textarea
                    value={alt}
                    onChange={onChangeAlt}
                />
            </div>
            </>
            }
            {props.image && 
            <>
                <p>{' :'}</p>
                <img 
                    style={{
                        maxHeight: '200px',
                        maxWidth: '100%'
                    }}
                    src={uploadedFile.imagePreviewUrl ? uploadedFile.imagePreviewUrl :  backendUrl + '/' + img}
                />
            </>
            }
            {
            props.image && 
            <>
                <p>{' :'}</p>
                <div>
                    <input 
                        onChange={onSelectIconHandler}
                        type="file"
                        accept="image/*"
                    />
                    {uploadedFile.file &&
                    <button
                        style={{backgroundColor:'brown'}}
                        onClick={() => setUploadedFile({file: null, imagePreviewUrl: null})}
                    >
                        {''}
                    </button>}
                </div>
            </>
            } 
            <button
            style={{
                marginTop: '20px',
                alignSelf: 'center',
                fontSize: '20px'
            }}
            onClick={onExportData}
            >
                {'  SEO'}
            </button>
        </div>
    );
};

export default Seo;