import React from 'react';
import axios from 'axios';
import { connect } from 'react-redux';
import { backendUrl } from '../../../config/config';

import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import { checkValidaty } from '../../../tools/checkValidaty';

import './clientEdit.scss';

const styles = theme => ({
    element: {
        margin: '20px 0'
    },
    button: {
        marginTop: '25px'
    }
});

class EditClient extends React.Component {


    state = {
        form: {
            fio: '',
            status: '',
            email: {
                value: '',
                validation: {
                    required: true,
                    isEmail: true
                },
                valid: true,
                touched: false
            },
            phone: {
                value: '',
                validation: {
                    required: true,
                    isPhone: true
                },
                valid: true,
                touched: false
            },
            address: '',

        },
        formIsValid: true,
        error: null,
        loading: false,
        idIsValid: false,
        userId: null
    }

    componentDidMount() {
        const userId = this.props.match.params.id;
        this.setState({ loading: true, userId: userId });

        axios.get(backendUrl + `/admin/client/${userId}`, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                console.log(res, res.data);
                this.setState({
                    form: {
                        ...this.state.form,
                        fio: res.data.user.name || '',
                        status: res.data.user.status,
                        phone: {
                            ...this.state.form.phone,
                            value: res.data.user.phoneNumber
                        },
                        email: {
                            ...this.state.form.email,
                            value: res.data.user.email
                        },
                        address: res.data.user.address
                    },
                    error: null,
                    idIsValid: true
                });
            })
            .catch(err => {
                console.log(err, err.response);
                const message = err.response ? err.response.data.error || err.response.data.message : err.toString();
                this.setState({ error: message, idIsValid: false });
            })
            .finally(() => {
                this.setState({ loading: false });
            })
    }

    onInputChangedHandler = (event, controlName) => {
        const updatedControls = {
            ...this.state.form,
            [controlName]: {
                ...this.state.form[controlName],
                value: event.target.value,
                touched: true,
                valid: checkValidaty(event.target.value, this.state.form[controlName].validation)
            }
        }
        let formIsValid = true;
        for (let elem in updatedControls) {
            if (!updatedControls[elem].valid && updatedControls[elem].valid !== undefined) {
                formIsValid = false;
            }
        }
        this.setState({ form: updatedControls, formIsValid: formIsValid });
    }

    onStatusChangedHandler = (event) => {
        this.setState((prevState) => ({
            form: {
                ...prevState.form,
                status: event.target.value
            }
        }))
    }

    onSubmitForm = () => {
        this.setState({ loading: true, error: null });

        axios.put(backendUrl + `/admin/client/${this.state.userId}`, {
            email: this.state.form.email.value,
            name: this.state.form.fio,
            status: this.state.form.status,
            phone: this.state.form.phone.value,
            address: this.state.form.address
        }, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                console.log(res, res.data);
                this.props.history.push('/clients/list?successed-changed=true');
            })
            .catch(err => {
                console.log(err, err.response);
                const message = err.response ? err.response.data.error || err.response.data.message : err.toString();
                this.setState({ error: message });
            })
            .finally(() => {
                this.setState({ loading: false });
            })
    }

    render() {

        console.log(this.state);

        const { classes } = this.props;

        const statuses = [
            "None", "Bad", "Vip"
        ]

        return (
            <div className="create-new-admin__content">
                <h1>
                    {' '}
                </h1>
                { this.state.error && <p className="log-in_error-field" >{this.state.error}</p>}
                <TextField
                    onChange={(event) => this.onInputChangedHandler(event, 'email')}
                    value={this.state.form.email.value}
                    error={!this.state.form.email.valid && this.state.form.email.touched}
                    helperText={!this.state.form.email.valid && this.state.form.email.touched ? 'enter correct email' : null}
                    className={classes.element}
                    label="E-mail"
                    variant="outlined"
                />
                <TextField
                    onChange={(event) => this.onInputChangedHandler(event, 'phone')}
                    value={this.state.form.phone.value}
                    error={!this.state.form.phone.valid && this.state.form.phone.touched}
                    helperText={!this.state.form.phone.valid && this.state.form.phone.touched ? 'enter correct phone' : null}
                    className={classes.element}
                    label="phone"
                    variant="outlined"
                />
                <TextField
                    value={this.state.form.fio}
                    onChange={(e) => this.setState({ form: { ...this.state.form, fio: e.target.value } })}
                    className={classes.element}
                    label=""
                    variant="outlined"
                />
                <TextField
                    value={this.state.form.address}
                    onChange={(e) => this.setState({ form: { ...this.state.form, address: e.target.value } })}
                    className={classes.element}
                    label="Address"
                    variant="outlined"
                />
                <TextField
                    select
                    label=""
                    value={this.state.form.status}
                    onChange={this.onStatusChangedHandler}
                    SelectProps={{
                        native: true,
                    }}
                    helperText=" "
                    variant="outlined"
                >
                    {statuses.map((option) => (
                        <option key={option} value={option}>
                            {option}
                        </option>
                    ))}
                </TextField>
                {this.state.loading ?
                    <CircularProgress />
                    : <Button
                        onClick={this.onSubmitForm}
                        disabled={!this.state.formIsValid || !this.state.idIsValid}
                        variant="contained"
                        color="primary"
                        className={classes.button}
                    >
                        {''}
                    </Button>}
            </div>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(withStyles(styles)(EditClient));