import { CircularProgress } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import Button from '@material-ui/core/Button';
import axios from 'axios';
import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { backendUrl } from '../../../../../../config/config';
import ErrorComponent from '../../../../../Errors/Error/Error';
import Modal from '../../../../../UI/Modal/Modal';

class AllPosts extends React.Component {

    state = {
        loading: true,
        error: false,
        posts: [],
        modalForSuccessDelete: false,
        modalForModeDelete: false,
        modalForSuccessAdd: false,
        deleteMode: []
    }

    componentDidMount() {

        if (this.props.location.state && this.props.location.state.successAdd) {
            this.setState({ modalForSuccessAdd: true });
            this.props.location.state = null;
        }

        axios.get(backendUrl + '/posts/', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res);
            this.setState({
                error: false,
                loading: false,
                posts: res.data
            });
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({ error: true, loading: false });
        })
    }

    onDeletePost = (id) => {
        this.setState({ loading: true });
        axios.delete(backendUrl + `/admin/post/${id}`, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                let newPosts = [ ...this.state.posts ];
                newPosts = newPosts.filter(el => el._id !== id);
                this.setState({
                    modalForSuccessDelete: true,
                    loading: false,
                    error: false,
                    posts: newPosts
                });
            })
            .catch(err => {
                this.setState({
                    loading: false,
                    error: true,
                });
            })
    }

    render() {

        const modalForSuccessDelete = (
            <>
                <p style={{ textAlign: 'center' }} >
                    {'   !'}
                </p>
            </>
        );

        const modalForModeDelete = (
            <div style={{textAlign: 'center'}}>
                <p style={{ textAlign: 'center' }}>
                    {'     '}
                    <span style={{ fontWeight: 700 }}>
                        {this.state.deleteMode[1]}
                    </span>
                </p>
                <Button
                onClick={() => {
                    this.onDeletePost(this.state.deleteMode[0]);
                    this.setState({
                        modalForModeDelete: false,
                        deleteMode: []
                    })
                }}
                color="primary"
                variant="outlined">
                    {''}
                </Button>
            </div>
        );

        const modalForSuccessAdd = (
            <>
                <p>{'    !'}</p>
            </>
        );

        return (
            <div style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                paddingBottom: '40px'
            }}>
                <Modal
                show={this.state.modalForModeDelete}
                modalClosed={() => this.setState({ modalForModeDelete: false })}>
                    {modalForModeDelete}
                </Modal>
                <Modal
                show={this.state.modalForSuccessAdd}
                modalClosed={() => this.setState({ modalForSuccessAdd: false })}>
                    {modalForSuccessAdd}
                </Modal>
                <Modal
                show={this.state.modalForSuccessDelete}
                modalClosed={
                    () => this.setState({ modalForSuccessDelete: false })
                }
                >
                    {modalForSuccessDelete}
                </Modal>
                { this.state.loading && !this.state.error && <CircularProgress /> }
                { this.state.error && !this.state.loading && <ErrorComponent /> }
                { 
                !this.state.posts[0] && !this.state.loading && !this.state.error && 
                <p>{'     .  -    .'}</p>
                }
                {
                   this.state.posts[0] && !this.state.loading && !this.state.error &&  
                   <div style={{marginTop: '40px'}}>
                    {this.state.posts.map(post => (
                        <div style={{
                            display: 'flex',
                            flexDirection: 'column',
                            alignItems: 'flex-end'
                        }}>
                            <div style={{
                                display: 'flex',
                                alignItems: 'center',
                                flexDirection: 'row'
                            }}>
                                <EditIcon 
                                    onClick={ () => 
                                        this.props.history.push(`/editor/blog/edit/${post._id}`)
                                    }
                                   style={{ 
                                    color: 'forestgreen', 
                                    marginRight: '10px', 
                                    marginBottom: '5px', 
                                    cursor: 'pointer'
                                    }} 
                                />
                                <DeleteIcon 
                                    onClick={ () => this.setState({
                                        deleteMode: [ post._id, post.title ],
                                        modalForModeDelete: true
                                    }) }
                                    style={{ 
                                        color: 'brown', 
                                        marginRight: '10px', 
                                        marginBottom: '5px', 
                                        cursor: 'pointer'
                                    }}
                                />
                            </div>
                            <Link
                                to={`/editor/blog/edit/${post._id}`}
                                itemProp={'list'}
                                className={'list'}
                                key={post._id}
                            >
                                <div
                                className={'list_context'}
                                >
                                    <img
                                        src={backendUrl + `/${post.image}`}
                                        alt={post.alt}
                                    >
                                    </img>
                                    <div
                                        className={'footer'}
                                    >
                                        <h1
                                        >
                                            {post.title}
                                        </h1>
                                        <p
                                        >
                                            {'01.01.2021'}
                                        </p>
                                        <li
                                        >
                                            <span
                                                style={{fontSize: '14px'}}
                                            >
                                                {' ...'}
                                            </span>
                                        </li>
                                    </div>
                                </div>
                            </Link>
                        </div>
                    ))}
                   </div>
                }
            </div>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(AllPosts);