import React from 'react';
import { NavLink, Switch, Route, Redirect, Link } from 'react-router-dom';

import { links, texts } from './list';

import FoodSilders from './mainComponents/FoodSliders/FoodSliders';
import EditFoodSlider from './mainComponents/EditFoodSlider/EditFoodSlider';
import SharesSlider from './mainComponents/SharesSlider/SharesSlider';
import BlogSlider from './mainComponents/BlogSlider/BlogSlider';
import SeoEdit from '../../components/SeoEdit/SeoEdit';
import PageText from '../PageText/PageText';

const Main = (props) => {

    const routes = (
        <Switch>
            <Route path="/pages/main/food-sliders" component={FoodSilders} />
            <Route path="/pages/main/blog-sliders" component={BlogSlider} />
            <Route path="/pages/main/popular-sliders" component={FoodSilders} />
            <Route path="/pages/main/edit-food-slider/:id" component={EditFoodSlider} />
            <Route path="/pages/main/shares-slider" component={SharesSlider} />
            <Route 
                path="/pages/main/main-seo" 
                render={(props) => <SeoEdit name="Menu" {...props} />} 
            />
            <Route 
                path="/pages/main/kh-seo" 
                render={(props) => <SeoEdit name="Kh" {...props} />} 
            />
            <Route 
                path="/pages/main/dp-seo" 
                render={(props) => <SeoEdit name="Dp" {...props} />} 
            />
            <Route 
                path="/pages/main/zp-seo" 
                render={(props) => <SeoEdit name="Zp" {...props} />} 
            />
            {texts.map((el, index) => (
                <Route 
                    key={index}
                    path={el.to}
                    render={(props) => <PageText {...props} />}
                />
            ))}
            <Redirect exact from="/pages/main" to="/pages/main/food-sliders" />
        </Switch>
    );

    return (
        <>
            <div className="all-admins_header">
                {links.map((el, index) => (
                    <NavLink 
                    key={index} 
                    to={el.to} 
                    activeClassName="all-admins_header__item-active"
                    className='all-admins_header__item'>
                        {el.label}
                    </NavLink>
                ))}
            </div>
            <div className="all-admins_header">
                {texts.map((el, index) => (
                    <Link 
                    key={index} 
                    to={{
                        pathname: el.to,
                        state: {
                            title: el.title,
                            fetch: el.fetch
                        }
                    }} 
                    activeClassName="all-admins_header__item-active"
                    className='all-admins_header__item'>
                        {el.label}
                    </Link>
                ))}
            </div>
            {routes}
        </>
    );
};

export default Main;