import React from 'react';
import axios from 'axios';
import { connect } from 'react-redux';
import { backendUrl } from '../../../../config/config';
import ErrorComponent from '../../../Errors/Error/Error';

import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import { checkValidaty } from '../../../../tools/checkValidaty';

import './newAdmin.scss';

const styles = theme => ({
    element: {
        margin: '20px 0'
    },
    button: {
        marginTop: '25px'
    }
});

class NewAdmin extends React.Component {


    state = {
        form: {
            fio: '',
            status: 'admin',
            email: {
                value: '',
                validation: {
                  required: true,
                  isEmail: true
                },
                valid: false,
                touched: false
              },
              password: {
                value: '',
                validation: {
                  required: true,
                  minLength: 6
                },
                valid: false,
                touched: false
              }
        },
        formIsValid: false,
        error: null,
        loading: false,
        serverError: false
    }

    componentDidMount() {
        this.setState({ loading: true });

        axios.get(backendUrl + '/admin/status', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then((res )=>{
            console.log(res, res.data);
            if (res.data.status === 'admin') {
                this.setState({ serverError: true });
            }
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({ serverError: true });
        })
        .finally(() => {
            this.setState({ loading: false });
        })
    }

    onInputChangedHandler = (event, controlName) => {
        const updatedControls = {
          ...this.state.form,
          [controlName]: {
            ...this.state.form[controlName],
            value: event.target.value,
            touched: true,
            valid: checkValidaty(event.target.value, this.state.form[controlName].validation)
          }
        }
        let formIsValid = true;
        for (let elem in updatedControls) {
          if (!updatedControls[elem].valid && updatedControls[elem].valid !== undefined) {
            formIsValid = false;
          }
        }
        this.setState({ form: updatedControls, formIsValid: formIsValid });
      }

    onStatusChangedHandler = (event) => {
        this.setState((prevState) => ({
            form: {
                ...prevState.form,
                status: event.target.value
            }
        }))
      }

      onSubmitForm = () => {
          this.setState({ loading: true, error: null });

          axios.post(backendUrl + '/admin/signup', {
            email: this.state.form.email.value,
            name: this.state.form.fio,
            status: this.state.form.status,
            password: this.state.form.password.value
            }, {
                headers: {
                    'Authorization': `Bearer ${this.props.token}`
                }
            })
            .then(res => {
                console.log(res, res.data);
                this.props.history.push('/super-admin-profile/users?successed-add=true')
            })
            .catch(err => {
                console.log(err, err.response);
                const message = err.response ? err.response.data.error || err.response.data.message : err.toString();
                this.setState({ error: message });
            })
            .finally(() => {
                this.setState({ loading: false });
            })
      }

    render() {

        console.log(this.state);

        const { classes } = this.props;

        const statuses = [
            'admin',
            'SuperAdmin'
        ]

        return (
            <>
            {this.state.serverError ?
            <ErrorComponent />
            : 
            <div className="create-new-admin__content">
                <h1>
                    {'  '}
                </h1>
                    { this.state.error && <p className="log-in_error-field" >{this.state.error}</p> }
                    <TextField 
                    onChange={(event) => this.onInputChangedHandler(event, 'email')}
                    value={this.state.form.email.value}
                    error={!this.state.form.email.valid && this.state.form.email.touched}
                    helperText={!this.state.form.email.valid && this.state.form.email.touched ? 'enter correct email' : null}
                    className={classes.element}
                    label="E-mail" 
                    variant="outlined"
                     />
                    <TextField 
                    value={this.state.form.fio}
                    onChange={(e) => this.setState({ form: { ...this.state.form, fio: e.target.value }})}
                    className={classes.element}
                    label="" 
                    variant="outlined"
                     />
                    <TextField 
                    onChange={(event) => this.onInputChangedHandler(event, 'password')}
                    value={this.state.form.password.value}
                    error={!this.state.form.password.valid && this.state.form.password.touched}
                    className={classes.element}
                    label="" 
                    type="password" 
                    variant="outlined" 
                    />
                    <TextField
                    select
                    label=""
                    value={this.state.form.status}
                    onChange={this.onStatusChangedHandler}
                    SelectProps={{
                        native: true,
                    }}
                    helperText=" "
                    variant="outlined"
                    >
                    {statuses.map((option) => (
                        <option key={option} value={option}>
                        {option}
                      </option>
                    ))}
                    </TextField>
                    {this.state.loading ?
                    <CircularProgress />
                    :<Button
                    onClick={this.onSubmitForm}
                    disabled={!this.state.formIsValid}
                    variant="contained" 
                    color="primary"
                    className={classes.button}
                    >
                        {''}
                    </Button>}
            </div>
            }
            </>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(withStyles(styles)(NewAdmin));