import React from 'react';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles((theme) => ({
    button: {
        textTransform: 'capitalize',
        fontSize: '14px',
        width: '140px',
        color: 'white'
    },
    redButton: {
        backgroundColor: '#ff6057',
        '&:hover': {
            backgroundColor: '#ff2b1f'
        }
    },
    greenButton: {
        backgroundColor: '#49a500',
        '&:hover': {
            backgroundColor: '#53bb01'
        }
    },
    yellowButton: {
        backgroundColor: '#fff44c',
        '&:hover': {
            backgroundColor: 'ffe706'
        }
    },
    orangeButton: {
        backgroundColor: '#ffc84c',
        '&:hover': {
            backgroundColor: '#ff9a08'
        }
    }
  }));

  const allStatuses = {
    'check': {
       name: ' ',
       color: 'greenButton'
    },
    'no-call': {
        name: '',
        color: 'redButton'
    },
    'new': {
        name: '',
        color: 'greenButton'
    },
    'send-to-delivery': {
        name: ' ',
        color: 'orangeButton'
    },
    'complete': {
        name: '',
        color: 'greenButton'
    },
    'send-to-assembling': {
        name: '',
        color: 'yellowButton'
    },
    'delivering': {
        name: '',
        color: 'yellowButton'
    },
    'client-confirmed': {
        name: '',
        color: 'yellowButton'
    },
    'samovivoz': {
        name: '',
        color: 'greenButton'
    },
    'cancel-other': {
        name: '',
        color: 'redButton'
    },
    'prepayed': {
        name: '',
        color: 'yellowButton'
    },
    'assembling': {
        name: ' ',
        color: 'orangeButton'
    }
}

const Status = (props) => {

    const classes = useStyles();

    return (
        <Button
        className={classes.button + ' ' + classes[allStatuses[props.status].color]}
        variant="contained"
        >
            {allStatuses[props.status].name}
        </Button>
    );
};

export default Status;