import React from "react";
import axios from "axios";
import { NavLink } from "react-router-dom";
import { connect } from "react-redux";
import { backendUrl } from "../../../config/config";
import CircularProgress from "@material-ui/core/CircularProgress";
import { withStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import Table from "@material-ui/core/Table";
import Paper from "@material-ui/core/Paper";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TablePagination from "@material-ui/core/TablePagination";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import ErrorComponent from "../../Errors/Error/Error";
import Modal from "../../UI/Modal/Modal";
import { useJsonToCsv } from "react-json-csv";
import { sortStrings, sortNumbers } from "../../../tools/sort";
import * as FileSaver from 'file-saver';
import * as XLSX from 'xlsx';

import "./usersList.scss";
const formats = [
  {
    value: "excel",
    label: "excel",
  },
  {
    value: "csv",
    label: "csv",
  },
  {
    value: "json",
    label: "json",
  },
];
const styles = (theme) => ({
  button: {
    backgroundColor: "#4caf50",
    marginLeft: "20px",
    color: "white",
    "&:hover": {
      backgroundColor: "#357a38",
    },
  },
  table: {
    width: "100%",
  },
  edit: {
    color: "#4caf50",
    cursor: "pointer",
    "&:hover": {
      color: "#357a38",
    },
  },
  delete: {
    color: "#b71c1c",
    cursor: "pointer",
    "&:hover": {
      color: "#801313",
    },
  },
  paginator: {
    width: "100%",
  },
  paper: {
    width: "90%",
    marginTop: "20px",
    marginBottom: "60px",
  },
  visuallyHidden: {
    border: 0,
    clip: "rect(0 0 0 0)",
    height: 1,
    margin: -1,
    overflow: "hidden",
    padding: 0,
    position: "absolute",
    top: 20,
    width: 1,
  },
  cancelButton: {
    textTransform: "capitalize",
    marginLeft: "15px",
    backgroundColor: "#b71c1c",
    "&:hover": {
      backgroundColor: "#801313",
    },
  },
});

const headCells = [
  { id: "name", numeric: false, disablePadding: false, label: "" },
  {
    id: "date",
    numeric: false,
    disablePadding: false,
    label: " ",
  },
  { id: "phone", numeric: false, disablePadding: false, label: "" },
  { id: "email", numeric: false, disablePadding: true, label: "E-mail" },
  {
    id: "AvgCheck",
    numeric: true,
    disablePadding: false,
    label: " ",
  },
  {
    id: "sumUserOrders",
    numeric: true,
    disablePadding: false,
    label: " ",
  },
];

class ClientList extends React.Component {
  state = {
    loading: true,
    error: false,
    users: null,
    rowsPerPage: 10,
    page: 0,
    orderBy: "name",
    order: "asc",
    modalupload: false,
    format: "csv",
    modalForSuccessAdd: false,
    modalForSuccessChange: false,
    searchArea: {
      email: "",
      fio: "",
      phone: "",
    },
    searchMode: false,
  };

  componentDidMount() {
    axios
      .get(backendUrl + "/admin/client/", {
        headers: {
          Authorization: `Bearer ${this.props.token}`,
        },
      })
      .then((res) => {
        console.log(res);
        const users = res.data;
        this.setState({ users: users });
      })
      .catch((err) => {
        console.log(err, err.response);
        this.setState({ error: true });
      })
      .finally(() => {
        this.setState({ loading: false });
      });
    if (this.props.location.search === "?successed-add=true") {
      this.setState({ modalForSuccessAdd: true });
    } else if (this.props.location.search === "?successed-changed=true") {
      this.setState({ modalForSuccessChange: true });
    }
  }

  handleChangeRowsPerPage = (event) => {
    this.setState({
      rowsPerPage: parseInt(event.target.value, 10),
      page: 0,
    });
  };

  handleChangePage = (event, newPage) => {
    this.setState({
      page: newPage,
    });
  };

  handleRequestSort = (event, property) => {
    const isAsc = this.state.orderBy === property && this.state.order === "asc";
    this.setState({
      order: isAsc ? "desc" : "asc",
      orderBy: property,
    });

    const users = this.state.users;

    const elem = headCells.find((el) => {
      if (el.id === property) {
        return true;
      } else return false;
    });

    if (elem.numeric) {
      this.setState({
        users: sortNumbers(
          property,
          users,
          this.state.order !== "desc" ? "asc" : "desc"
        ),
      });
    } else {
      this.setState({
        users: sortStrings(
          property,
          users,
          this.state.order !== "desc" ? "asc" : "desc"
        ),
      });
    }
  };

  onModalForSuccessAddClosed = () => {
    this.setState({ modalForSuccessAdd: false });
    this.props.history.push("/clients/list");
  };

  onModalForSuccessChange = () => {
    this.setState({ modalForSuccessChange: false });
    this.props.history.push("/clients/list");
  };

  onSelectOrder = (id) => {
    this.props.history.push(`/clients/check/${id}`);
  };

  onDownload = () => {
    axios
      .get(backendUrl + "/admin/client/file/download/plz", {
        headers: {
          Authorization: `Bearer ${this.props.token}`,
        },
      })
      .then((res) => {
        console.log(res.data);
        let response;
        if (this.state.format == "json") {
          response = new Blob([JSON.stringify(res.data)], {
            type: "application/json",
          });
          console.log(response);
          let url = window.URL.createObjectURL(response);
          let a = document.createElement("a");
          a.href = url;
          a.download = "clients.json";
          a.click();
        } else if (this.state.format == "excel") {
          const fileType =
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8";
          const fileExtension = "clients.xlsx";
          const ws = XLSX.utils.json_to_sheet(res.data);
          const wb = { Sheets: { data: ws }, SheetNames: ["data"] };
          const excelBuffer = XLSX.write(wb, {
            bookType: "xlsx",
            type: "array",
          });
          const data = new Blob([excelBuffer], { type: fileType });
          FileSaver.saveAs(data, fileExtension);
        } else if (this.state.format == "csv") {
          const filename = "clients",
            fields = {
              _id: "Index",
              name: "Name",
              email: "Email",
              phoneNumber: "Phone",
              date: "DateOfRegister",
              sumUserBonus: "sumUserBonus",
            },
            data = res.data;
          const { saveAsCsv } = useJsonToCsv();
          saveAsCsv({ data, fields, filename });
        }
      })
      .catch((err) => {
        console.log(err, err.response);
        this.setState({ error: true });
      })
      .finally(() => {
        this.setState({ loading: false });
      });
  };

  onSearch = () => {
    this.setState({ loading: true });

    axios
      .post(
        backendUrl + "/admin/client/search",
        {
          email: this.state.searchArea.email,
          name: this.state.searchArea.fio,
          phone: this.state.searchArea.phone,
        },
        {
          headers: {
            Authorization: `Bearer ${this.props.token}`,
          },
        }
      )
      .then((res) => {
        console.log(res);
        this.setState({
          searchMode: true,
          users: res.data.customers,
        });
      })
      .catch((err) => {
        console.log(err, err.response);
        this.setState({ error: true });
      })
      .finally(() => {
        this.setState({ loading: false });
      });
  };
  uploadModalTrue = () => {
    this.setState({ modalupload: true });
  };

  render() {
    const { classes } = this.props;

    const modalForUpload = (
      <>
        <div>
          <TextField
            id="standard-select-currency"
            select
            label="Select"
            value={this.state.format}
            onChange={(e) => {
              this.setState({
                format: e.target.value,
              });
            }}
            helperText="Please select your format"
          >
            {formats.map((option) => (
              <option key={option.label} value={option.value}>
                {option.label}
              </option>
            ))}
          </TextField>
          <Button
            onClick={() => {
              this.onDownload();
            }}
            variant="contained"
          >
            
          </Button>
        </div>
      </>
    );
    const modalForSuccessChanged = (
      <>
        <p style={{ textAlign: "center" }}>{"   "}</p>
      </>
    );

    return (
      <>
        <div className="users-list__content-area">
          {this.state.loading && <CircularProgress />}

          {this.state.error && !this.state.loading && <ErrorComponent />}

          {!this.state.error && !this.state.loading && (
            <>
              <Modal
                show={this.state.modalupload}
                modalClosed={() => {
                  this.setState({ modalupload: false });
                  this.props.history.push("/clients/list");
                }}
              >
                {modalForUpload}
              </Modal>
              <Modal
                show={this.state.modalForSuccessChange}
                modalClosed={this.onModalForSuccessChange}
              >
                {modalForSuccessChanged}
              </Modal>
              <div className="users-list__content-area__controls">
                <div className="users-list__content-area__controls__top">
                  <h1></h1>
                </div>
                <div className="users-list__content-area__controls__search">
                  <div className="users-list__content-area__controls__search__inputs">
                    <span className="users-list__content-area__controls__search__inputs__item">
                      <TextField
                        onChange={(e) =>
                          this.setState((prevState) => ({
                            searchArea: {
                              ...prevState.searchArea,
                              email: e.target.value,
                            },
                          }))
                        }
                        className={classes.input}
                        label="E-mail"
                        variant="outlined"
                      />
                    </span>
                    <span className="users-list__content-area__controls__search__inputs__item">
                      <TextField
                        onChange={(e) =>
                          this.setState((prevState) => ({
                            searchArea: {
                              ...prevState.searchArea,
                              fio: e.target.value,
                            },
                          }))
                        }
                        className={classes.input}
                        label=""
                        variant="outlined"
                      />
                    </span>
                    <span className="users-list__content-area__controls__search__inputs__item">
                      <TextField
                        onChange={(e) =>
                          this.setState((prevState) => ({
                            searchArea: {
                              ...prevState.searchArea,
                              phone: e.target.value,
                            },
                          }))
                        }
                        className={classes.input}
                        label=""
                        variant="outlined"
                      />
                    </span>
                    <Button
                      onClick={this.onSearch}
                      variant="contained"
                      className={classes.button}
                    >
                      {""}
                    </Button>
                    {this.state.searchMode && (
                      <Button
                        onClick={() => window.location.reload()}
                        className={classes.cancelButton}
                        variant="contained"
                        color="secondary"
                      >
                        {""}
                      </Button>
                    )}
                  </div>
                </div>
              </div>
              <Paper elevation={2} className={classes.paper}>
                <Table
                  className={classes.table}
                  size="medium"
                  aria-label="a dense table"
                >
                  <TableHead>
                    <TableRow>
                      {headCells.map((headCell) => {
                        return (
                          <TableCell
                            align="center"
                            key={headCell.id}
                            sortDirection={
                              headCell.id !== this.state.orderBy
                                ? false
                                : this.state.order
                            }
                          >
                            <TableSortLabel
                              active={
                                headCell.id !== "edit" &&
                                headCell.id !== "delete"
                              }
                              direction={
                                headCell.id === this.state.orderBy
                                  ? this.state.order
                                  : "asc"
                              }
                              onClick={(e) =>
                                this.handleRequestSort(e, headCell.id)
                              }
                            >
                              {headCell.label}
                              {headCell.id !== "edit" &&
                              headCell.id !== "delete" ? (
                                <span className={classes.visuallyHidden}>
                                  {this.state.order === "desc"
                                    ? "sorted descending"
                                    : "sorted ascending"}
                                </span>
                              ) : null}
                            </TableSortLabel>
                          </TableCell>
                        );
                      })}
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {this.state.users
                      .slice(
                        this.state.page * this.state.rowsPerPage,
                        this.state.page * this.state.rowsPerPage +
                          this.state.rowsPerPage
                      )
                      .map((user) => (
                        <TableRow
                          key={user._id}
                          style={{ cursor: "pointer" }}
                          onClick={() => this.onSelectOrder(user._id)}
                        >
                          <TableCell component="th" scope="row" align="center">
                            {user.name}
                          </TableCell>
                          <TableCell align="center">
                            {new Date(user.date).toLocaleString()}
                          </TableCell>
                          <TableCell align="center">
                            {user.phoneNumber}
                          </TableCell>
                          <TableCell align="center">{user.email}</TableCell>
                          <TableCell align="center">{user.AvgCheck}</TableCell>
                          <TableCell align="center">
                            {user.sumUserOrders}
                          </TableCell>
                          {/* <TableCell align="center">
                                                                <EditIcon
                                                                    onClick={() => this.onEditUser(user._id)}
                                                                    className={classes.edit} />
                                                            </TableCell>
                                                            <TableCell align="center">
                                                                <DeleteIcon
                                                                    onClick={() => this.onDeleteUser(user._id)}
                                                                    className={classes.delete} />
                                                            </TableCell> */}
                        </TableRow>
                      ))}
                  </TableBody>
                </Table>

                <TablePagination
                  className={classes.paginator}
                  rowsPerPageOptions={[5, 10, 25]}
                  component="div"
                  count={this.state.users.length}
                  rowsPerPage={this.state.rowsPerPage}
                  page={this.state.page}
                  onChangePage={this.handleChangePage}
                  onChangeRowsPerPage={this.handleChangeRowsPerPage}
                  labelRowsPerPage={"   :"}
                />
              </Paper>
              <div
                style={{
                  float: "left",
                  alignSelf: "flex-start",
                  marginLeft: "5%",
                  marginTop: "-7.5%",
                }}
              >
                <Button onClick={this.uploadModalTrue} variant="contained">
                   
                </Button>
                {/* <a href=""> </a> */}
              </div>
            </>
          )}
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  token: state.auth.token,
});

export default connect(mapStateToProps)(withStyles(styles)(ClientList));
