import React from 'react';
import { NavLink, Switch, Route, Redirect } from 'react-router-dom';
import { links } from './list';

import AddShare from './components/AddShare/AddShare';
import AllShares from './components/Shares/Shares';

class Shares extends React.Component {

    render() {

        const routes = (
            <Switch>
                <Route path="/editor/shares/all" component={AllShares} />
                <Route path="/editor/shares/add-share" component={AddShare} />
                <Redirect exact from="/editor/shares" to="/editor/shares/all" />
            </Switch>
        );

        return (
        <>
            <div className="all-admins_header">
                {links.map((el, index) => (
                    <NavLink 
                    key={index} 
                    to={el.to} 
                    activeClassName="all-admins_header__item-active"
                    className='all-admins_header__item'>
                        {el.label}
                    </NavLink>
                ))}
            </div>
            {routes}
          </>
        );
    }
}

export default Shares;