import React from 'react';
import axios from 'axios';
import { backendUrl } from '../../../../config/config';
import Button from '@material-ui/core/Button';
import Modal from '../../../UI/Modal/Modal'
import { connect } from 'react-redux';

import './addLetter.scss';
import { CircularProgress } from '@material-ui/core';

class AddLetter extends React.Component {


    state = {
        letterHeader: '',
        letterHTML: '',
        loading: false,
        error: null,
        successModal: false,
        lookingHTML: false
    }

    onButtonPressedHandler = () => {
        this.setState({ loading: true });
        axios.post(backendUrl + '/admin/mail/mail/', {
            subject: this.state.letterHeader,
            content: this.state.letterHTML
        }, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            this.setState({ successModal: true })
        })
        .catch(err => {
            console.log(err);
        })
        .finally(() => this.setState({ loading: false }));
    }

    onLookingEmailLetter = () => {
        if (this.state.lookingHTML) {
            this.setState({
                lookingHTML: false
            });
        } else {
                this.setState({
                    lookingHTML: true
                });
        }
    }

    render() {

        const successModalContent = (
            <p style={{ textAlign: 'center' }}>
                {'   !'}
            </p>
        );

        return (
            <div style={{
                display: 'flex', 
                flexDirection: 'column', 
                alignItems: 'center'
            }}>
                {this.state.error && 
                    <p style={{ fontSize: '24px', color: 'salmon'}}>
                        {this.state.error}
                    </p>
                }
                <Modal
                show={this.state.successModal}
                modalClosed={() => {
                    this.setState({ successModal: false });
                }}>
                    {successModalContent}
                </Modal>
                <h1>{'     '}</h1>
                <p>
                    {' :'}
                </p>
                <textarea 
                    value={this.state.letterHeader}
                    onChange={(e) => this.setState({ letterHeader: e.target.value })}
                    className="add-letter__textarea"
                    style={{
                        height: '30px'
                    }}
                />
                <p>{`      html    
                    (    ,       html ):`}</p>
                <textarea
                    value={this.state.letterHTML}
                    onChange={(e) => this.setState({ 
                        letterHTML: e.target.value,
                        lookingHTML: false,
                        src: ''
                     })}
                    className="add-letter__textarea"
                />
                {
                    this.state.letterHTML !== '' && !this.state.lookingHTML && 
                    <p className="add-letter__link-to-letter" onClick={this.onLookingEmailLetter}> 
                        {'      :'}
                    </p>
                }
                {this.state.lookingHTML && this.state.letterHTML !== '' &&
                    <p className="add-letter__link-to-letter" onClick={this.onLookingEmailLetter}>
                        {''}
                    </p>
                }
                {this.state.lookingHTML && 
                <div style={{ display: 'flex', flexDirection: 'row',}}>
                    <iframe 
                    width="900" 
                    height="600" 
                    srcDoc={this.state.letterHTML}>
                        {'    Frame'}
                    </iframe>
                    <iframe 
                    style={{ marginLeft: '40px'}}
                    width="360" 
                    height="600" 
                    srcDoc={this.state.letterHTML}>
                        {'    Frame'}
                    </iframe>
                </div>
                }
                {this.state.loading ?
                <CircularProgress/> :
                <Button 
                onClick={this.onButtonPressedHandler}
                size="large"
                color="primary"
                variant="contained"
                disabled={ 
                    this.state.letterHTML.trim() === '' || this.state.letterHeader.trim() === '' 
                }
                style={{
                    marginTop: '30px',
                    marginBottom: '30px'
                }}
                >
                    {' '}
                </Button>}
            </div>
        );
    }
}   

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps, null)(AddLetter);