import React from 'react';
import axios from 'axios';
import { Switch, Route, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { backendUrl } from './config/config';
import * as actions from './store/actions/index';

import Header from './components/Header/Header';
import ErrorComponent from './components/Errors/Error/Error'; 
import NotMatchPage from './components/Errors/NotMatchPage/NotMatchPage';

import LogIn from './components/Auth/LogIn/LogIn';
import Pages from './components/Pages/Pages';
import Storage from './components/Storage/Storage';
import Clients from './components/Clients/Clients';
import Orders from './components/Orders/Orders';
import Editor from './components/Editor/Editor';
import Sending from './components/Sending/Sending';
import LogOut from './components/Auth/LogOut/LogOut';
import SuperAdminProfile from './components/SuperAdminProfile/SuperAdminProfile';
import ForgotPassword from './components/Auth/ForgotPassword/ForgotPassword';
import RestorePassword from './components/Auth/RestorePassword/RestorePassword';

class App extends React.Component {

  state = {
    loading: false,
    error: false
  }

  componentDidMount() {
    const token = localStorage.getItem('token-admin-panel-24rolls');
    if ( token ) {
      this.props.onResetToken(token);
      if (!this.props.status) {
        axios.get(backendUrl + '/admin/status', {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        })
        .then(res => {
          console.log(res);
          const status = res.data.status;
          this.props.onSetAdminStatus(status);
          this.setState({ error: false });
        })
        .catch(err => {
          console.log(err, err.response);
          this.setState({ error: true });
        })
        .finally(() => {
          this.setState({ loading: false });
        })
      }
    };
  }

  render() {

    let routes;
    if (!this.props.isAuth) {
      routes = (
        <Switch>
              <Route 
              exact
              key='route-1'
              path="/log-in" 
              component={LogIn} />
              <Route 
              exact
              key='route-2'
              path="/forgot-password" 
              component={ForgotPassword} />
              <Route 
              key='route-3'
              path="/restore-password/:token" 
              component={RestorePassword} />
              <Redirect exact from='/' to='/log-in' />
              <Route key='route-4' component={NotMatchPage} />
        </Switch>
      );
    } else {
      routes = (
        <Switch>
          <Route path="/restore-password/:token" component={RestorePassword} />
          <Route path="/pages" component={Pages} />
          <Route path="/storage" component={Storage} />
          <Route path="/clients" component={Clients} />
          <Route path="/orders" component={Orders} />
          <Route path="/editor" component={Editor} />
          <Route path="/sending" component={Sending} />
          <Route path="/logout" component={LogOut} />
          { this.props.status && <Route path="/super-admin-profile" component={SuperAdminProfile} /> }
          <Redirect exact from='/' to='/storage' />
          <Redirect exact from='/log-in' to='/storage' />
          <Route key='route-4' component={NotMatchPage} />
        </Switch>
      );
    }
  
    return (
     <Header status={this.props.status}>
       <>
       {this.state.error 
       ?
       <ErrorComponent />
       :
       routes}
       </>
     </Header>
    );
  }
}

const mapStateToProps = state => ({
  isAuth: state.auth.token !== null,
  status: state.auth.status
});

const mapDispatchToProps = dispatch => ({
  onResetToken: (token) => dispatch(actions.resetToken(token)),
  onSetAdminStatus: (status) => dispatch(actions.setAdminStatus(status))
})

export default connect(mapStateToProps, mapDispatchToProps)(App);
