import React from 'react';
import { NavLink, Switch, Route, Redirect } from 'react-router-dom';
import { links } from './list';

import AllSPosts from './components/AllPosts/AllPosts';
import AddBlog from './components/AddBlog/AddBlog';
import BlogArticle from './components/BlogArticle/BlogArticle';
import Comments from './components/Comments/Comments';

class Blog extends React.Component {

    render() {

        const routes = (
            <Switch>
                <Route path="/editor/blog/all" component={AllSPosts} />
                <Route path="/editor/blog/edit/:id" component={BlogArticle} />
                <Route path="/editor/blog/add-blog" component={AddBlog} />
                <Route path="/editor/blog/comments" component={Comments} />
                <Redirect exact from="/editor/blog" to="/editor/blog/all" />
            </Switch>
        );

        return (
        <>
            <div className="all-admins_header">
                {links.map((el, index) => (
                    <NavLink 
                    key={index} 
                    to={el.to} 
                    activeClassName="all-admins_header__item-active"
                    className='all-admins_header__item'>
                        {el.label}
                    </NavLink>
                ))}
            </div>
            {routes}
          </>
        );
    }
}

export default Blog;