import axios from 'axios';
import React from 'react';
import { connect } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { backendUrl } from '../../../../config/config';
import { withStyles } from '@material-ui/styles';
import Modal from '../../../UI/Modal/Modal';
import ErrorComponent from '../../../Errors/Error/Error';
import CircularProgress from '@material-ui/core/CircularProgress';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import Table from '@material-ui/core/Table';
import Paper from '@material-ui/core/Paper';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TablePagination from '@material-ui/core/TablePagination';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';


const style = theme => ({
    table: {
        width: '100%'
    },
    edit: {
        color: '#4caf50',
        cursor: 'pointer',
        '&:hover': {
            color: '#357a38'
        }
    },
    delete: {
        color: '#b71c1c',
        cursor: 'pointer',
        '&:hover': {
            color: '#801313'
        }
    },
    paper: {
        width: '90%',
        marginTop: '20px',
        marginBottom: '60px'
    }
});

const headCells = [
    { id: 'icon', numeric: false, disablePadding: true, label: '' },
    { id: 'name_ru', numeric: false, disablePadding: false, label: '' },
    { id: 'name_ua', numeric: false, disablePadding: false, label: '(UA)' },
    { id: 'alt', numeric: false, disablePadding: false, label: 'alt' },
    { id: 'route', numeric: false, disablePadding: false, label: 'route' },
    { id: 'edit', numeric: false, disablePadding: false, label: '' },
    { id: 'delete', numeric: false, disablePadding: false, label: '' }
];

class Categories extends React.Component {

    state = {
        error: false,
        loading: true,
        categories: null,
        page: 0,
        rowsPerPage: 10,
        deleteMode: null,
        modalForSuccessAdd: false
    }

    componentDidMount() {
        this.setState({ loading: true });

        if ( this.props.location.search === '?success-created=true' ) {
            this.setState({ modalForSuccessAdd: true });
        }

        axios.get(backendUrl + '/admin/product/categories', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            } 
        })
        .then(res => {
            console.log(res, res.data);
            const categories = res.data.message;
            this.setState({ categories: categories });
        })
        .catch(err => {
            console.log(err, err.response);
            const message = err.response ? err.response.data.error || err.response.data.message : err.toString();
            this.setState({ error: message });
        })
        .finally(() => {
            this.setState({ loading: false });
        })
    }

    handleChangeRowsPerPage = (event) => {
        this.setState({
            rowsPerPage: parseInt(event.target.value, 10),
            page: 0
        })
      };

      handleChangePage = (event, newPage) => {
        this.setState({
            page: newPage
        })
      };

    onDeleteCategory = (id) => {

        axios.delete(backendUrl + `/admin/product/category/${id}`, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
             }
        })
        .then(res => {
            console.log(res);
            window.location.reload();
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({ error: true });
        });
    }

    onEditCategory = (id) => {
        this.props.history.push(`/storage/edit-category/${id}`);
    }

    render() {

        const { classes } = this.props;
        let modalDeleteContent = null;

        const modalForSuccessAdd = (
            <>
                <p style={{ textAlign: 'center' }}>
                    {'   '}
                </p>
            </>
        )

        if ( this.state.deleteMode !== null ) {
            modalDeleteContent = (
                <div style={{ 
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center',
                    alignItems: 'center'
                 }} >
                  <p style={{ textAlign: 'center' }}>
                      {'     '}
                    <span style={{ fontWeight: 700 }} >{this.state.deleteMode[1]}</span>
                  </p>
                  <Button
                    onClick={() => this.onDeleteCategory(this.state.deleteMode[0])}
                  >
                      {''}
                  </Button>
                </div>
            );
        };

        return (
            <>
            {this.state.error ? 
            <ErrorComponent /> : 
            <>
                <Modal
                    show={this.state.modalForSuccessAdd}
                    modalClosed={() => {
                        this.setState({modalForSuccessAdd: false});
                        this.props.history.push('/storage/categories');
                    }}>
                    {modalForSuccessAdd}
                </Modal>
                <Modal
                    show={this.state.deleteMode !== null}
                    modalClosed={() => {
                        this.setState({ deleteMode: null });
                    }}>
                        {modalDeleteContent}
                </Modal>
                {this.state.loading ? 
                <CircularProgress/>
                :
                <>
                    <div className="users-list__content-area__controls__top">
                                <h1></h1>
                                <NavLink to="/storage/add-category">
                                    <Button
                                    variant="contained" 
                                    color="primary"
                                    startIcon={<AddIcon />}>
                                        {''}
                                    </Button>
                                </NavLink>
                    </div>
                    <p style={{ paddingLeft: '20px'}}> 
                             route  !          !
                    </p>
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent:'center'
                    }}>
                        <Paper elevation={2} className={classes.paper}>
                                <Table className={classes.table} size="medium" aria-label="a dense table">
                                    <TableHead>
                                    <TableRow>
                                        {headCells.map(headCell => (
                                            <TableCell
                                            align="left"
                                            key={headCell.id}
                                            >
                                                    {headCell.label}
                                            </TableCell>
                                        ))}
                                    </TableRow>
                                    </TableHead>
                                    <TableBody>
                                    {this.state.categories
                                    .slice(
                                        this.state.page * this.state.rowsPerPage, 
                                        this.state.page * this.state.rowsPerPage 
                                        + this.state.rowsPerPage).map((user) => (
                                        <TableRow key={user._id}>
                                            <TableCell component="th" scope="row" align="left">
                                                <img 
                                                style={{
                                                    maxWidth: '60px',
                                                    maxHeight: '35px'
                                                }}
                                                alt="" 
                                                src={backendUrl + '/' + user.image} 
                                                />
                                            </TableCell>
                                            <TableCell align="left">{user.name_ru}</TableCell>
                                            <TableCell align="left">{user.name_ua}</TableCell>
                                            <TableCell align="left">{user.alt}</TableCell>
                                            <TableCell align="left">{user.route}</TableCell>
                                            <TableCell align="left">
                                                { user._id !== '5ff17a7f6158c22e503feaa1' &&
                                                    <EditIcon
                                                    onClick={() => this.onEditCategory(user._id)}
                                                    className={classes.edit} 
                                                    />
                                                }
                                            </TableCell>
                                            <TableCell align="left">
                                                { user._id !== '5ff17a7f6158c22e503feaa1' &&
                                                    <DeleteIcon 
                                                    onClick={() => this.setState({ deleteMode: [ user._id, user.name_ru ] })}
                                                    className={classes.delete}/>
                                                }
                                            </TableCell>
                                        </TableRow>
                                    ))}
                                    </TableBody>
                                </Table>
                                <TablePagination
                                    className={classes.paginator}
                                    rowsPerPageOptions={[5, 10, 25]}
                                    component="div"
                                    count={this.state.categories.length}
                                    rowsPerPage={this.state.rowsPerPage}
                                    page={this.state.page}
                                    onChangePage={this.handleChangePage}
                                    onChangeRowsPerPage={this.handleChangeRowsPerPage}
                                    labelRowsPerPage={'   :'}
                                />
                            </Paper>
                        </div>
                </>
                }
            </>}
            </>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(withStyles(style)(Categories));