import React from 'react';
import { Switch, Route, NavLink, Redirect } from 'react-router-dom';
import { links } from './list';

import EmailUsers from './components/EmailUsers/EmailUsers';
import SmsUsers from './components/SmsUsers/SmsUsers';
import AddLetter from './components/AddLetter/AddLetter';

class Sending extends React.Component {
    render() {

        const routes = (
            <Switch>
                <Route path="/sending/users-email" component={EmailUsers} />
                <Route path="/sending/users-sms" component={SmsUsers} />
                <Route path="/sending/add-letter" component={AddLetter} />
                <Redirect from="/sending" to="/sending/users-email" />
            </Switch>
        );

        return (
            <>  
                <div className="all-admins_header">
                    {links.map((el, index) => (
                        <NavLink 
                        key={index} 
                        to={el.to} 
                        activeClassName="all-admins_header__item-active"
                        className='all-admins_header__item'>
                            {el.label}
                        </NavLink>
                    ))}
                </div>
                {routes}
            </>
        );
    }
}

export default Sending;