import React from 'react';
import { NavLink, Switch, Route } from 'react-router-dom';

import Shares from './components/Shares/Shares';
import Blog from './components/Blog/Blog';

import './editor.scss';

const Editor = ( props ) => {

    const routes = (
        <Switch>
            <Route path="/editor/shares" render={(props) => <Shares {...props} />} />
            <Route path="/editor/blog" render={(props) => <Blog {...props} />} />
        </Switch>
    );

    const path = props.location.pathname;

    return (
        <>
            {path === '/editor' ?
            (
                <div className="editor-content">
                    <ul>
                        <NavLink to="/editor/shares" >
                            <li>{''}</li>
                        </NavLink>
                        <NavLink to="/editor/blog" >
                            <li>{''}</li>
                        </NavLink>
                    </ul>
                </div>
            ) :
            (
              routes
            )}
        </>
    )
};

export default Editor;