import React from 'react';
import { connect } from 'react-redux';
import { NavLink } from 'react-router-dom';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import CircularProgress from '@material-ui/core/CircularProgress';
import { checkValidaty } from '../../../tools/checkValidaty';
import Modal from '../../UI/Modal/Modal';
import * as actions from '../../../store/actions/index';

import '../auth.scss';

const styles = theme => ({
    paper: {
      marginTop: theme.spacing(8),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    avatar: {
      margin: theme.spacing(1),
      backgroundColor: theme.palette.secondary.main,
    },
    form: {
      width: '100%',
      marginTop: theme.spacing(1),
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
    },
    link: {
      color: '#3f51b5',
      '&:hover': {
        textDecoration: 'underline'
      }
    },
    circular: {
      marginLeft: '175px'
    }
  });

class LogIn extends React.Component {

    state = {
        controls: {
            email: {
              value: '',
              validation: {
                required: true,
                isEmail: true
              },
              valid: false,
              touched: false
            },
            password: {
              value: '',
              validation: {
                required: true,
                minLength: 6
              },
              valid: false,
              touched: false
            }
          },
          formIsValid: false,
          modalForSuccessChangePass: false
    }

    componentDidMount() {
      if ( this.props.location.search === '?password_reseted_success?true' ) {
        this.setState({ modalForSuccessChangePass: true });
      }
    }

    onInputChangedHandler = (event, controlName) => {
        const updatedControls = {
          ...this.state.controls,
          [controlName]: {
            ...this.state.controls[controlName],
            value: event.target.value,
            touched: true,
            valid: checkValidaty(event.target.value, this.state.controls[controlName].validation)
          }
        }
        let formIsValid = true;
        for (let elem in updatedControls) {
          if (!updatedControls[elem].valid) {
            formIsValid = false;
          }
        }
        this.setState({ controls: updatedControls, formIsValid: formIsValid });
      }

      onSubmitHandler = (event) => {
        event.preventDefault();
        const userData = {
          email: this.state.controls.email.value,
          password: this.state.controls.password.value
        };
        this.props.onLogIn(userData);
      }

    onCloseModalForSuccessChangePass = () => {
      this.props.history.push('/log-in');
      this.setState({ modalForSuccessChangePass: false });
    }


    render() {

      console.log(this.props);

        const { classes } = this.props;

        const modalContent = (
          <>
            <p style={{ textAlign: 'center' }}>{'   !'}</p>
          </>
        );

        return (
          <>
            <Modal
              show={this.state.modalForSuccessChangePass}
              modalClosed={this.onCloseModalForSuccessChangePass}>
                  {modalContent}
            </Modal>
            <Container component="main" maxWidth="xs">
              <CssBaseline />
              <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                  <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                  {''}
                </Typography>

                {this.props.error && 
                <p className="log-in_error-field" >
                    {this.props.error}
                </p>}

                <form className={classes.form} noValidate>
                  <TextField
                    onChange={(event) => this.onInputChangedHandler(event, 'email')}
                    value={this.state.controls.email.value}
                    error={!this.state.controls.email.valid && this.state.controls.email.touched}
                    helperText={!this.state.controls.email.valid && this.state.controls.email.touched ? 'enter correct email' : null}
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    id="email"
                    label=""
                    autoComplete="email"
                    autoFocus
                  />
                  <TextField
                    onChange={(event) => this.onInputChangedHandler(event, 'password')}
                    value={this.state.controls.password.value}
                    error={!this.state.controls.password.valid && this.state.controls.password.touched}
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    label=""
                    type="password"
                    id="password"
                    autoComplete="current-password"
                  />
                  {this.props.loading ? 
                  <CircularProgress className={classes.circular} /> : 
                  <Button
                    disabled={!this.state.formIsValid}
                    onClick={(e) => this.onSubmitHandler(e)}
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                  >
                    {''}
                  </Button>}
                  <Grid container>
                  <Grid item xs>
                    <NavLink to="/forgot-password" variant="body2" className={classes.link}>
                       ?
                    </NavLink>
                  </Grid>
                </Grid>
                </form>
              </div>
            </Container>
          </>
        );
    }
}

const mapStateToProps = state => ({
    loading: state.auth.loading,
    error: state.auth.error
});

const mapDispatchToProps = dispatch => ({
    onLogIn: (data) => dispatch(actions.logIn(data))
});

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(LogIn));