import React, {Fragment} from 'react';
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import swip from '../../../../../../../assets/page/Main/swip.svg';
import './index.scss';
import { backendUrl } from '../../../../../../../config/config';

const MainSlider = (props) => {
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
    };
    return (
        <div className={'main-slider'}>
            <Slider {...settings}
                className={'slider-block'}
            >
                {
                    props.list.map((el, idx) =>
                   <Fragment key={idx}>
                       <div

                           className={'list'}
                       >
                           <div className={'list_left'}>
                               <div className={'absolute'}>
                                   <h2>
                                       {el.label}
                                   </h2>
                                   {el.product && 
                                   <p>
                                          {el.product.weight} .
                                   </p>
                                   }
                                   <span style={{whiteSpace: 'pre-line', wordBreak: 'break-all'}}>
                                    {el.product && el.product.description_ru.slice(0, 100)}
                                    </span>
                                   <div className={'absolute_footer'}>
                                       <button
                                       >
                                           {''}
                                       </button>
                                       <div className={'right'}>
                                           {el.product && 
                                           <>
                                                <span>{el.product.price} .</span>
                                                <h2>{(el.product.price * (100 - el.product.percent)/100).toFixed(0)} .</h2>
                                           </>
                                           }
                                       </div>
                                   </div>

                               </div>
                           </div>
                           <div className={'list_right'}>
                               {
                                   el.product ?
                                   <img src={backendUrl + '/' + el.product.image} alt=""/> :
                                   <img src={el.image} alt=""/>
                               }
                           </div>
                       </div>
                        <span className="dots">
                        <img src={swip} alt=""/> 0{el.id}
                        </span>
                   </Fragment>
                        )

                }
            </Slider>
        </div>
    );
}

export default MainSlider;