import React from 'react';
import axios from 'axios';
import { backendUrl } from '../../../../../../config/config';

import { connect } from 'react-redux';
import { Button, CircularProgress } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import DeleteIcon from '@material-ui/icons/Delete';
import ErrorComponent from '../../../../../Errors/Error/Error';
import CustomFoodBlock from '../CustomFoodBlock/CustomFoodBlock';

class FoodSilders extends React.Component {

    state = {
        sliderId: null,
        loading: true,
        error: false,
        slider: null,
        categories: [],
        freeGoods: [],
        goodToAdd: '',
        updateForm: {
            category: '',
            goods: []
        }
    }

    componentDidMount() {
        const sliderId = this.props.match.params.id;
        this.setState({ sliderId: sliderId });

        axios.get(backendUrl + `/admin/sliders/${sliderId}`, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res);
            this.setState({
                slider: res.data,
                error: false,
                updateForm: {
                    ...this.state.updateForm,
                    category: res.data.categoryId ? res.data.categoryId.name_ru : '',
                    goods: res.data.items
                }
            })
            if (res.data.categoryId) {
                axios.get(backendUrl + `/admin/product/category/plz/${res.data.categoryId._id}`, {
                    headers: {
                        'Authorization': `Bearer ${this.props.token}`
                    } 
                })
                .then(res => {
                    console.log(res);
                    this.setState({ 
                        error: false,
                        freeGoods: res.data
                     });
                })
                .catch(err => {
                    console.log(err, err.response);
                    this.setState({ error: true });
                })
            } else {
                this.setState({freeGoods: []});
            }

            axios.get(backendUrl + '/products/categories', {
                headers: {
                    'Authorization': `Bearer ${this.props.token}`
                } 
            })
            .then(res => {
                console.log(res);
                const categories = res.data.cats.filter(
                    el => el._id !== "5ff17a7f6158c22e503feaa1"
                )
                this.setState({
                    loading: false,
                    error: false,
                    categories: categories
                });
            })
            .catch(err => {
                console.log(err);
                this.setState({
                    loading: false,
                    error: true
                })
            });
        })
        .catch(err => {
            console.log(err);
            this.setState({
                error: true,
                loading: false
            })
        });
    }

    onDeleteGood = (id) => {
        let goodsList = [ ...this.state.updateForm.goods];
        goodsList = goodsList.filter((el) => el._id !== id);

        this.setState((prevState) => ({ 
            updateForm: {
                ...prevState.updateForm,
                goods: goodsList
        } 
    }));
    }

    onAddGood = () => {
        const goodName = this.state.goodToAdd;
        const good = this.state.freeGoods.filter(el => el.name_ru === goodName)[0];

        let updatedGoods = this.state.updateForm.goods;
        updatedGoods.push(good);

        console.log(updatedGoods);
            this.setState({
                goodToAdd: '',
                updateForm: {
                    ...this.state.updateForm,
                    goods: updatedGoods
                }
            });
    }

    onChangeCategory = (event) => {
            this.setState((prevState) => ({ updateForm: {
                ...prevState.updateForm,
                category: event.target.value,
                goodToAdd: ''
            }}));

            const categoryObj = this.state.categories.find(el => {
                return el.name_ru === event.target.value; 
            });
            const categoryId = categoryObj._id;
            
            axios.get(backendUrl + `/admin/product/category/plz/${categoryId}`, {
                headers: {
                    'Authorization': `Bearer ${this.props.token}`
                } 
            })
            .then(res => {
                console.log(res);
                this.setState({ 
                    error: false,
                    freeGoods: res.data
                 });
            })
            .catch(err => {
                console.log(err, err.response);
                this.setState({ error: true });
            });
    }

    onEditSlider = () => {
        const category = this.state.categories.find(
            el => el.name_ru === this.state.updateForm.category
            );
        const categoryId = category._id;

        this.setState({ loading: true });
        axios.put(backendUrl + `/admin/sliders/${this.state.sliderId}`, {
            categoryId: categoryId,
            items: this.state.updateForm.goods
        }, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }  
        })
        .then(res => {
            console.log(res);
            this.props.history.push({
                pathname: '/pages/main/food-sliders',
                state: {
                    successAdd: true
                }
            })
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({ 
                error: true,
                loading: false
            });
        });
    }

    render() {

        console.log(this.state);

        return (
            <div style={{
                display: 'flex',
            flexDirection: 'column',
            alignItems: 'center', paddingBottom: '40px'}}
            >
                {this.state.loading && <CircularProgress />}
                {this.state.error && !this.state.loading && <ErrorComponent />}
                { !this.state.error && !this.state.loading && this.state.slider &&
                    <>
                        <p>
                             {'  '} 
                             <span style={{fontWeight: 700}}>
                                {this.state.slider.categoryId ? this.state.slider.categoryId.name_ru : ''} 
                             </span>
                        </p>
                        <p>
                            {'       10!'}
                        </p>
                            <div className={'main-swiper'}>
                                <div className={'main-swiper_header'}>
                                    <h1>
                                        {this.state.updateForm.category}
                                    </h1>
                                    </div>
                                        <div className={'wrapper'}>
                                            {
                                                this.state.updateForm.goods.map(el =>
                                                    <CustomFoodBlock
                                                        key={el._id}
                                                        food={el}
                                                        style={false}
                                                    />
                                                )
                                            }
                                        </div>
                                </div>
                                <div style={{textAlign: 'center'}}>
                                    <p>{':'}</p>
                                    <TextField
                                    id="filled-select-currency"
                                    select
                                    label=""
                                    value={this.state.updateForm.category}
                                    onChange={(e) => this.onChangeCategory(e)}
                                    helperText=" "
                                    variant="filled"
                                    >
                                    {this.state.categories.map((option) => (
                                        <MenuItem key={option._id} value={option.name_ru}>
                                            {option.name_ru}
                                        </MenuItem>
                                    ))}
                                    </TextField>
                                    <div style={{ display: 'flex' }}>
                                        <div>
                                        <p>{' :'}</p>
                                        <ul className="food-slider-list-goods">
                                            {this.state.updateForm.goods[0] && this.state.updateForm.goods.map((el) => (
                                                <li key={el._id}>
                                                    <div>
                                                        <img src={backendUrl + '/' + el.image} />
                                                        <div style={{ marginLeft: '15px' }}>
                                                            <p>{el.name_ru}</p>
                                                        </div>
                                                        <DeleteIcon 
                                                        onClick={() => this.onDeleteGood(el._id)}
                                                        style={{ 
                                                            color: 'brown', cursor: 'pointer'
                                                        }}/>
                                                    </div>
                                                </li>
                                            ))}
                                            {
                                            !this.state.updateForm.goods[0] && 
                                            <p style={{margin: '0 20px'}}>
                                                {' !     '}
                                            </p>
                                            }
                                        </ul>
                                        </div>
                                        <div style={{ 
                                            marginLeft: '50px',
                                            display: 'flex',
                                            flexDirection: 'column',
                                            alignItems: 'center'
                                         }} >
                                            <p>{' '}</p>
                                            <TextField
                                            select
                                            label=""
                                            value={this.state.goodToAdd}
                                            onChange={(event) => this.setState({
                                                goodToAdd: event.target.value
                                            })}
                                            helperText=" "
                                            variant="filled"
                                            >
                                            {this.state.freeGoods.map((option) => {

                                                const ifExist = this.state.updateForm.goods.find(el => el._id === option._id);
                                                if (ifExist) {
                                                    return null;
                                                } else {
                                                    return (
                                                        <MenuItem 
                                                        key={option._id} 
                                                        value={option.name_ru}>
                                                            {option.name_ru}
                                                        </MenuItem>
                                                    )
                                                }
                                            })}
                                            </TextField>
                                            <Button
                                            onClick={this.onAddGood}
                                            disabled={
                                                this.state.goodToAdd === '' 
                                                ||
                                                this.state.updateForm.goods.length >= 10
                                        }
                                            style={{marginTop: '20px'}}
                                            color="primary"
                                            variant="outlined">
                                                {''}
                                            </Button>
                                        </div>
                                    </div>
                                </div>
                    </>
                }
                <Button
                onClick={this.onEditSlider}
                color="primary"
                variant="contained"
                style={{
                    marginTop: '30px'
                }}
                >
                    {''}
                </Button>
            </div>
        );
    }
};

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(FoodSilders);
