import React from 'react';
import { Route, Switch, Redirect, NavLink } from 'react-router-dom';
import { links } from './list';

import Categories from './components/Caterogies/Categories';
import AddCategory from './components/AddCategory/AddCategory';
import EditCategory from './components/EditCategory/EditCategory';

import AddGoods from './components/AddGoods/AddGoods';
import EditGoods from './components/EditGoods/EditGoods';
import Goods from './components/Goods/Goods';

import Filters from './components/Filters/Filters';
import AddFilter from './components/AddFilter/AddFilter';
import EditFilter from './components/EditFilter/EditFilter';

class Storage extends React.Component {
    render() {

        const routes = (
            <Switch>
                <Route path="/storage/categories" component={Categories} />
                <Route path="/storage/add-category" component={AddCategory} />
                <Route path="/storage/edit-category/:id" component={EditCategory} />

                <Route path="/storage/goods" component={Goods} />
                <Route path="/storage/add-goods" component={AddGoods} />
                <Route path="/storage/edit-goods/:id" component={EditGoods} />

                <Route path="/storage/filters" component={Filters} />
                <Route path="/storage/add-filter" component={AddFilter} />
                <Route path="/storage/edit-filtery/:id" component={EditFilter} />

                <Redirect exact from="/storage" to="/storage/goods" />
            </Switch>
        );

        return (
            <>  
                <div className="all-admins_header">
                    {links.map((el, index) => (
                        <NavLink 
                        key={index} 
                        to={el.to} 
                        activeClassName="all-admins_header__item-active"
                        className='all-admins_header__item'>
                            {el.label}
                        </NavLink>
                    ))}
                </div>
                {routes}
            </>
        );
    }
}

export default Storage;