import React from 'react';
import axios from 'axios';
import { backendUrl } from '../../../../config/config';
import { connect } from 'react-redux';
import ErrorComponent from '../../../Errors/Error/Error';
import AddGood from '../CreateOrder/AddGood/AddGood';


import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import DeleteIcon from '@material-ui/icons/Delete';
import { CircularProgress } from '@material-ui/core';

const allStatuses = [
    {
       name: ' ',
       id: 'check'
    },
    {
        name: '',
        id: 'no-call'
    },
    {
        name: '',
        id: 'new'
    },
    {
        name: ' ',
        id: 'send-to-delivery'
    },
    {
        name: '',
        id: 'complete'
    },
    {
        name: '',
        id: 'send-to-assembling'
    },
    {
        name: '',
        id: 'delivering'
    },
    {
        name: '',
        id: 'client-confirmed'
    },
    {
        name: '',
        id: 'samovivoz'
    },
    {
        name: '',
        id: 'cancel-other'
    },
    {
        name: '',
        id: 'prepayed'
    },
    {
        name: ' ',
        id: 'assembling'
    }
]

class UpdateOrder extends React.Component {


    state = {
        loading: false,
        error: false,
        addMode: false,
        shops: [],
        categories: [],
        form: {
            city: '',
            name: '',
            phone: '',
            address: '',

            deliveryType: ' ',
            paymentType: '',
            paymentStatus: ' ',

            orderStatus: '',

            cutlery: 0,
            payBack: 0,
            comment: ''
        },
        currentGoods: [],
        bonus: 0,
        orderId: '',
    }

    componentDidMount() {

        this.setState({ loading: true });

        // get shops
        axios.get(backendUrl + '/admin/shop/', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            this.setState({
                shops: res.data,
                error: false,
                form: {
                    ...this.state.form,
                    city: res.data[0].name
                }
            })
        })
        .catch(err => {
            this.setState({ error: true, loading : false });
        });


        //get categories
        axios.get(backendUrl + '/admin/product/categories', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res);
            this.setState({
                categories: res.data.message,
                error: false
            });
        })
        .catch(err => {
            this.setState({ error: true, loading : false });
        });

        // get order by id
        const orderId = this.props.match.params.id;
        this.setState({ orderId: orderId });
        console.log(orderId);
        axios.get(backendUrl + `/admin/order/${orderId}`, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res);

            const orderName = allStatuses.filter(el => el.id === res.data.order.status)[0].name;

            this.setState({
                loading: false,
                error: false,
                form: {
                    ...this.state.form,
                    city: res.data.order.address.city,
                    name: res.data.order.userId.name,
                    phone: res.data.order.userId.phoneNumber,
                    address: res.data.order.address.text,

                    deliveryType: res.data.order.delivery.status === 'courier' ?
                    ' ' : '',
                    paymentType: res.data.order.oplata.type === 'cash' ? 
                    '' : ' ',
                    paymentStatus: res.data.order.oplata.status === 'not-paid' ?
                    ' ' : '',

                    orderStatus: orderName,

                    cutlery: res.data.order.cutlery,
                    payBack: res.data.order.sdachaS,
                    comment: res.data.order.comment
                },
                currentGoods: res.data.order.items.map(el => ({
                    ...el.product,
                    quantity: el.quantity
                })),
                bonus: res.data.order.bonus
            });
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({
                error: true,
                loading: false
            })
        })
    }

    onSelectOption = (e, id) => {
        this.setState({
            form: {
                ...this.state.form,
                [id]: e.target.value
            }
        })
    }

    onChangeInput = (e, id) => {
        const updatedForm = {
            ...this.state.form
        };
        updatedForm[id] = e.target.value;

        this.setState({ form: updatedForm });
    }

    onAddGood = (product) => {
        this.setState({addMode: false});

        let updatedCurrentGoods = [ ...this.state.currentGoods ];
        const goodIsExist = updatedCurrentGoods.filter(el => el._id === product._id);
        if (goodIsExist[0]) {
            updatedCurrentGoods = updatedCurrentGoods.map(el => {
                if (el._id === product._id) {
                    return {
                        ...el,
                        quantity: el.quantity + product.quantity
                    }
                } else return el;
            })
        } else {
            updatedCurrentGoods.push(product);
        }

        this.setState({currentGoods: updatedCurrentGoods});

    }

    onAddQuantity = (id) => {
        let newFindedGoods = [ ...this.state.currentGoods ];
        newFindedGoods = newFindedGoods.map(el => {
            if (el._id === id) {
                return {
                ...el,
                quantity: el.quantity + 1
                }
            } else {
                return el;
            }
        });
        this.setState({
            currentGoods: newFindedGoods
        });
    }

    onMinusQuantity = (id) => {
        let newFindedGoods = [ ...this.state.currentGoods ];
        newFindedGoods = newFindedGoods.map(el => {
            if (el._id === id) {
                return {
                ...el,
                quantity: el.quantity === 1 ? 1 : el.quantity - 1
                }
            } else {
                return el;
            }
        });
        this.setState({
            currentGoods: newFindedGoods
        });
    }

    onDeleteGood = (id) => {
        const newGoods = [ ...this.state.currentGoods ].filter(el => el._id !== id);
        this.setState({
            currentGoods: newGoods
        });
    }

    onUpdateOrder = () => {
        this.setState({ loading: true });

        let searchCity;
        if (this.state.form.city === 'Kharkov') {
            searchCity = 'Kh'
        } else if (this.state.form.city === 'Zaporijja') {
            searchCity = 'Zp'
        } else {
            searchCity = 'Dp'
        }
        const shop = [ ...this.state.shops ].filter(el => el.name === searchCity);
        const shopId = shop[0]._id;

        const allGoods = [ ...this.state.currentGoods ].map(el => ({
            product: el._id,
            quantity: el.quantity
        }));

        const statusName = allStatuses.filter(el => el.name === this.state.form.orderStatus)[0].id;

        axios.put(backendUrl + `/admin/order/${this.state.orderId}`, {
            status: statusName,
            shop: shopId,
            name: this.state.form.name,
            phone: this.state.form.phone,
            cutlery: this.state.form.cutlery,
            comment: this.state.form.comment,
            sdachaS: this.state.form.payBack,
            delivery: {
                deliver: 'roflik',
                status: this.state.form.deliveryType === ' ' ? 'courier' : 'self-delivery'
            },
            oplata: {
                type: this.state.form.paymentType === '' ? 'cash' : 'bank-card',
                status: this.state.form.paymentStatus === ' ' ? 'not-paid': 'paid'
            },
            address: {
                text: this.state.form.address,
                city: this.state.form.city
            },
            items: allGoods
        }, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res);
            this.props.history.push('/orders');
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({ error: true, loading: false });
        });
    }

    onDeleteOrder = () => {
        this.setState({loading: false});

        axios.delete(backendUrl + `/admin/order/${this.state.orderId}`, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            this.setState({error: false});
            this.props.history.push('/orders');
        })
        .catch(err => {
            this.setState({
                error: true,
                loading: false
            });
        })
    }

    render() {

        console.log(this.state);

        const totalSum = 
        this.state.currentGoods.reduce(
            (acc, val) => acc + Number((val.price * val.quantity * (100 - val.percent)/100).toFixed(0)), 
        0);

        const formIsValid = this.state.currentGoods[0] && this.state.form.name !== '' && this.state.form.phone !== '' && this.state.form.street !== '';

        return (
            <div className="create-order">
                { this.state.loading && !this.state.error && <CircularProgress /> }
                { this.state.error && !this.state.loading && <ErrorComponent /> }
                { this.state.shops[0] && this.state.categories[0] && !this.state.loading && !this.state.error &&
                <>  
                    <AddGood 
                        shops={this.state.shops}
                        categories={this.state.categories}
                        onAddGood={this.onAddGood}
                        show={this.state.addMode}
                        modalClosed={()=>this.setState({addMode: false})}
                    />
                    <h3>{' '}</h3>
                    {/* shop */}
                    <div className="create-order__omposition">


                        <div className='create-order__omposition_header'>
                            <h3>{' '}</h3>
                            <Button
                            onClick={()=>this.setState({addMode: true})}
                            color="primary"
                            variant="contained"
                            style={{
                                fontSize: '12px',
                                textTransform: 'capitalize'
                            }}>
                                {' '}
                            </Button>
                        </div>

                        <div className="create-order__omposition_body">
                         {!this.state.currentGoods[0] && 
                         <p style={{
                             textAlign: 'center'
                         }}>
                             {'    ...'}
                         </p> 
                         }   
                         {this.state.currentGoods[0] && 
                            <ul>
                                <li style={{
                                        width: '100%',
                                        borderBottom: '1px solid #aaa',
                                        display: 'flex',
                                        flexDirection: 'row',
                                        padding: '10px 0'
                                }}>
                                        <div className="ul-li-good">
                                            {''}
                                        </div>
                                        <div className="ul-li-price">
                                            {''}
                                        </div>
                                        <div className="ul-li-quantity">
                                            {''}
                                        </div>
                                        <div className="ul-li-add">
                                            {''}
                                        </div>
                                </li>
                            {this.state.currentGoods.map(el => (
                                    <li style={{
                                        width: '100%',
                                        borderBottom: '1px solid #aaa',
                                        display: 'flex',
                                        flexDirection: 'row',
                                        padding: '10px 0'
                                    }}>
                                        <div className="ul-li-good">
                                            <img 
                                            style={{maxWidth: '50px', maxHeight: '50px'}}
                                            src={backendUrl + '/' + el.image}
                                            />
                                            <span style={{marginLeft:'15px'}}> 
                                                {el.name_ru}
                                            </span>
                                        </div>
                                        <div className="ul-li-price">
                                            <span>{(el.price * (100 - el.percent)/100).toFixed(0)}{' '}</span>
                                            {el.percent !== 0 && 
                                                <span style={{textDecoration: 'line-through', marginLeft: '5px'}}>
                                                    {el.price}{' '}
                                                </span>
                                            }
                                        </div>
                                        <div className="ul-li-quantity">
                                            <IconButton 
                                            onClick={() => this.onMinusQuantity(el._id)}
                                            aria-label="delete"
                                            >
                                                <RemoveIcon fontSize="small" />
                                            </IconButton>
                                            {el.quantity}{' .'}
                                            <IconButton 
                                            onClick={() => this.onAddQuantity(el._id)}
                                            aria-label="delete"
                                            >
                                                <AddIcon fontSize="small" />
                                            </IconButton>
                                        </div>
                                        <div className="ul-li-add">
                                        <IconButton 
                                            onClick={() => this.onDeleteGood(el._id) }
                                            aria-label="delete"
                                            >
                                                <DeleteIcon
                                                    style={{
                                                        color: 'brown'
                                                    }} 
                                                    fontSize="small" 
                                                />
                                            </IconButton>
                                        </div>
                                    </li>
                                ))}
                            </ul>
                         }
                        </div>


                        <div className="create-order__omposition_footer">
                            <p>{` : ${totalSum} .`}</p>
                            <p>{` : ${this.state.bonus} .`}</p>
                            <p>
                                {': '}
                                <span 
                                    style={{
                                        fontWeight: 700
                                    }}
                                >
                                    {`${totalSum - this.state.bonus} .`}
                                </span>
                            </p>
                        </div>


                    </div>
                    <h4>{''}</h4>
                    <div className="create-order_container">
                        <p>{':'}</p>
                        <TextField
                            select
                            value={this.state.form.city}
                            onChange={(e) => this.onSelectOption(e, 'city')}
                            SelectProps={{ native: true }}
                            helperText=" "
                            style={{marginLeft: '30px'}}
                        >
                            {this.state.shops.map((el) => (
                                <option key={el._id} value={el.name}>
                                    {el.name}
                                </option>
                            ))}
                        </TextField>
                    </div>
                    <div className="create-order_container">
                        <p>{' :'}</p>
                        <TextField
                            select
                            value={this.state.form.deliveryType}
                            onChange={(e) => this.onSelectOption(e, 'deliveryType')}
                            SelectProps={{ native: true }}
                            helperText="  "
                            style={{marginLeft: '30px'}}
                        >
                            {[' ', ''].map((el) => (
                                <option key={el} value={el}>
                                    {el}
                                </option>
                            ))}
                        </TextField>
                    </div>
                    <div className="create-order_container">
                        <p>{' :'}</p>
                        <TextField
                            select
                            value={this.state.form.paymentType}
                            onChange={(e) => this.onSelectOption(e, 'paymentType')}
                            SelectProps={{ native: true }}
                            helperText="  "
                            style={{marginLeft: '30px'}}
                        >
                            {['', ' '].map((el) => (
                                <option key={el} value={el}>
                                    {el}
                                </option>
                            ))}
                        </TextField>
                    </div>
                    {/*   */}
                    <h4 style={{color: 'forestgreen', fontSize: '20px'}}>
                        {''}
                    </h4>
                    <div className="create-order_container" style={{color: 'forestgreen'}}>
                        <p>{'  :'}</p>
                        <TextField
                            select
                            value={this.state.form.paymentStatus}
                            onChange={(e) => this.onSelectOption(e, 'paymentStatus')}
                            SelectProps={{ native: true }}
                            style={{marginLeft: '30px'}}
                        >
                            {['', ' '].map((el) => (
                                <option key={el} value={el}>
                                    {el}
                                </option>
                            ))}
                        </TextField>
                    </div>
                    <div className="create-order_container" style={{color: 'forestgreen'}}>
                        <p>{'  :'}</p>
                        <TextField
                            select
                            value={this.state.form.orderStatus}
                            onChange={(e) => this.onSelectOption(e, 'orderStatus')}
                            SelectProps={{ native: true }}
                            style={{marginLeft: '30px'}}
                        >
                            {allStatuses.map((el) => (
                                <option key={el.id} value={el.name}>
                                    {el.name}
                                </option>
                            ))}
                        </TextField>
                    </div>
                    {/*  */}
                    <h4>{''}</h4>
                    <div className="create-order_container">
                        <p>{':'}</p>
                        <input
                        value={this.state.form.name}
                        onChange={(e) => this.onChangeInput(e, 'name')}
                        />
                    </div>
                    <div className="create-order_container">
                        <p>{' :'}</p>
                        <input
                        value={this.state.form.phone}
                        onChange={(e) => this.onChangeInput(e, 'phone')}
                        />
                    </div>
                    {/*  */}
                    <h4>{''}</h4>
                    <div className="create-order_container">
                        <p>{':'}</p>
                        <textarea
                        style={{height: '200px'}}
                        value={this.state.form.address}
                        onChange={(e) => this.onChangeInput(e, 'address')}
                        />
                    </div>
                        {/*     */}
                        <h4>{'   '}</h4>
                        {/*  , ,    */}
                        <div className="create-order_container">
                            <p>{':'}</p>
                            <input 
                            value={this.state.form.cutlery}
                            onChange={(e) => this.onChangeInput(e, 'cutlery')}
                            type="number"
                            />
                        </div>
                        <div className="create-order_container">
                            <p>{' :'}</p>
                            <input 
                            value={this.state.form.payBack}
                            onChange={(e) => this.onChangeInput(e, 'payBack')}
                            type="number"
                            />
                        </div>
                        <div className="create-order_container">
                            <p>{' :'}</p>
                            <textarea
                            value={this.state.form.comment}
                            onChange={(e) => this.onChangeInput(e, 'comment')}
                            />
                        </div>
                        <Button
                        onClick={this.onUpdateOrder}
                        variant="contained"
                        color="primary"
                        disabled={!formIsValid}
                        style={{marginTop: '20px'}}>
                            {' '}
                        </Button>
                        <Button
                        onClick={this.onDeleteOrder}
                        variant="contained"
                        color="secondary"
                        style={{marginTop: '20px'}}>
                            {' '}
                        </Button>
                </>
                }
            </div>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
})

export default connect(mapStateToProps)(UpdateOrder);