import axios from 'axios';
import React from 'react';
import { connect } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { backendUrl } from '../../../../config/config';
import { withStyles } from '@material-ui/styles';
import Modal from '../../../UI/Modal/Modal';
import ErrorComponent from '../../../Errors/Error/Error';
import CircularProgress from '@material-ui/core/CircularProgress';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import Table from '@material-ui/core/Table';
import Paper from '@material-ui/core/Paper';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TablePagination from '@material-ui/core/TablePagination';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import TextField from '@material-ui/core/TextField';
import { useJsonToCsv } from 'react-json-csv';
import { sortStrings, sortNumbers } from '../../../../tools/sort';
import * as FileSaver from 'file-saver';
import * as XLSX from 'xlsx';

const style = theme => ({
    table: {
        width: '100%'
    },
    edit: {
        color: '#4caf50',
        cursor: 'pointer',
        '&:hover': {
            color: '#357a38'
        }
    },
    delete: {
        color: '#b71c1c',
        cursor: 'pointer',
        '&:hover': {
            color: '#801313'
        }
    },
    paper: {
        width: '90%',
        marginTop: '20px',
        marginBottom: '60px'
    },
    visuallyHidden: {
        border: 0,
        clip: 'rect(0 0 0 0)',
        height: 1,
        margin: -1,
        overflow: 'hidden',
        padding: 0,
        position: 'absolute',
        top: 20,
        width: 1,
    },
    button: {
        backgroundColor: '#4caf50',
        marginLeft: '20px',
        color: 'white',
        '&:hover': {
            backgroundColor: '#357a38'
        }
    },
    descrContDiv: {
        whiteSpace: 'pre-wrap'
    },
    descrCont: {
        maxWidth: '300px',
        wordWrap: 'break-word'
    },
    cancelButton: {
        textTransform: 'capitalize',
        marginLeft: '15px',
        backgroundColor: '#b71c1c',
        '&:hover': {
            backgroundColor: '#801313'
        }
    }
});

const headCells = [
    { id: 'image', numeric: false, disablePadding: true, label: '' },
    { id: 'name_ru', numeric: false, disablePadding: false, label: '' },
    { id: 'category', numeric: false, disablePadding: false, label: '' },
    { id: 'filter', numeric: false, disablePadding: false, label: '' },
    { id: 'shops', numeric: false, disablePadding: false, label: '' },
    { id: 'price', numeric: true, disablePadding: false, label: ' ' },
    { id: 'percent', numeric: true, disablePadding: false, label: '' },
    { id: 'weight', numeric: true, disablePadding: false, label: '' },
    { id: 'edit', numeric: false, disablePadding: false, label: '' },
    { id: 'delete', numeric: false, disablePadding: false, label: '' }
];

const formats = [
    {
        value: 'excel',
        label: 'excel',
    },
    {
        value: 'csv',
        label: 'csv',
    },
    {
        value: 'json',
        label: 'json',
    }
]

class Goods extends React.Component {

    state = {
        error: false,
        loading: true,
        categories: [''],
        filters: [''],
        page: 0,
        rowsPerPage: 10,
        deleteMode: null,
        modalForSuccessAdd: false,
        modalForSuccessChanged: false,
        modalupload: false,
        format: 'csv',
        orderBy: 'name',
        order: 'asc',
        goods: null,
        file: null,
        searchArea: {
            name: '',
            category: '',
            ingredients: '',
            filter: ''
        },
        
    }

    componentDidMount() {
        this.setState({ loading: true });


        axios.get(backendUrl + '/admin/product/categories', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                console.log(res, res.data);
                const updatedCategories = this.state.categories.concat(res.data.message);
                this.setState({
                    categories: updatedCategories,
                    searchArea: {
                        ...this.state.searchArea,
                        category: res.data.message[0].name_ru
                    }
                });
            })
            .catch(err => {
                console.log(err, err.data);
                this.setState({ error: true });
            });

        axios.get(backendUrl + '/admin/product/filters', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                console.log(res, res.data);
                const updatedFilters = this.state.filters.concat(res.data.fils);
                if (res.data.fils[0]) {
                    this.setState({
                        filters: updatedFilters,
                        searchArea: {
                            ...this.state.searchArea,
                            filter: res.data.fils[0].name_ru
                        }
                    });
                }
            })
            .catch(err => {
                console.log(err, err.data);
                this.setState({ error: true });
            });

        if (this.props.location.search === '?success-created=true') {
            this.setState({ modalForSuccessAdd: true });
        } else if (this.props.location.search === '?success-changed=true') {
            this.setState({ modalForSuccessChanged: true });
        }

        axios.get(backendUrl + '/admin/product/', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                console.log(res, res.data);
                const goods = res.data.message;
                this.setState({ goods: goods });
            })
            .catch(err => {
                console.log(err, err.response);
                const message = err.response ? err.response.data.error.toString() || err.response.data.message.toString() : err.toString();
                this.setState({ error: message });
            })
            .finally(() => {
                this.setState({ loading: false });
            })
    }

    handleChangeRowsPerPage = (event) => {
        this.setState({
            rowsPerPage: parseInt(event.target.value, 10),
            page: 0
        })
    };

    handleChangePage = (event, newPage) => {
        this.setState({
            page: newPage
        })
    };

    onDeleteGood = (id) => {

        axios.delete(backendUrl + `/admin/product/${id}`, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                console.log(res);
                window.location.reload();
            })
            .catch(err => {
                console.log(err, err.response);
                this.setState({ error: true });
            });
    }

    onEditGood = (id) => {
        this.props.history.push(`/storage/edit-goods/${id}`);
    }

    handleRequestSort = (event, property) => {
        const isAsc = this.state.orderBy === property && this.state.order === 'asc'
        this.setState({
            order: isAsc ? 'desc' : 'asc',
            orderBy: property,
        });
        const goods = this.state.goods;
        const elem = headCells.find(el => {
            if (el.id === property) {
                return true;
            } else return false;
        });
        if (elem.numeric) {
            this.setState({
                goods: sortNumbers(property, goods, this.state.order !== 'desc' ? 'asc' : 'desc')
            });
        } else {
            this.setState({
                goods: sortStrings(property, goods, this.state.order !== 'desc' ? 'asc' : 'desc')
            });
        }
    };
    onDownload = () => {
        axios.get(backendUrl + '/admin/product/file/download/plz', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                console.log(res.data);
                let response
                if (this.state.format === 'json') {
                    response = new Blob([JSON.stringify(res.data)], { type: 'application/json' });
                    console.log(response);
                    let url = window.URL.createObjectURL(response);
                    let a = document.createElement('a');
                    a.href = url;
                    a.download = 'products.json';
                    a.click();
                }
                else if (this.state.format === 'excel') {
                    const fileType = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
                        const fileExtension = 'products.xlsx';
                        const ws = XLSX.utils.json_to_sheet(res.data);
                        const wb = { Sheets: { 'data': ws }, SheetNames: ['data'] };
                        const excelBuffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
                        const data = new Blob([excelBuffer], {type: fileType});
                        FileSaver.saveAs(data, fileExtension);
                    
                }

                else if (this.state.format === 'csv') {
                    const filename = 'products',
                        fields = {
                            "_id": "Index",
                            "name_ru": "Name",
                            "description_ru": "Description",
                            "image": "Image",
                            "price": "Price"
                        },
                        data = res.data
                    const { saveAsCsv } = useJsonToCsv();
                    saveAsCsv({ data, fields, filename })
                }

            })
            .catch(err => {
                console.log(err, err.response);
                this.setState({ error: true });
            })
            .finally(() => {
                this.setState({ loading: false });
            })
    }
    onSelectIconHandler = (event) => {
        const reader = new FileReader();
        reader.onloadend = () => {

            let formData = new FormData();
            formData.append('excel', file);
            console.log('zxc');
            axios.post(backendUrl + '/admin/product/file/upload', formData, {
                headers: {
                    'Authorization': `Bearer ${this.props.token}`
                }
            })
                .then(res => {
                    console.log(res.data);
                    this.setState({ error: null });
                    this.props.history.push('/storage/goods?success-created=true');
                })
                .catch(err => {
                    console.log(err, err.response);
                    this.setState({ error: true });
                })
                .finally(() => {
                    this.setState({ loading: false });
                })
        };
        const file = event.target.files[0];
        reader.readAsDataURL(file);



    }



    onSearch = () => {
        this.setState({ loading: true });
        const categoryElem = this.state.categories.find(el => {
            if (el.name_ru === this.state.searchArea.category) {
                return true;
            } else return false;
        })
        const categoryId = categoryElem ? categoryElem._id : null;

        const filterElem = this.state.filters.find(el => {
            if (el.name_ru === this.state.searchArea.category) {
                return true;
            } else return false;
        })
        const filterId = filterElem ? filterElem._id : null;

        axios.post(backendUrl + '/admin/product/search', {
            description_ru: this.state.searchArea.ingredients,
            name_ru: this.state.searchArea.name,
            categoryId: categoryId
        }, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                console.log(res);
                this.setState({
                    searchMode: true,
                    goods: res.data.products
                });
            })
            .catch(err => {
                console.log(err, err.response);
                this.setState({ error: true });
            })
            .finally(() => {
                this.setState({ loading: false });
            });
    }

    onCategoryChangedHandler = (e) => {
        this.setState({
            searchArea: {
                ...this.state.searchArea,
                category: e.target.value
            }
        })
    }

    onFilterChangedHandler = (e) => {
        this.setState({
            searchArea: {
                ...this.state.searchArea,
                filter: e.target.value
            }
        });
    }

    uploadModalTrue = () => {
        this.setState({ modalupload: true })
    }

    render() {

        const { classes } = this.props;
        let modalDeleteContent = null;

        const modalForUpload = (
            <>
                <div>
                    <TextField
                        id="standard-select-currency"
                        select
                        label="Select"
                        value={this.state.format}
                        onChange={(e) => {
                            this.setState({
                                format: e.target.value
                            })
                        }}
                        helperText="Please select your format"
                    >
                        {formats.map((option) => (
                            <option key={option.label} value={option.value}>
                                {option.label}
                            </option>
                        ))}
                    </TextField>
                    <Button
                        onClick={() => { this.onDownload() }}
                        variant="contained"
                    >
                        
                </Button>
                </div>
            </>
        )

        const modalForSuccessAdd = (
            <>
                <p style={{ textAlign: 'center' }}>
                    {'   '}
                </p>
            </>
        );

        const modalForSuccessChange = (
            <>
                <p style={{ textAlign: 'center' }}>
                    {'   '}
                </p>
            </>
        );

        if (this.state.deleteMode !== null) {
            modalDeleteContent = (
                <div style={{
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center',
                    alignItems: 'center'
                }} >
                    <p style={{ textAlign: 'center' }}>
                        {'     '}
                        <span style={{ fontWeight: 700 }} >{this.state.deleteMode[1]}</span>
                    </p>
                    <Button
                        onClick={() => this.onDeleteGood(this.state.deleteMode[0])}
                    >
                        {''}
                    </Button>
                </div>
            );
        };

        return (
            <>
                {this.state.error ?
                    <ErrorComponent /> :
                    <>
                        <Modal
                            show={this.state.modalupload}
                            modalClosed={() => {
                                this.setState({ modalupload: false });
                                this.props.history.push('/storage/goods');
                            }}>
                            {modalForUpload}
                        </Modal>
                        <Modal
                            show={this.state.modalForSuccessAdd}
                            modalClosed={() => {
                                this.setState({ modalForSuccessAdd: false });
                                this.props.history.push('/storage/goods');
                            }}>
                            {modalForSuccessAdd}
                        </Modal>
                        <Modal
                            show={this.state.modalForSuccessChanged}
                            modalClosed={() => {
                                this.setState({ modalForSuccessChanged: false });
                                this.props.history.push('/storage/goods');
                            }}>
                            {modalForSuccessChange}
                        </Modal>
                        <Modal
                            show={this.state.deleteMode !== null}
                            modalClosed={() => {
                                this.setState({ deleteMode: null });
                            }}>
                            {modalDeleteContent}
                        </Modal>
                        {this.state.loading ?
                            <CircularProgress />
                            :
                            <>
                                <div className="users-list__content-area__controls__top">
                                    <h1></h1>
                                    <div>
                                        <NavLink to="/storage/add-goods">
                                            <Button
                                                variant="contained"
                                                color="primary"
                                                startIcon={<AddIcon />}>
                                                {''}
                                            </Button>
                                        </NavLink>
                                    </div>
                                </div>
                                <div className="users-list__content-area__controls__search">
                                    <div className="users-list__content-area__controls__search__inputs">
                                        <span className="users-list__content-area__controls__search__inputs__item">
                                            <TextField
                                                onChange={(e) => this.setState((prevState) => ({
                                                    searchArea: {
                                                        ...prevState.searchArea,
                                                        name: e.target.value
                                                    }
                                                }))}
                                                className={classes.input}
                                                label=""
                                                variant="outlined"
                                            />
                                        </span>
                                        <span className="users-list__content-area__controls__search__inputs__item">
                                            <TextField
                                                select
                                                label=""
                                                value={this.state.searchArea.category}
                                                onChange={this.onCategoryChangedHandler}
                                                SelectProps={{
                                                    native: true,
                                                }}
                                                variant="outlined"
                                            >
                                                {this.state.categories.map((option, index) => (
                                                    <option key={index} value={option.name_ru}>
                                                        {option.name_ru}
                                                    </option>
                                                ))}
                                            </TextField>
                                        </span>
                                        <span className="users-list__content-area__controls__search__inputs__item">
                                            <TextField
                                                select
                                                label=""
                                                value={this.state.searchArea.filter}
                                                onChange={this.onFilterChangedHandler}
                                                SelectProps={{
                                                    native: true,
                                                }}
                                                variant="outlined"
                                            >
                                                {this.state.filters.map((option, index) => (
                                                    <option key={index} value={option.name_ru}>
                                                        {option.name_ru}
                                                    </option>
                                                ))}
                                            </TextField>
                                        </span>
                                        <span className="users-list__content-area__controls__search__inputs__item">
                                            <TextField
                                                onChange={(e) => this.setState((prevState) => ({
                                                    searchArea: {
                                                        ...prevState.searchArea,
                                                        ingredients: e.target.value
                                                    }
                                                }))}
                                                className={classes.input}
                                                label=""
                                                variant="outlined"
                                            />
                                        </span>
                                        <Button
                                            onClick={this.onSearch}
                                            variant="contained"
                                            className={classes.button}>
                                            {''}
                                        </Button>
                                        {this.state.searchMode &&
                                            <Button
                                                onClick={() => window.location.reload()}
                                                className={classes.cancelButton}
                                                variant="contained"
                                                color="secondary">
                                                {''}
                                            </Button>}
                                    </div>
                                </div>
                                <div style={{
                                    display: 'flex',
                                    alignItems: 'center',
                                    justifyContent: 'center'
                                }}>
                                    <Paper elevation={2} className={classes.paper}>
                                        <Table className={classes.table} size="medium" aria-label="a dense table">
                                            <TableHead>
                                                <TableRow>
                                                    {headCells.map((headCell, index) => {
                                                        return (
                                                            <TableCell
                                                                align="left"
                                                                key={headCell.id}
                                                                sortDirection={
                                                                    headCell.id !== this.state.orderBy
                                                                        ? false : this.state.order
                                                                }>
                                                                <TableSortLabel
                                                                    active={
                                                                        headCell.id !== 'edit' &&
                                                                        headCell.id !== 'delete' &&
                                                                        headCell.id !== 'image'
                                                                    }
                                                                    direction={
                                                                        headCell.id === this.state.orderBy
                                                                            ? this.state.order : 'asc'
                                                                    }
                                                                    onClick={(e) => this.handleRequestSort(e, headCell.id)}
                                                                >
                                                                    {headCell.label}
                                                                    {
                                                                        headCell.id !== 'edit' &&
                                                                            headCell.id !== 'delete'
                                                                            && headCell.id !== 'image' ? (
                                                                                <span className={classes.visuallyHidden}>
                                                                                    {this.state.order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                                                                                </span>
                                                                            ) : null}
                                                                </TableSortLabel>
                                                            </TableCell>
                                                        );
                                                    }
                                                    )}
                                                </TableRow>
                                            </TableHead>
                                            <TableBody>
                                                {this.state.goods
                                                    .slice(
                                                        this.state.page * this.state.rowsPerPage,
                                                        this.state.page * this.state.rowsPerPage
                                                        + this.state.rowsPerPage).map((user) => (
                                                            <TableRow key={user._id}>
                                                                <TableCell component="th" scope="row" align="center">
                                                                    <div className="food-block-container">
                                                                        <div className="list_header">
                                                                            <img
                                                                                alt=""
                                                                                src={backendUrl + '/' + user.image} />
                                                                        </div>
                                                                    </div>
                                                                </TableCell>
                                                                <TableCell align="left">{user.name_ru}</TableCell>
                                                                <TableCell align="left">{user.categoryId.name_ru}</TableCell>
                                                                <TableCell align="left">
                                                                    {user.filter ? user.filter.name_ru : ''}
                                                                </TableCell>
                                                                <TableCell align="left" className={classes.descrCont}>
                                                                    <p> {user.shop.map(el => el.name + ', ')} </p>
                                                                </TableCell>
                                                                <TableCell align="left">{user.price}</TableCell>
                                                                <TableCell align="left">{user.percent + '%'}</TableCell>
                                                                <TableCell align="left">{user.weight}</TableCell>
                                                                <TableCell align="left">
                                                                    <EditIcon
                                                                        onClick={() => this.onEditGood(user._id)}
                                                                        className={classes.edit} />
                                                                </TableCell>
                                                                <TableCell align="left">
                                                                    <DeleteIcon
                                                                        onClick={() => this.setState({ deleteMode: [user._id, user.name_ru] })}
                                                                        className={classes.delete} />
                                                                </TableCell>
                                                            </TableRow>
                                                        ))}
                                            </TableBody>
                                        </Table>
                                        <TablePagination
                                            className={classes.paginator}
                                            rowsPerPageOptions={[5, 10, 25]}
                                            component="div"
                                            count={this.state.goods.length}
                                            rowsPerPage={this.state.rowsPerPage}
                                            page={this.state.page}
                                            onChangePage={this.handleChangePage}
                                            onChangeRowsPerPage={this.handleChangeRowsPerPage}
                                            labelRowsPerPage={'   :'}
                                        />
                                        <div style={{ alignSelf: 'flex-start', marginTop: "-3.6%" }}>
                                            <Button
                                                onClick={this.uploadModalTrue}
                                                variant="contained"
                                            >
                                                 
                                        </Button>
                                        </div>
                                        <div style={{ alignSelf: 'flex-start', marginTop: "-3%", marginLeft: "20%" }}>
                                            <Button variant="contained">
                                                <label htmlFor="idishnik"> </label>
                                                <input
                                                    id="idishnik"
                                                    onChange={(event) => this.onSelectIconHandler(event)}
                                                    type="file"
                                                    accept="*"
                                                    style={{ display: "none" }}
                                                />
                                            </Button>
                                        </div>

                                    </Paper>
                                </div>
                            </>
                        }
                    </>}
            </>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(withStyles(style)(Goods));