import React from 'react';
import { Switch, Route,  Redirect } from 'react-router-dom';

import clientList from './clientsList/clientsList'; 
import clientEdit from './clientsEdit/clientEdit'; 
import clientCheck from './clientsCheck/ClientsCheck';


class Clients extends React.Component {
    render() {
        const routes = (
            <Switch>
                <Route path="/clients/list" component={clientList} />
                <Route exact path="/clients/edit/:id" component={clientEdit} />
                <Route  path="/clients/check/:id" component={clientCheck} />
                <Redirect from="/clients" to="/clients/list" />
            </Switch>
        );

        return (
            <>
                {routes}
            </>
        );
    }
}

export default Clients;