import React from 'react';
import axios from 'axios';
import {backendUrl} from '../../../../../../config/config';
import CircularProgress from '@material-ui/core/CircularProgress';
import ErrorComponent from '../../../../../Errors/Error/Error';
import Button from '@material-ui/core/Button';
import Slider from '@material-ui/core/Slider';
import ReactStars from 'react-rating-stars-component';
import { checkValidaty } from '../../../../../../tools/checkValidaty';
import { connect } from 'react-redux';

import Seo from '../../../../../SEO/SEO';

import './addBlog.scss';

const inputStyles = {
    width: '90%',
    minHeight: '40px',
    height: '25px',
    border: '1px solid #d1d6da',
    borderRadius: '4px',
    color: '#555555',
    backgroundColor: '#fff',
    lineHeight: 1.428571429,
    fontSize: '16px',
    overflow: 'auto',
    boxShadow: 'inset 0 1px 1px rgba(0,0,0,0.075)',
    resize: 'vertical',
    padding: '8px'
};

const invalidInputStyles = {
    width: '90%',
    minHeight: '40px',
    height: '25px',
    border: '2px solid salmon',
    borderRadius: '4px',
    color: '#555555',
    backgroundColor: '#fff',
    lineHeight: 1.428571429,
    fontSize: '16px',
    overflow: 'auto',
    boxShadow: 'inset 0 1px 1px rgba(0,0,0,0.075)',
    resize: 'vertical',
    padding: '8px'
}

class AddBlog extends React.Component {

    state = {
        loading: false,
        error: false,
        file: null,
        imageUrl: null,
        form: {
            content: {
                value: '',
                valid: false,
                touched: false,
                validation: {
                    required: true,
                    maxLength: 1000000
                }
            },
            topic: {
                value: '',
                valid: false,
                touched: false,
                validation: {
                    required: true,
                }
            },
            alt: {
                value: '',
                valid: false,
                touched: false,
                validation: {
                    required: true,
                }
            },
            route: {
                value: '',
                valid: false,
                touched: false,
                validation: {
                    required: true,
                }
            },
            rate: {
                value: 5,
            }
        },
        seo: {
            title: '',
            description: '',
            keywords: ''
        }
    }

    onChangeTextArea = (e, id) => {
        const updatedForm = {
            ...this.state.form
        };
        updatedForm[id].touched = true;
        updatedForm[id].value = e.target.value;
        updatedForm[id].valid = checkValidaty(e.target.value, updatedForm[id].validation);

        this.setState({
            form: updatedForm
        });
    }

    onChangeRate = (value) => {
            this.setState((prevState) => ({
            form: {
                ...prevState.form,
                rate: {
                    value: value
                }
            }
        }))
    }

    onSelectImageHandler = (event) => {
        const reader = new FileReader();
        reader.onloadend = () => {
            this.setState({
                file: file,
                imageUrl: reader.result
            })
        };

        const file = event.target.files[0];
        if (file) { reader.readAsDataURL(file); }
        else { this.setState({ file: null, imageUrl: null }) }
    }

    onButtonPressed = () => {
        this.setState({ loading: true });

        const formData = new FormData();
        formData.append('title', this.state.form.topic.value);
        formData.append('content', this.state.form.content.value);
        formData.append('alt', this.state.form.alt.value);
        formData.append('image', this.state.file);
        formData.append('rate', this.state.form.rate.value);
        formData.append('route', this.state.form.route.value);
        formData.append('seo_title', this.state.seo.title);
        formData.append('seo_description', this.state.seo.description);
        formData.append('seo_keywords', this.state.seo.keywords);

        axios.post(backendUrl + '/admin/post/add', formData, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res);
            this.props.history.push({
                pathname: '/editor/blog/all',
                state: {
                    successAdd: true
                }
            });
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({ error: true, loading: false });
        });
    }

    onExportData = (obj) => {
        this.setState({
            seo: {
                title: obj.title,
                description: obj.description,
                keywords: obj.keywords
            }
        });
    }

    onLoadIFrame = (obj) => {
        console.log(obj);
        obj.target.style.height = obj.target.contentWindow.document.documentElement.scrollHeight + 'px';
    }

    render() {

        return (
            <div style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                paddingBottom: '40px'
            }}>
                { this.state.loading && !this.state.error && <CircularProgress /> }
                { this.state.error && !this.state.loading && <ErrorComponent /> }
                { !this.state.error && !this.state.loading && 
                    <>
                        <h3>{'  :'}</h3>
                        <p>{'  :'}</p>
                        <textarea 
                        value={this.state.form.topic.value}
                        onChange={(e) => this.onChangeTextArea(e, 'topic')}
                        style={
                            !this.state.form.topic.valid && this.state.form.topic.touched ?  invalidInputStyles : 
                            inputStyles
                        } 
                        />
                        <p>{' html :'}</p>
                        <textarea 
                        value={this.state.form.content.value}
                        onChange={(e) => this.onChangeTextArea(e, 'content')}
                        style={{
                            ...inputStyles,
                            height: '600px'
                        }} 
                        />
                        <p>{'  0  5:'}</p>
                        <Slider
                            style={{ width: '500px' }}
                            value={this.state.form.rate.value}
                            onChange={(e, value) => this.onChangeRate(value)}
                            aria-labelledby="discrete-slider"
                            valueLabelDisplay="auto"
                            step={0.5}
                            marks
                            min={0}
                            max={5}
                        />
                        <p>{' alt  :'}</p>
                        <textarea 
                        value={this.state.form.alt.value}
                        onChange={(e) => this.onChangeTextArea(e, 'alt')}
                        style={
                            !this.state.form.alt.valid && this.state.form.alt.touched ?  invalidInputStyles : 
                            inputStyles
                        } 
                        />
                        <p>{' route  :'}</p>
                        <textarea 
                        value={this.state.form.route.value}
                        onChange={(e) => this.onChangeTextArea(e, 'route')}
                        style={
                            !this.state.form.route.valid && this.state.form.route.touched ?  invalidInputStyles : 
                            inputStyles
                        } 
                        />
                        <p>{' (png, jpg):'}</p>
                        <input
                         onChange={(event) => this.onSelectImageHandler(event)}
                         type="file"
                         accept="image/*" 
                        />
                        {
                            this.state.file  &&
                            this.state.form.topic.valid && this.state.form.content.valid &&
                            <>
                                <p>{'    (    ):'}</p>
                                <>
                                            <div
                                                className={'list'}
                                            >
                                                <div
                                                    className={'list_context'}
                                                >
                                                    <img
                                                        src={this.state.imageUrl}
                                                        alt={this.state.form.alt.value}
                                                    >
                                                    </img>
                                                    <div
                                                        className={'footer'}
                                                    >
                                                        <h1
                                                        >
                                                            {this.state.form.topic.value}
                                                        </h1>
                                                        <p
                                                        >
                                                            {'01.01.2021'}
                                                        </p>
                                                        <li
                                                        >
                                                            <span
                                                                style={{fontSize: '14px'}}
                                                            >
                                                                {' ...'}
                                                            </span>
                                                        </li>
                                                    </div>
                                                </div>
                                            </div>

                                            <div className={'blog-article_section'} >
                                                        <div
                                                            className={'blog_container'}
                                                        >
                                                            <div className={'imageTitle'}>
                                                                <img
                                                                    itemProp={'image'}
                                                                    itemType={'url'}
                                                                    itemScope
                                                                    src={this.state.imageUrl}
                                                                    alt={this.state.form.alt.value}
                                                                >
                                                                </img>
                                                            </div>
                                                            <div
                                                                className={'blog_container_filling'}
                                                            >
                                                                <div
                                                                    itemProp={'filling_header'}
                                                                    className={'blog_container_filling_header'}
                                                                >
                                                                    <h4 itemProp={'name'}>
                                                                        {this.state.form.topic.value}
                                                                    </h4>
                                                                    <p itemProp={'date'}>
                                                                        {'01.01.2021'}
                                                                    </p>
                                                                </div>
                                                                <div className='blog_container_filling_center'
                                                                >
                                                                    <iframe 
                                                                        onLoad={this.onLoadIFrame}
                                                                        style={{
                                                                            width: '100%',
                                                                            border: 'none',
                                                                            height: '100%',
                                                                            overflow: 'visible'
                                                                        }} 
                                                                        srcDoc={this.state.form.content.value}
                                                                    >
                                                                            {'    Frame'}
                                                                    </iframe>
                                                                </div>
                                                                <div className='blog_container_filling_bottom'
                                                                >
                                                                    <div style={{
                                                                        display: 'flex',
                                                                        flexDirection: 'row'
                                                                    }}>
                                                                        <ReactStars
                                                                        count={5}
                                                                        isHalf={true}
                                                                        value={this.state.form.rate.value}
                                                                        edit={false}
                                                                        size={24} 
                                                                        />
                                                                        <span style={{marginTop: '8px', marginLeft: '5px'}}>
                                                                            ({this.state.form.rate.value.toFixed(1)})
                                                                        </span>
                                                                    </div>
                                                                    <p itemProp={'text'}>
                                                                        {' '}{`(0)`}
                                                                    </p>
                                                                </div>
                                                            </div>
                                                        </div>
                                            </div>
                                </>
                            </>
                        }
                        <p>{'     SEO     !'}</p>
                        <Seo 
                            seo={this.state.seo}
                            onExportData={this.onExportData}
                        />
                        <Button
                        onClick={this.onButtonPressed}
                        disabled={
                            !(this.state.form.route.valid && this.state.form.topic.valid &&
                            this.state.form.content.valid && this.state.form.alt.valid && this.state.file)
                        }
                        color="primary"
                        variant="contained"
                        style={{marginTop: '20px'}}>
                            {''}
                        </Button>
                    </>
                }
            </div>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(AddBlog);