import React from 'react';
import axios from 'axios';
import { connect } from 'react-redux';
import { backendUrl } from '../../../../config/config';
import { withStyles } from '@material-ui/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

import Seo from '../../../SEO/SEO';

const style = theme => ({
    element: {
        margin: '20px 0'
    },
    button: {
        marginTop: '25px'
    },
    cancelButton: {
        marginTop: '10px'
    }
});

class EditFilter extends React.Component {

    state = {
        error: false,
        loading: false,
        form: {
            nameRU: {
                value: '',
                valid: true,
                touched: false
              },
              nameUA: {
                value: '',
                valid: true,
                touched: false
              },
              route: {
                value: '',
                valid: true,
                touched: false
              },
              alt: {
                  value: '',
                  valid: true,
                  touched: false
              }
        },
        seo: {
            title: '',
            description: '',
            keywords: ''
        },
        formIsValid: true,
        file: null,
        imagePreviewUrl: null,
        filterId: null
    }

    componentDidMount() {
        this.setState({ loading: true });

        const filterId = this.props.match.params.id;
        this.setState({ filterId: filterId });
        axios.get(backendUrl + `/admin/product/filter/id/${filterId}`, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res.data);
            this.setState({
                loading: false,
                error: false,
                form: {
                    ...this.state.form,
                    nameRU: {
                        value: res.data.name_ru,
                        valid: true,
                        touched: false
                    },
                    nameUA: {
                        value: res.data.name_ua,
                        valid: true,
                        touched: false
                    },
                    route: {
                        value: res.data.route,
                        valid: true,
                        touched: false
                      },
                    alt: {
                        value: res.data.alt,
                        valid: true,
                        touched: false
                    }
                },
                seo: {
                    title: res.data.seo_title,
                    description: res.data.seo_description,
                    keywords: res.data.seo_keywords
                }
            });
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({
                loading: false,
                error: true
            });
        });
    }

    onSelectIconHandler = (event) => {
        const reader = new FileReader();
        reader.onloadend = () => {
            this.setState({
                file: file,
                imagePreviewUrl: reader.result
            })
        };

        const file = event.target.files[0];
        if (file) { reader.readAsDataURL(file); }
        else { this.setState({ file: null, imagePreviewUrl: null }) }
    }

    onExportData = (obj) => {
        this.setState({
            seo: {
                title: obj.title,
                description: obj.description,
                keywords: obj.keywords
            }
        });
    }

    onInputChangedHandler = (event, name) => {
        const updatedForm = {
            ...this.state.form
        };
        updatedForm[name].value = event.target.value;
        updatedForm[name].touched = true;
        updatedForm[name].valid = event.target.value !== '';

        let formIsValid = true;
        for (let elem in updatedForm) {
            if (!updatedForm[elem].valid && updatedForm[elem].valid !== undefined) {
              formIsValid = false;
            }
          };
        this.setState({ form: updatedForm, formIsValid: formIsValid });
    }

    onSubmitForm = () => {
        this.setState({ loading: true });

        if (this.state.file) {
            let formData = new FormData();
            formData.append('name_ru', this.state.form.nameRU.value);
            formData.append('name_ua', this.state.form.nameUA.value);
            formData.append('route', this.state.form.route.value);
            formData.append('image', this.state.file);
            formData.append('alt', this.state.form.alt.value);
            formData.append('seo_title', this.state.seo.title);
            formData.append('seo_description', this.state.seo.description);
            formData.append('seo_keywords', this.state.seo.keywords);

            axios.put(backendUrl + `/admin/product/filter/${this.state.filterId}`, formData, {
                headers: {
                    'Authorization': `Bearer ${this.props.token}`
                }
            })
            .then(res => {
                this.props.history.push('/storage/filters');
            })
            .catch(err => {
                this.setState({ error: true, loading: false });
            });

        } else {

            axios.put(backendUrl + `/admin/product/filter/noImage/${this.state.filterId}`, {
                'name_ru': this.state.form.nameRU.value,
                'name_ua': this.state.form.nameUA.value,
                route: this.state.form.route.value,
                alt: this.state.form.alt.value,
                'seo_title': this.state.seo.title,
                'seo_description': this.state.seo.description,
                'seo_keywords': this.state.seo.keywords
            }, {
                headers: {
                    'Authorization': `Bearer ${this.props.token}`
                }
            })
            .then(res => {
                this.props.history.push('/storage/filters');
            })
            .catch(err => {
                this.setState({ error: true, loading: false });
            });

        }
    }

    render() {

        const { classes } = this.props;

        return (
            <div className="edit-category">
                <h1>{' '}</h1>
                { this.state.error && <p className="log-in_error-field" >{this.state.error}</p> }
                    <TextField 
                    onChange={(event) => this.onInputChangedHandler(event, 'nameRU')}
                    value={this.state.form.nameRU.value}
                    className={classes.element}
                    label="(RU)" 
                    variant="outlined"
                     />
                    <TextField 
                    onChange={(event) => this.onInputChangedHandler(event, 'nameUA')}
                    value={this.state.form.nameUA.value}
                    className={classes.element}
                    label="(UA)" 
                    variant="outlined"
                     />
                    <TextField 
                    onChange={(event) => this.onInputChangedHandler(event, 'route')}
                    value={this.state.form.route.value}
                    className={classes.element}
                    label="route" 
                    variant="outlined" 
                    />
                    <TextField 
                    onChange={(event) => this.onInputChangedHandler(event, 'alt')}
                    value={this.state.form.alt.value}
                    className={classes.element}
                    label="alt " 
                    variant="outlined" 
                    />
                    <label style={{margin: '10px 0'}}>
                             
                    </label>
                    <input 
                    onChange={(event) => this.onSelectIconHandler(event)}
                    type="file"
                    accept="image/*"
                    />
                    {this.state.imagePreviewUrl &&
                    <>
                        <Button
                        onClick={() => this.setState({ file: null, imagePreviewUrl: null })}
                        className={classes.cancelButton}
                        size="small"
                        variant="contained"
                        color="secondary">
                            {''}
                        </Button>
                        <img 
                        style={{ marginTop: '20px', maxWidth: '100%' }}
                        alt="icon"
                        src={this.state.imagePreviewUrl}
                        />
                        <p>    :</p>
                        <img 
                        style={{ 
                            marginTop: '10px',  
                            maxWidth: '50px',
                            maxHeight: '45px' 
                        }}
                        alt="icon"
                        src={this.state.imagePreviewUrl}
                        />
                    </>}
                    <p>{'     SEO     !'}</p>
                    <Seo 
                        seo={this.state.seo}
                        onExportData={this.onExportData}
                    />
                    {this.state.loading ?
                    <CircularProgress />
                    :
                    <Button
                    onClick={this.onSubmitForm}
                    disabled={!this.state.formIsValid}
                    variant="contained" 
                    color="primary"
                    className={classes.button}
                    >
                        {''}
                    </Button>}
            </div>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(withStyles(style)(EditFilter));