import React from 'react';
import axios from 'axios';
import { backendUrl } from '../../../../../../config/config';
import { connect } from 'react-redux';

import MainSlider from './MainSlider/index';
import AddGood from '../../../../../Orders/components/CreateOrder/AddGood/AddGood';
import ErrorComponent from '../../../../../Errors/Error/Error';
import { CircularProgress } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import pureImg from '../../../../../../assets/page/Main/pureImg.jpg';

import './SharesSlider.scss';

class SharesSlider extends React.Component {

    state = {
        loading: false,
        error: false,
        slider: [],

        shops: null,
        categories: null,

        edit: null,
        addMode: false,

        editedArea: {
            label: '',
            good: null
        }
    }

    componentDidMount() {
        this.setState({ loading: true });


        // get shops
        axios.get(backendUrl + '/admin/shop/', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            this.setState({
                shops: res.data,
                error: false
            })
        })
        .catch(err => {
            this.setState({ error: true, loading : false });
        });


        //get categories
        axios.get(backendUrl + '/admin/product/categories', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res);
            this.setState({
                categories: res.data.message,
                error: false
            });
        })
        .catch(err => {
            this.setState({ error: true, loading : false });
        })


        axios.get(backendUrl + '/admin/sliders/main/one/', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => { 
            console.log(res.data[0].items[0]);
            let slider = res.data[0].items.map((el, index) => ({
                ...el,
                id: index + 1
            }));
            while (slider.length < 4 ) {
                slider.push({
                    id: slider.length + 1,
                    label: ' ',
                    product: {
                        image: pureImg
                    }
                });
            }
            this.setState({
                slider: slider,
                error: false,
                loading: false
            });
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({
                error: true,
                loading: false
            })
        });
    }

    onAddGood = (product) => {
        this.setState({ 
            addMode: false,
            editedArea: {
                ...this.state.editedArea,
                good: product
            }
         });
    }

    editShare = () => {
        let newSlider = this.state.slider;
        newSlider = newSlider.map(el => {
            if (el.id === this.state.edit) {
                return {
                    ...el,
                    label: this.state.editedArea.label,
                    product: this.state.editedArea.good
                }
            } else {
                return el;
            }
        });
        this.setState({
            edit: null,
            slider: newSlider,
            editedArea: {
                label: '',
                good: null
            }
        });
    }

    onEditSlider = () => {
        this.setState({ loading: false });

        axios.put(backendUrl + '/admin/sliders/main/60104e609c0ea9597e8f908a', {
            items: this.state.slider.map(el => ({
                label: el.label,
                product: el.product._id
            }))
        }, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                window.location.reload();
            })
            .catch(err => {
                console.log(err, err.response);
                this.setState({error: true, loading: false});
            });
    }

    render() {

        console.log(this.state);

        const sliderIsValid = this.state.slider.filter(el => el.product ? el.product._id : false).length === 4 ;

        return (
            <div style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                padding: '30px 0'
            }}>
                {this.state.loading && !this.state.error && <CircularProgress /> }
                {!this.state.loading && this.state.error && <ErrorComponent /> }
                {!this.state.loading && !this.state.error && 
                this.state.slider && this.state.shops && this.state.categories && 
                <>
                    <AddGood 
                        shares
                        shops={this.state.shops}
                        categories={this.state.categories}
                        onAddGood={this.onAddGood}
                        show={this.state.addMode}
                        modalClosed={()=>this.setState({addMode: false})}
                    />
                    <h3>{'  '}</h3>
                    <h5>{' :'}</h5>
                    <div style={{
                        display: 'flex', 
                        width: '70%',
                        flexDirection: 'column',
                        justifyContent: 'center',
                        position: 'sticky',
                        }}>
                        <MainSlider list={this.state.slider} />
                    </div>
                    <h5>{' :'}</h5>
                    <ul className="action-slider-ul">
                        {this.state.slider.map(el => (
                            <li 
                            className="action-slider-ul_label-li" 
                            onClick={() => this.setState({ edit: el.id })}
                            >
                                <h5> {el.id} </h5>
                            </li>
                        ))}
                        <li 
                        className={
                            this.state.edit ? 
                            "action-slider-ul_label-edit label-opened" :
                            "action-slider-ul_label-edit label-closed"
                        }
                        >
                            <h3>{` ${this.state.edit} `}</h3>
                            <p>{'  :'}</p>
                            <textarea 
                                value={this.state.editedArea.label}
                                onChange={(e) => {
                                    this.setState({
                                        editedArea: {
                                            ...this.state.editedArea,
                                            label: e.target.value
                                        }
                                    })
                                }}
                            />
                            <button onClick={
                                () => this.setState((prevState) => ({
                                    addMode: !prevState.addMode
                                }))
                            }>
                                {'   '}
                            </button>
                            <p>{' :'}</p>
                            {
                               !this.state.editedArea.good ? 
                               <p style={{marginTop:'0px'}}>{'     ...'}</p> :
                               <ul style={{width: '600px'}}>
                                <li style={{
                                        width: '100%',
                                        borderBottom: '1px solid #aaa',
                                        display: 'flex',
                                        flexDirection: 'row',
                                        padding: '10px 0'
                                }}>
                                        <div className="ul-li-good">
                                            {''}
                                        </div>
                                        <div className="ul-li-price">
                                            {''}
                                        </div>
                                        <div className="ul-li-quantity">
                                            {'C'}
                                        </div>
                                </li>
                                    <li style={{
                                        width: '100%',
                                        borderBottom: '1px solid #aaa',
                                        display: 'flex',
                                        flexDirection: 'row',
                                        padding: '10px 0'
                                    }}>
                                        <div className="ul-li-good">
                                            <img 
                                            style={{maxWidth: '50px', maxHeight: '50px'}}
                                            src={backendUrl + '/' + this.state.editedArea.good.image}
                                            />
                                            <span style={{marginLeft:'15px'}}> 
                                                {this.state.editedArea.good.name_ru}
                                            </span>
                                        </div>
                                        <div className="ul-li-price">
                                            {this.state.editedArea.good.price}{' '}
                                        </div>
                                        <div className="ul-li-quantity">
                                            {this.state.editedArea.good.percent}%
                                        </div>
                                    </li>
                            </ul>
                            }
                            <button 
                            onClick={this.editShare}
                            disabled={
                                this.state.editedArea.label === '' || !this.state.editedArea.good
                            }
                            >
                                {' '}
                            </button>
                        </li>
                    </ul>
                </>}
                <Button
                    color="primary"
                    variant="contained"
                    onClick={this.onEditSlider}
                    disabled={!sliderIsValid}
                >
                    {' '}
                </Button>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(SharesSlider);