import React from 'react';
import axios from 'axios';
import { connect } from 'react-redux';
import { backendUrl } from '../../../config/config';

import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
import avatar from './default-avatar.png';
import './index.scss';

const allStatuses = {
    'check': {
        name: ' ',
        color: 'forestgreen'
    },
    'no-call': {
        name: '',
        color: '#dc143c'
    },
    'new': {
        name: '',
        color: 'forestgreen'
    },
    'send-to-delivery': {
        name: ' ',
        color: '#ffc84c'
    },
    'complete': {
        name: '',
        color: 'forestgreen'
    },
    'send-to-assembling': {
        name: '',
        color: '#fff44c'
    },
    'delivering': {
        name: '',
        color: '#ffc84c'
    },
    'client-confirmed': {
        name: '',
        color: '#fff44c'
    },
    'samovivoz': {
        name: '',
        color: 'forestgreen'
    },
    'cancel-other': {
        name: '',
        color: '#dc143c'
    },
    'prepayed': {
        name: '',
        color: '#fff44c'
    },
    'assembling': {
        name: ' ',
        color: '#ffc84c'
    }
}



class Check extends React.Component {
    state = {
        orders: null,
        user: null,
        orders: []
    }

    componentDidMount() {

        const userId = this.props.match.params.id;
        console.log(userId);
        this.setState({ loading: true, userId: userId });

        axios.get(backendUrl + `/admin/client/${userId}`, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                const users = res.data.user;
                let orders = res.data.orders;
                orders = orders.map((el, index) => {
                    let status = allStatuses[el.status];
                    return {
                        id: el.number,
                        name: users.name,
                        date: el.markDatetime.slice(0, 10),
                        time: el.markDatetime.slice(11, 16),
                        status: status,
                        price: el.totalSumm,
                        order: el.items.map((product, index) => ({
                            id: index,
                            name: product.offer.displayName,
                            price: product.initialPrice,
                            count: product.quantity
                        }))
                    }
                })
                this.setState({ user: users, orders: orders });
            })
            .catch(err => {
                console.log(err, err.response);
                this.setState({ error: true });
            })
            .finally(() => {
                this.setState({ loading: false });
            });
        if (this.props.location.search === '?successed-add=true') {
            this.setState({ modalForSuccessAdd: true });
        } else if (this.props.location.search === '?successed-changed=true') {
            this.setState({ modalForSuccessChange: true });
        }
    }

    onDeleteUser = () => {
        let id = this.props.match.params.id
        axios.delete(backendUrl + `/admin/client/${id}`, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                console.log(res);
                this.props.history.push('/clients/list')
            })
            .catch(err => {
                console.log(err, err.response);
            });
    }

    onEditUser = () => {
        let id = this.props.match.params.id
        this.props.history.push(`/clients/edit/${id}`);
    }

    render() {

        return <>
            {
                this.state.loading &&
                <CircularProgress />
            }
            {
                this.state.user &&
                <div >
                    <div id="header" style={{
                        marginLeft: "20px",
                        marginTop: "20px",
                        display: 'flex',
                        flexDirection: 'row'
                    }}>
                        <div style={{
                            textTransform: 'capitalize',
                            fontSize: '40px'
                        }}>  {this.state.user.name ? this.state.user.name : "ZXC"}</div>
                        <div
                            style={{
                                marginLeft: "60%"
                            }}
                        >
                            <Button color="primary"
                                variant="contained"
                                style={{
                                    textTransform: 'capitalize',
                                    fontSize: '20px'
                                }} onClick={()=>{this.onEditUser()} }>
                                
                                </Button>
                            <Button color="primary"
                                variant="contained"
                                style={{
                                    textTransform: 'capitalize',
                                    fontSize: '20px'
                                }} onClick={()=>{this.onDeleteUser()} }>
                                
                                    </Button>
                        </div>

                    </div>
                    <div style={{
                        display: 'flex',
                        flexDirection: 'row',
                        justifyContent: 'space-between',
                        alignItems: "flex-start"
                    }}>
                        <div className="orders_list_container">
                            <div style={{ padddingTop: "20px" }}>
                                <img
                                    style={{
                                        width: '46px',
                                        height: '46px',
                                        borderRadius: '100%'
                                    }}
                                    src={avatar}
                                    alt=""
                                />
                                <h2>{this.state.user.name}</h2>
                            </div>
                            <hr />
                            <div><h2>A:</h2>
                                    <p style={{
                                    fontWeight: 'bold',
                                }}> : {this.state.user.city || "Kharkiv"}</p>
                                <p style={{
                                    fontWeight: 'bold',
                                }}>{this.state.user.address}</p>
                            </div>
                            <hr />
                            <div  >
                                <p> : {new Date(this.state.user.date).toLocaleString()}</p>
                                <p> : {this.state.user.birthday || "-"}</p>
                            </div>
                            <div style={{
                                display: 'flex',
                                flexDirection: 'row',
                                justifyContent: 'space-between',
                                alignItems: "flex-start",
                                textAlign: "center"
                            }}>
                                <div>
                                    <p>{this.state.user.sumUserOrders || "-"}</p>
                                    <p> </p>
                                </div>
                                <div>
                                    <p>{this.state.user.sumUserBonus || "-"}</p>
                                    <p> </p>
                                </div>
                                <div>
                                    <p>{this.state.user.percentUserBonus || "-"}</p>
                                    <p> </p>
                                </div>
                                <div>
                                    <p>{this.state.user.AvgCheck || "-"}</p>
                                    <p> </p>
                                </div>
                            </div>
                            <div style={{
                                display: 'flex',
                                flexDirection: 'row',
                                justifyContent: 'space-between',
                                alignItems: "flex-start",
                                textAlign: "center"
                            }}>
                                <div>
                                    <p>{"-"}</p>
                                    <p> </p>
                                </div>
                                <div>
                                    <p>{ "-"}</p>
                                    <p>Instagram</p>
                                </div>
                                <div>
                                    <p>{ "-"}</p>
                                    <p></p>
                                </div>
                                <div>
                                    <p>{ "-"}</p>
                                    <p></p>
                                </div>
                            </div>
                        </div>
                        <div className={"mainese"} >
                            {this.state.orders.map((orders, index) =>
                                <div
                                    key={index}
                                    className={'order_history_footer_list'}
                                >
                                    <div className={'content'}>
                                        <div className={'content_header'}>
                                            <div className={'content_header_left'}>
                                                <h2></h2>
                                                <h2>{orders.id}</h2>
                                                <span
                                                    style={{
                                                        background: orders.status.color,
                                                        borderRadius: '2px',
                                                        color: orders.status.color === 'forestgreen' ? '#ededed' : 'black',
                                                        padding: '3px 18px',
                                                        fontWeight: 600
                                                    }}
                                                >
                                                    {orders.status.name}
                                                </span>
                                            </div>
                                            <div className={'content_header_right'}>
                                                <img
                                                    src={avatar}
                                                    alt=""
                                                />
                                                <p>{orders.name}</p>
                                                <span>
                                                    {
                                                        orders.date.slice(-2) +
                                                        '.' + orders.date.slice(5, 7) +
                                                        '.' + orders.date.slice(0, 4)
                                                    }
                                                </span>
                                                <span>{orders.time}</span>
                                            </div>
                                        </div>
                                        <div className={'content_body'}>
                                            {
                                                orders.order.map((el, id) => {
                                                    if (el.name !== " ") return (
                                                        <li
                                                            key={id}
                                                            className={'content_body_block'}
                                                        >
                                                            <div className={'left'}>
                                                                <p>{el.name}</p>
                                                            </div>
                                                            <div className={'right'}>
                                                                <span>{el.count}x{el.price} .</span>
                                                                <span>{el.count * el.price} .</span>
                                                            </div>
                                                        </li>
                                                    );
                                                })
                                            }
                                        </div>
                                        <div className={'content_footer'}>
                                            <h2></h2>
                                            <h2>{orders.price} .</h2>
                                        </div>
                                    </div>
                                </div>
                            )
                            }
                        </div>
                    </div>
                </div>
            }</>;
    }
}
const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(Check);