import React from 'react';
import { Switch, Route, NavLink, Redirect } from 'react-router-dom';

import UsersList from './components/UsersList/UsersList';
import NewAdmin from './components/NewAdmin/NewAdmin';
import UpdateAdmin from './components/UpdateAdmin/UpdateAdmin';
import { links } from './list';

import './superAdminProfile.scss';

class SuperAdminProfile extends React.Component {
    render() {
        const routes = (
            <Switch>
                <Route path="/super-admin-profile/users" component={UsersList} />
                <Route exact path="/super-admin-profile/add-admin" component={NewAdmin} />
                <Route exact path="/super-admin-profile/edit-admin/:id" component={UpdateAdmin} />
                <Redirect from="/super-admin-profile" to="/super-admin-profile/users" />
            </Switch>
        );

        return (
            <>
                <div className="all-admins_header">
                    {links.map((el, index) => (
                        <NavLink 
                        key={index} 
                        to={el.to} 
                        activeClassName="all-admins_header__item-active"
                        className='all-admins_header__item'>
                            {el.label}
                        </NavLink>
                    ))}
                </div>
                {routes}
            </>
        );
    }
}

export default SuperAdminProfile;