import React from 'react';
import axios from 'axios';
import { backendUrl } from '../../../../config/config';
import { connect } from 'react-redux';

import CircularProgress from '@material-ui/core/CircularProgress';
import { withStyles } from '@material-ui/styles'
import ErrorComponent from '../../../Errors/Error/Error';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const style = theme => ({
    table: {
        width: '600px'
      },
    container: {
        maxWidth: '500px',
        wordWrap: 'break-word'
    }
}) ;

class EmailUsers extends React.Component {

    state = {
        error: false,
        loading: true,
        users: []
    }

    componentDidMount() {
        axios.get(backendUrl + '/admin/mail/mail', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                this.setState({
                    error: false,
                    users: res.data
                })
            })
            .catch(err => {
                this.setState({ error: true });
            })
            .finally(() => this.setState({ loading: false }));
    }

    render() {

        const { classes } = this.props;
        console.log(this.state.users);

        return (
            <div style={{
                display: 'flex', 
                flexDirection: 'column', 
                alignItems: 'center'
            }}>
            {this.state.loading && !this.state.error && <CircularProgress />}
            {this.state.error && !this.state.loading && <ErrorComponent />}
            {!this.state.error && !this.state.loading &&
            (
                <>
                <h1>{'   E-mail '}</h1>
                {!this.state.users[0] ? 
                <p>{'        '}</p>
                :
                <TableContainer component={Paper} className={classes.table}>
                <Table className={classes.table} size="small" aria-label="a dense table">
                    <TableHead>
                    <TableRow>
                        <TableCell align="left">{'E-mail'}</TableCell>
                    </TableRow>
                    </TableHead>
                    <TableBody>
                    {this.state.users.map((row) => (
                        <TableRow key={row._id}>
                        <TableCell component="th" scope="row" align="left" className={classes.container}>
                            {row.email}
                        </TableCell>
                        </TableRow>
                    ))}
                    </TableBody>
                </Table>
                </TableContainer>
                }
                </>
            )
            }
            </div>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(withStyles(style)(EmailUsers));