import React from 'react';
import axios from 'axios';
import { backendUrl } from '../../../../../../config/config';
import { connect } from 'react-redux';
import { CircularProgress } from '@material-ui/core';
import ErrorComponent from '../../../../../Errors/Error/Error';
import CustomFoodBlock from '../CustomFoodBlock/CustomFoodBlock';

// for sliders

import './foodSliders.scss';

class FoodSilders extends React.Component {

    state = {
        loading: true,
        error: false,
        sliders: null
    }

    componentDidMount() {
        axios.get(backendUrl + '/admin/sliders/', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res);
            this.setState({ error: false, loading: false, sliders: res.data.sliders });
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({ error: true, loading: false });
        });
    }

    render() {

        console.log(this.props);

        return (
            <div style={{
                display: 'flex',
            flexDirection: 'column',
            alignItems: 'center', width: '100%'}}
            >
                {this.state.loading && <CircularProgress />}
                {this.state.error && !this.state.loading && <ErrorComponent />}
                { !this.state.error && !this.state.loading && this.state.sliders &&
                    <>
                        <h3> {' '} </h3>
                        {
                            this.state.sliders.map((el, index) => (
                                <div 
                                className="main-slider-container"
                                onClick={() => {
                                    this.props.history
                                    .push(`/pages/main/edit-food-slider/${el._id}`);
                                }}
                                key={el.index} 
                                >
                                    <p style={{
                                        fontSize: '30px', fontWeight: 700
                                    }}>
                                        {el.name} {':'}
                                    </p>
                                    <div className={'main-swiper'}>
                                        <div className={'main-swiper_header'}>
                                            <h1>
                                                {el.categoryId ? el.categoryId.name_ru : ''}
                                            </h1>
                                        </div>
                                            <div className={'wrapper'}>
                                                {
                                                    el.items.slice(0, 4).map(el =>
                                                        <CustomFoodBlock
                                                            key={el._id}
                                                            food={el}
                                                            style={false}
                                                        />
                                                    )
                                                }
                                            </div>
                                    </div>
                                </div>
                            ))
                        }
                    </>
                }
            </div>
        );
    }
};

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(FoodSilders);