import React from 'react';
import { Switch, Route, NavLink, Redirect } from 'react-router-dom';

import OrderList from './components/OrderList/orderList';
import UpdateOrder from './components/UpdateOrder/updateOrder';
import CreateOrder from './components/CreateOrder/CreateOrder';

import './order.scss';

class Orders extends React.Component {
    render() {
        const routes = (
            <Switch>
                <Route path="/orders/all" component={OrderList} />
                <Route exact path="/orders/edit/:id" component={UpdateOrder} />
                <Route  path="/orders/create" component={CreateOrder} />
                <Redirect from="/orders" to="/orders/all" />
            </Switch>
        );

        return (
            <>
                <div className="all-admins_header">
                        <NavLink 
                        to='/orders/all'
                        activeClassName="all-admins_header__item-active"
                        className='all-admins_header__item'>
                            {''}
                        </NavLink>
                </div>
                {routes}
            </>
        );
    }
}

export default Orders;