import React from 'react';
import axios from 'axios';
import { backendUrl } from '../../../../../config/config';
import { connect } from 'react-redux';

import './addGood.scss';
import { CircularProgress } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import ErrorComponent from '../../../../Errors/Error/Error';

class AddGood extends React.Component {

    state = {
        loading: false,
        error: false,

        shop: this.props.shops[0].name,
        category: this.props.categories[0].name_ru,

        findedGoods: [],
        searchName: ''
    }

    onChangeOption = (e, id) => {
        this.setState({
            [id]: e.target.value
        })
    }

    onFindGoods = () => {
        this.setState({ loading: true });

        const currentShop = this.props.shops.find(el => el.name === this.state.shop);
        const shopId = currentShop._id;

        const currentCategory = this.props.categories.find(el => el.name_ru === this.state.category);
        const categoryId = currentCategory._id; 

        axios.get(backendUrl + `/admin/product/${categoryId}/${shopId}`, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                console.log(res);
                this.setState({
                    error: false,
                    findedGoods: res.data.product.map(el => ({
                        ...el,
                        quantity: 1
                    }))
                })
            })
            .catch(err => {
                console.log(err, err.response);
                this.setState({ error: true });
            })
            .finally(() => {
                this.setState({loading: false})
            });
    }

    onAddQuantity = (id) => {
        let newFindedGoods = [ ...this.state.findedGoods ];
        newFindedGoods = newFindedGoods.map(el => {
            if (el._id === id) {
                return {
                ...el,
                quantity: el.quantity + 1
                }
            } else {
                return el;
            }
        });
        this.setState({
            findedGoods: newFindedGoods
        });
    }

    onMinusQuantity = (id) => {
        let newFindedGoods = [ ...this.state.findedGoods ];
        newFindedGoods = newFindedGoods.map(el => {
            if (el._id === id) {
                return {
                ...el,
                quantity: el.quantity === 1 ? 1 : el.quantity - 1
                }
            } else {
                return el;
            }
        });
        this.setState({
            findedGoods: newFindedGoods
        });
    }

    onSearch = () => {
        let newFindedGoods = [ ...this.state.findedGoods ];
        newFindedGoods = newFindedGoods.filter(el => {
           return el.name_ru.indexOf(this.state.searchName) !== -1;
        });

        this.setState({
            findedGoods: newFindedGoods
        });
    }

    onCancelSearch = () => {
        this.onFindGoods();
        this.setState({
            searchName: ''
        })
    }

    componentDidMount() {
    }

    render() {

        return (
            <>
            {this.props.show && 
            <>
                <div 
                    className="modal-block-backdrop" 
                    onClick={this.props.modalClosed}
                />
                <div className="add-good-in-order">
                    <div className="add-good-in-order__left">
                        <p>{' :'}</p>
                        <select 
                        value={this.state.shop}
                        onChange={(e)=>this.onChangeOption(e, 'shop')}
                        >
                            {this.props.shops.map(el => (
                                <option value={el.name}>
                                    {el.name}
                                </option>
                            ))}
                        </select>
                        <p>{' :'}</p>
                        <select 
                        value={this.state.category}
                        onChange={(e)=>this.onChangeOption(e, 'category')}
                        >
                            {this.props.categories.map(el => (
                                <option value={el.name_ru}>
                                    {el.name_ru}
                                </option>
                            ))}
                        </select>
                        <button
                            onClick={this.onFindGoods}
                        >
                            {' '}
                        </button>
                    </div>
                    <div className="add-good-in-order__right">
                        <div className="add-good-in-order__right_header">
                            <h3>{' '}</h3>
                            <div>
                                <input
                                value={this.state.searchName}
                                onChange={(e)=>this.onChangeOption(e, 'searchName')}
                                placeholder="   " 
                                />
                                <button
                                onClick={this.onSearch}
                                >
                                    {''}
                                </button>
                                <button 
                                onClick={this.onCancelSearch}
                                style={{backgroundColor:'brown'}}
                                >
                                    {''}
                                </button>
                            </div>
                        </div>
                        { this.state.loading && <CircularProgress /> }
                        { this.state.error && <ErrorComponent /> }
                        { !this.state.findedGoods[0] && !this.state.loading && !this.state.error && 
                        <p>{'   '}</p> 
                        }
                        { this.state.findedGoods[0] && !this.state.loading && !this.state.error &&
                            <ul>
                                <li>
                                        <div className="ul-li-good">
                                            {''}
                                        </div>
                                        <div className="ul-li-price">
                                            {''}
                                        </div>
                                        {!this.props.shares ?
                                        <div className="ul-li-quantity">
                                            {''}
                                        </div> :
                                        <div className="ul-li-quantity">
                                            {''}
                                        </div>
                                        }
                                        <div className="ul-li-add">
                                            {''}
                                        </div>
                                </li>
                                {this.state.findedGoods.map(el => (
                                    <li>
                                        <div className="ul-li-good">
                                            <img 
                                            style={{maxWidth: '50px', maxHeight: '50px'}}
                                            src={backendUrl + '/' + el.image}
                                            />
                                            <span style={{marginLeft:'15px'}}> 
                                                {el.name_ru}
                                            </span>
                                        </div>
                                        <div className="ul-li-price">
                                            {el.price}{' '}
                                        </div>
                                        {!this.props.shares ? 
                                        <div className="ul-li-quantity">
                                            <IconButton 
                                            onClick={() => this.onMinusQuantity(el._id)}
                                            aria-label="delete"
                                            >
                                                <RemoveIcon fontSize="small" />
                                            </IconButton>
                                            {el.quantity}{' .'}
                                            <IconButton 
                                            onClick={() => this.onAddQuantity(el._id)}
                                            aria-label="delete"
                                            >
                                                <AddIcon fontSize="small" />
                                            </IconButton>
                                        </div> :
                                        <div className="ul-li-quantity">
                                            {el.percent}%
                                        </div>
                                        }
                                        <div className="ul-li-add">
                                            <Button
                                            onClick={()=>this.props.onAddGood(el)}
                                            color="primary"
                                            variant="contained"
                                            style={{
                                                textTransform: 'capitalize'
                                            }}
                                            >
                                                {''}
                                            </Button>
                                        </div>
                                    </li>
                                ))}
                            </ul>
                        }
                    </div>
                </div>
            </>
            }
            </>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(AddGood);