import React from 'react';
import axios from 'axios';
import { connect } from 'react-redux';
import { backendUrl } from '../../../../../../config/config';
import { checkValidaty } from '../../../../../../tools/checkValidaty';
import Slider from '@material-ui/core/Slider';
import Button from '@material-ui/core/Button';

import ReactStars from 'react-rating-stars-component';
import Spinner from "@material-ui/core/CircularProgress";
import ErrorIndicator from "../../../../../Errors/Error/Error";
import dayjs from 'dayjs';

const inputStyles = {
    width: '90%',
    minHeight: '40px',
    height: '25px',
    border: '1px solid #d1d6da',
    borderRadius: '4px',
    color: '#555555',
    backgroundColor: '#fff',
    lineHeight: 1.428571429,
    fontSize: '16px',
    overflow: 'auto',
    boxShadow: 'inset 0 1px 1px rgba(0,0,0,0.075)',
    resize: 'vertical',
    padding: '8px'
};

const invalidInputStyles = {
    width: '90%',
    minHeight: '40px',
    height: '25px',
    border: '2px solid salmon',
    borderRadius: '4px',
    color: '#555555',
    backgroundColor: '#fff',
    lineHeight: 1.428571429,
    fontSize: '16px',
    overflow: 'auto',
    boxShadow: 'inset 0 1px 1px rgba(0,0,0,0.075)',
    resize: 'vertical',
    padding: '8px'
}

class BlogArticle extends React.Component {

    state = {
        loading: false,
        error: false,
        file: null,
        imageUrl: null,
        postId: null,
        form: {
            content: {
                value: '',
                valid: false,
                touched: false,
                validation: {
                    required: true,
                    maxLength: 1000000
                }
            },
            topic: {
                value: '',
                valid: false,
                touched: false,
                validation: {
                    required: true,
                    maxLength: 150
                }
            },
            alt: {
                value: '',
                valid: false,
                touched: false,
                validation: {
                    required: true,
                }
            },
            route: {
                value: '',
                valid: false,
                touched: false,
                validation: {
                    required: true,
                }
            },
            rate: {
                value: 5,
            },
            image: '',
            date: '',
            comments: []
        },
        seo: {
            title: '',
            description: '',
            keywords: ''
        }
    }

    componentDidMount() {
       this.setState({
           loading: true,
           error: false
       });

        const postId = this.props.match.params.id;
        this.setState({postId : postId});

        axios.get(backendUrl + `/posts/${postId}`)
            .then(res => {
                console.log(res);
                this.setState({
                    loading: false,
                    error: false,
                    form: {
                        content: {
                            value: res.data.content,
                            valid: true,
                            touched: false,
                            validation: {
                                required: true,
                                maxLength: 1000000
                            }
                        },
                        topic: {
                            value: res.data.title,
                            valid: true,
                            touched: false,
                            validation: {
                                required: true,
                                maxLength: 150
                            }
                        },
                        alt: {
                            value: res.data.alt,
                            valid: true,
                            touched: false,
                            validation: {
                                required: true,
                                maxLength: 50
                            }
                        },
                        route: {
                            value: res.data.route,
                            valid: true,
                            touched: false,
                            validation: {
                                required: true,
                            }
                        },
                        rate: {
                            value: res.data.rate,
                        },
                        image: res.data.image,
                        date: res.data.date,
                        comments: res.data.comments
                    },
                    seo: {
                        title: res.data.seo_title,
                        description: res.data.seo_description,
                        keywords: res.data.seo_keywords
                    }
                });
            })
            .catch(err => {
                console.log(err, err.repsponse);
                this.setState({
                    loading: false,
                    error: true
                });
            })
    }

    onChangeTextArea = (e, id) => {
        const updatedForm = {
            ...this.state.form
        };
        updatedForm[id].touched = true;
        updatedForm[id].value = e.target.value;
        updatedForm[id].valid = checkValidaty(e.target.value, updatedForm[id].validation);

        this.setState({
            form: updatedForm
        });
    }

    onChangeRate = (value) => {
            this.setState((prevState) => ({
            form: {
                ...prevState.form,
                rate: {
                    value: value
                }
            }
        }))
    }

    onSelectImageHandler = (event) => {
        const reader = new FileReader();
        reader.onloadend = () => {
            this.setState({
                file: file,
                imageUrl: reader.result
            })
        };

        const file = event.target.files[0];
        if (file) { reader.readAsDataURL(file); }
        else { this.setState({ file: null, imageUrl: null }) }
    }

    onExportData = (obj) => {
        this.setState({
            seo: {
                title: obj.title,
                description: obj.description,
                keywords: obj.keywords
            }
        });
    }

    onLoadIFrame = (obj) => {
        console.log(obj);
        obj.target.style.height = obj.target.contentWindow.document.documentElement.scrollHeight + 'px';
    }

    onButtonPressed = () => {
        this.setState({ loading: true, error: false });

        if (this.state.file) {

            const formData = new FormData();
            formData.append('title', this.state.form.topic.value);
            formData.append('content', this.state.form.content.value);
            formData.append('alt', this.state.form.alt.value);
            formData.append('image', this.state.file);
            formData.append('rate', this.state.form.rate.value);
            formData.append('route', this.state.form.route.value);
            formData.append('seo_title', this.state.seo.title);
            formData.append('seo_description', this.state.seo.description);
            formData.append('seo_keywords', this.state.seo.keywords);
            axios.put(backendUrl + `/admin/post/image/${this.state.postId}`, formData, {
                headers: {
                    'Authorization': `Bearer ${this.props.token}`
                }
            })
            .then(res => {
                console.log(res);
                this.props.history.push({
                    pathname: '/editor/blog/all'
                });
            })
            .catch(err => {
                console.log(err, err.response);
                this.setState({ error: true, loading: false });
            });

        } else {

            axios.put(backendUrl + `/admin/post/${this.state.postId}`, {
                title: this.state.form.topic.value,
                content: this.state.form.content.value,
                alt: this.state.form.alt.value,
                rate: this.state.form.rate.value,
                route: this.state.form.route.value,
                'seo_title': this.state.seo.title,
                'seo_description': this.state.seo.description,
                'seo_keywords': this.state.seo.keywords
            }, {
                headers: {
                    'Authorization': `Bearer ${this.props.token}`
                }
            })
            .then(res => {
                console.log(res);
                this.props.history.push({
                    pathname: '/editor/blog/all'
                });
            })
            .catch(err => {
                console.log(err, err.response);
                this.setState({ error: true, loading: false });
            });

        }
    }

    render() {

        const {
            loading,
            error,
            form
        } = this.state;

        return (
            <div
                itemProp={'blogArticle'}
                itemScope
                className={'blog-article_section'}
            >
                {loading && <Spinner /> }
                {error && <ErrorIndicator />}
                {!loading && !error &&
                    <>  
                        <div className={'blog_container'}>
                            <div className={'imageTitle'}>
                                <img
                                    src={this.state.file ? this.state.imageUrl : backendUrl + `/${form.image}`}
                                    alt={form.alt.value}
                                />
                            </div>
                            <div className={'blog_container_filling'}>
                                <div className={'blog_container_filling_header'}>
                                    <h4>
                                        {form.topic.value}
                                    </h4>
                                    <p>
                                        {dayjs(form.date).format('DD.MM.YYYY')}
                                    </p>
                                </div>
                                <div className={'blog_container_filling_center'}>
                                    <iframe 
                                        onLoad={this.onLoadIFrame}
                                        style={{
                                            width: '100%',
                                            border: 'none',
                                            height: '100%'
                                        }} 
                                        srcDoc={form.content.value}
                                    >
                                        {'    Frame'}
                                    </iframe>
                                </div>
                                <div className={'blog_container_filling_bottom'}>
                                    <div style={{
                                        display: 'flex',
                                        flexDirection: 'row'
                                    }}>
                                        <ReactStars
                                        count={5}
                                        value={form.rate.value}
                                        edit={false}
                                        size={24} 
                                        isHalf={true}
                                        />
                                        <span style={{marginTop: '8px', marginLeft: '5px'}}>
                                            ({form.rate.value.toFixed(1)})
                                        </span>
                                    </div>
                                    <p>
                                        {' '}{`(${form.comments.length})`}
                                    </p>
                                </div>
                            </div>
                        </div>
                        <div style={{
                            display: 'flex',
                            alignItems: 'center',
                            flexDirection: 'column',
                            width: '100%'
                        }}>
                            <h3>{' :'}</h3>
                            <p>{' :'}</p>
                            <textarea 
                            value={this.state.form.topic.value}
                            onChange={(e) => this.onChangeTextArea(e, 'topic')}
                            style={
                                !this.state.form.topic.valid && this.state.form.topic.touched ?  invalidInputStyles : 
                                inputStyles
                            } 
                            />
                            <p>{'HTML :'}</p>
                            <textarea 
                            value={this.state.form.content.value}
                            onChange={(e) => this.onChangeTextArea(e, 'content')}
                            style={{
                                ...inputStyles,
                                height: '600px'
                            }} 
                            />
                            <p>{'  0  5:'}</p>
                            <Slider
                                style={{ width: '500px' }}
                                value={this.state.form.rate.value}
                                onChange={(e, value) => this.onChangeRate(value)}
                                aria-labelledby="discrete-slider"
                                valueLabelDisplay="auto"
                                step={0.5}
                                marks
                                min={0}
                                max={5}
                            />
                            <p>{' alt  :'}</p>
                            <textarea 
                            value={this.state.form.alt.value}
                            onChange={(e) => this.onChangeTextArea(e, 'alt')}
                            style={
                                !this.state.form.alt.valid && this.state.form.alt.touched ?  invalidInputStyles : 
                                inputStyles
                            } 
                            />
                            <p>{' route  :'}</p>
                            <textarea 
                            value={this.state.form.route.value}
                            onChange={(e) => this.onChangeTextArea(e, 'route')}
                            style={
                                !this.state.form.route.valid && this.state.form.route.touched ?  invalidInputStyles : 
                                inputStyles
                            } 
                            />
                            <p>{'   ,  (png, jpg):'}</p>
                            <input
                            onChange={(event) => this.onSelectImageHandler(event)}
                            type="file"
                            accept="image/*" 
                            />
                            {this.state.file && 
                            <button
                                onClick={() => this.setState({file: null, imageUrl: null})}
                                style={{
                                    marginTop: '10px'
                                }}
                            >
                                {''}
                            </button>
                            }
                            <Button
                                onClick={this.onButtonPressed}
                                disabled={
                                    !(this.state.form.route.valid && this.state.form.topic.valid &&
                                    this.state.form.content.valid  )
                                }
                                color="primary"
                                variant="contained"
                                style={{marginTop: '20px'}}
                            >
                                {''}
                            </Button>
                        </div>
                    </>
                }
            </div>
        );
    }


}

const mapStateToProps = (state) => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(BlogArticle);