import React from 'react';
import axios from 'axios';
import { backendUrl } from '../../../../../../config/config';
import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
import ErrorComponent from '../../../../../Errors/Error/Error';
import { checkValidaty } from '../../../../../../tools/checkValidaty';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';

import './addShare.scss';

const inputStyles = {
    width: '1123px',
    minHeight: '25px',
    height: '25px',
    border: '1px solid #d1d6da',
    borderRadius: '4px',
    color: '#555555',
    backgroundColor: '#fff',
    lineHeight: 1.428571429,
    fontSize: '16px',
    overflow: 'auto',
    boxShadow: 'inset 0 1px 1px rgba(0,0,0,0.075)',
    resize: 'vertical',
    padding: '8px'
};

const invalidInputStyles = {
    width: '1123px',
    minHeight: '25px',
    height: '25px',
    border: '2px solid salmon',
    borderRadius: '4px',
    color: '#555555',
    backgroundColor: '#fff',
    lineHeight: 1.428571429,
    fontSize: '16px',
    overflow: 'auto',
    boxShadow: 'inset 0 1px 1px rgba(0,0,0,0.075)',
    resize: 'vertical',
    padding: '8px'
}

class AddShare extends React.Component {

    state = {
        form: {
            shareHeader: {
                value: '',
                valid: false,
                touched: false,
                validation: {
                    required: true,
                    maxLength: 100
                }
            },
            alt: {
                value: '',
                valid: false,
                touched: false,
                validation: {
                    required: true,
                }
            },
            shareBody: {
                value: '',
                valid: false,
                touched: false,
                validation: {
                    required: true,
                    maxLength: 400
                }
            }
        },
        file: null,
        imageUrl: null,
        loading: false,
        error: false
    }

    onChangeInputHandler = (event, id) => {
        const updatedForm = {
            ...this.state.form
        };
        updatedForm[id].value = event.target.value;
        updatedForm[id].touched = true;
        updatedForm[id].valid = checkValidaty(event.target.value, updatedForm[id].validation);
        if (event.key === 'Enter') {
            console.log('do validate');
          }

        this.setState({ form: updatedForm });
    }

    onSelectImageHandler = (event) => {
        const reader = new FileReader();
        reader.onloadend = () => {
            this.setState({
                file: file,
                imageUrl: reader.result
            })
        };

        const file = event.target.files[0];
        if (file) { reader.readAsDataURL(file); }
        else { this.setState({ file: null, imageUrl: null }) }
    }

    onCreateButtonPressed = () => {
        this.setState({ loading: true });

        let formData = new FormData();
        formData.append('title', this.state.form.shareHeader.value);
        formData.append('description', this.state.form.shareBody.value);
        formData.append('alt', this.state.form.alt.value);
        formData.append('image', this.state.file);

        axios.post(backendUrl + '/admin/action/add', formData, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res);
            this.props.history.push({
                pathname: '/editor/shares/all',
                state: { successAdd: true  }
            });
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({ error: true, loading: false });
        });
    }

    render() {

        return (
            <>
            { this.state.error && <ErrorComponent/> }
            {
            this.state.loading && !this.state.error ? 
            <CircularProgress />
            :
            !this.state.loading && !this.state.error ?
            <div
            style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center'
            }}>  
                <h1>{'C  '}</h1>
                <p>{'  (  100 ):'}</p>
                <textarea
                value={this.state.form.shareHeader.value}
                onChange={(e) => this.onChangeInputHandler(e, 'shareHeader') }
                style={
                    !this.state.form.shareHeader.valid && this.state.form.shareHeader.touched ?  invalidInputStyles : 
                    inputStyles
                } 
                />
                <p>{'  (  400 ):'}</p>
                <textarea
                value={this.state.form.shareBody.value}
                onChange={(e) => this.onChangeInputHandler(e, 'shareBody') }
                style={
                    !this.state.form.shareHeader.valid && this.state.form.shareHeader.touched ?  invalidInputStyles : 
                    inputStyles
                } 
                />
                <p>{'alt   :'}</p>
                <textarea
                value={this.state.form.alt.value}
                onChange={(e) => this.onChangeInputHandler(e, 'alt') }
                style={
                    !this.state.form.shareHeader.valid && this.state.form.shareHeader.touched ?  invalidInputStyles : 
                    inputStyles
                } 
                />
                <p>{' :'}</p>
                <input 
                    onChange={(event) => this.onSelectImageHandler(event)}
                    type="file"
                    accept="image/*"
                />
                {this.state.file && this.state.form.shareBody.valid &&  this.state.form.shareHeader.valid && this.state.form.alt.valid && 
                <div>
                    <h3>{'     :'}</h3>
                    <div className="masonry-layout__panel">
                            <div className="masonry-layout__panel__header">
                                <img src={this.state.imageUrl} alt={this.state.form.alt.value}/>
                            </div>
                            <div className="masonry-layout__panel__content">
                                <h4>{this.state.form.shareHeader.value}</h4>
                                <p>{this.state.form.shareBody.value}</p>
                            </div>
                    </div>
                </div>}
                <Button
                style={{
                    marginTop: '5px',
                    marginBottom: '25px'
                }}
                color="primary"
                variant="contained"
                disabled={
                    !this.state.file || !this.state.form.shareBody.valid || !this.state.form.shareHeader.valid 
                }
                onClick={this.onCreateButtonPressed}>
                    {''}
                </Button>
            </div> : null
            }
            </>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default withRouter(connect(mapStateToProps)(AddShare));