import React from 'react';
import axios from 'axios';
import { NavLink } from 'react-router-dom';
import { connect } from 'react-redux';
import { backendUrl } from '../../../../config/config';
import CircularProgress from '@material-ui/core/CircularProgress';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import DoneIcon from '@material-ui/icons/Done';
import CloseIcon from '@material-ui/icons/Close';
import Table from '@material-ui/core/Table';
import Paper from '@material-ui/core/Paper';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TablePagination from '@material-ui/core/TablePagination';
import ErrorComponent from '../../../Errors/Error/Error';
import Modal from '../../../UI/Modal/Modal';
import Status from '../UpdateOrder/Status/Status';
import * as FileSaver from 'file-saver';
import * as XLSX from 'xlsx';

import {
    useJsonToCsv
} from 'react-json-csv';
import { sortStrings } from '../../../../tools/sort';

import './list.scss';

const formats = [
    {
        value: 'excel',
        label: 'excel',
    },
    {
        value: 'csv',
        label: 'csv',
    },
    {
        value: 'json',
        label: 'json',
    }
]

const styles = theme => ({
    button: {
        backgroundColor: '#4caf50',
        marginLeft: '20px',
        color: 'white',
        '&:hover': {
            backgroundColor: '#357a38'
        }
    },
    table: {
        width: '100%'
    },
    edit: {
        color: '#4caf50',
        cursor: 'pointer',
        '&:hover': {
            color: '#357a38'
        }
    },
    delete: {
        color: '#b71c1c',
        cursor: 'pointer',
        '&:hover': {
            color: '#801313'
        }
    },
    paginator: {
        width: '100%'
    },
    paper: {
        width: '98%',
        marginTop: '20px',
        marginBottom: '60px',
    },
    visuallyHidden: {
        border: 0,
        clip: 'rect(0 0 0 0)',
        height: 1,
        margin: -1,
        overflow: 'hidden',
        padding: 0,
        position: 'absolute',
        top: 20,
        width: 1,
    },
    cancelButton: {
        textTransform: 'capitalize',
        marginLeft: '15px',
        backgroundColor: '#b71c1c',
        '&:hover': {
            backgroundColor: '#801313'
        }
    },

    tableCellStyle: {
        fontSize: '11px',
    }

});

const headCells = [
    { id: 'shop', label: 'M', width: '4%' },
    { id: 'date', label: '  ', width: '8%' },
    { id: 'items', label: '', width: '24%' },
    { id: 'status', label: '', width: '15%' },
    { id: 'sum', label: '', width: '6%' },
    { id: 'address', label: '', width: '16%' },
    { id: 'delivery', label: ' ', width: '8%' },
    { id: 'payType', label: ' ', width: '15%' },
    { id: 'payed', label: '', width: '4%' }
];

class OrdersList extends React.Component {

    state = {
        loading: true,
        error: false,
        orders: null,
        rowsPerPage: 5,
        page: 0,
        modalupload: false,
        format: 'csv',
        orderBy: 'date',
        order: 'asc',
        modalForSuccessAdd: false,
        modalForSuccessChange: false,
        searchArea: {
            shop: '',
            fio: ''
        },
        searchMode: false
    }

    componentDidMount() {
        axios.get(backendUrl + '/admin/order/get/100 ', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                const orders = res.data.orders;
                this.setState({ orders: orders });
            })
            .catch(err => {
                console.log(err, err.response);
                this.setState({ error: true });
            })
            .finally(() => {
                this.setState({ loading: false });
            });

        if (this.props.location.search === '?successed-add=true') {
            this.setState({ modalForSuccessAdd: true });
        } else if (this.props.location.search === '?successed-changed=true') {
            this.setState({ modalForSuccessChange: true });
        }
    }


    handleChangeRowsPerPage = (event) => {
        this.setState({
            rowsPerPage: parseInt(event.target.value, 10),
            page: 0
        })
    };

    handleChangePage = (event, newPage) => {
        this.setState({
            page: newPage
        })
    };

    handleRequestSort = (event, property) => {
        const isAsc = this.state.orderBy === property && this.state.order === 'asc'
        this.setState({
            order: isAsc ? 'desc' : 'asc',
            orderBy: property,
        });
        const orders = this.state.orders;
        this.setState({
            orders: sortStrings(property, orders, this.state.order !== 'desc' ? 'asc' : 'desc')
        })
    };

    onModalForSuccessAddClosed = () => {
        this.setState({ modalForSuccessAdd: false });
        this.props.history.push('/orders/add');
    }

    onModalForSuccessChange = () => {
        this.setState({ modalForSuccessChange: false });
        this.props.history.push('/orders/add');
    }

    onDeleteUser = (id) => {

        axios.delete(backendUrl + `/admin/order/${id}`, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                console.log(res);
                window.location.reload();
            })
            .catch(err => {
                console.log(err, err.response);
            });
    }

    onEditUser = (id) => {
        this.props.history.push(`/order/edit/${id}`);
    }

    onDownload = () => {
        axios.get(backendUrl + '/admin/order/file/download/plz', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                console.log(res.data);
                let response
                if (this.state.format == 'json') {
                    response = new Blob([JSON.stringify(res.data)], { type: 'application/json' });
                    console.log(response);
                    let url = window.URL.createObjectURL(response);
                    let a = document.createElement('a');
                    a.href = url;
                    a.download = 'orders.json';
                    a.click();
                }
                else if (this.state.format == 'excel') {
                    const fileType = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
                        const fileExtension = 'orders.xlsx';
                        const ws = XLSX.utils.json_to_sheet(res.data);
                        const wb = { Sheets: { 'data': ws }, SheetNames: ['data'] };
                        const excelBuffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
                        const data = new Blob([excelBuffer], {type: fileType});
                        FileSaver.saveAs(data, fileExtension);

                }

                else if (this.state.format == 'csv') {
                    res.data.map((el) => { return { ...el, address: el.address.text, userName:el.userId.name, userPhone:el.userId.phoneNumber } })
                    const filename = 'orders',
                        fields = {
                            "_id": "Index",
                            "userName": "Name",
                            "userPhone": "Phone",
                            "status": "status",
                            "image": "Image",
                            "sum": "Price",
                            "date": "date",
                            "address": "Address"

                        },
                        data = res.data
                    const { saveAsCsv } = useJsonToCsv();
                    saveAsCsv({ data, fields, filename })
                }

            })
            .catch(err => {
                console.log(err, err.response);
                this.setState({ error: true });
            })
            .finally(() => {
                this.setState({ loading: false });
            })
    }


    onSearch = () => {
        this.setState({ loading: true });

        axios.post(backendUrl + '/admin/order/search', {
            name: this.state.searchArea.fio,
            shop: this.state.searchArea.shop
        }, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                console.log(res);
                this.setState({
                    searchMode: true,
                    orders: res.data
                });
            })
            .catch(err => {
                console.log(err, err.response);
                this.setState({ error: true });
            })
            .finally(() => {
                this.setState({ loading: false });
            })
    }

    onSelectOrder = (id) => {
        this.props.history.push(`/orders/edit/${id}`);
    }
    uploadModalTrue = () => {
        this.setState({ modalupload: true })
    }

    render() {
        console.log(this.state);
        const { classes } = this.props;

        const modalForSuccessAddContent = (
            <>
                <p style={{ textAlign: 'center' }}>{'    '}</p>
            </>
        );
        const modalForUpload = (
            <>
                <div>
                    <TextField
                        id="standard-select-currency"
                        select
                        label="Select"
                        value={this.state.format}
                        onChange={(e) => {
                            this.setState({
                                format: e.target.value
                            })
                        }}
                        helperText="Please select your format"
                    >
                        {formats.map((option) => (
                            <option key={option.label} value={option.value}>
                                {option.label}
                            </option>
                        ))}
                    </TextField>
                    <Button
                        onClick={() => { this.onDownload() }}
                        variant="contained"
                    >
                        
                </Button>
                </div>
            </>
        )

        const modalForSuccessChanged = (
            <>
                <p style={{ textAlign: 'center' }}>{'   '}</p>
            </>
        )

        return (
            <>
                <div className="orders-list__content-area">
                    {this.state.loading && <CircularProgress />}

                    {this.state.error && !this.state.loading && <ErrorComponent />}

                    {!this.state.error && !this.state.loading &&
                        (
                            <>
                                <Modal
                                    show={this.state.modalupload}
                                    modalClosed={() => {
                                        this.setState({ modalupload: false });
                                        this.props.history.push('/orders/all');
                                    }}>
                                    {modalForUpload}
                                </Modal>
                                <Modal
                                    show={this.state.modalForSuccessAdd}
                                    modalClosed={this.onModalForSuccessAddClosed}>
                                    {modalForSuccessAddContent}
                                </Modal>
                                <Modal
                                    show={this.state.modalForSuccessChange}
                                    modalClosed={this.onModalForSuccessChange}>
                                    {modalForSuccessChanged}
                                </Modal>
                                <div className="orders-list__content-area__controls">
                                    <div className="orders-list__content-area__controls__top">
                                        <h1></h1>
                                        <NavLink to="/orders/create">
                                            <Button
                                                onClick={() => this.props.history.push('/orders/create')}
                                                variant="contained"
                                                color="primary"
                                                startIcon={<AddIcon />}>
                                                {'  '}
                                            </Button>
                                        </NavLink>
                                    </div>
                                    <div className="orders-list__content-area__controls__search">
                                        <div className="orders-list__content-area__controls__search__inputs">
                                            <span className="orders-list__content-area__controls__search__inputs__item">
                                                <TextField
                                                    onChange={(e) => this.setState((prevState) => ({
                                                        searchArea: {
                                                            ...prevState.searchArea,
                                                            shop: e.target.value
                                                        }
                                                    }))}
                                                    className={classes.input}
                                                    label="Shop"
                                                    variant="outlined"
                                                />
                                            </span>
                                            <span className="orders-list__content-area__controls__search__inputs__item">
                                                <TextField
                                                    onChange={(e) => this.setState((prevState) => ({
                                                        searchArea: {
                                                            ...prevState.searchArea,
                                                            fio: e.target.value
                                                        }
                                                    }))}
                                                    className={classes.input}
                                                    label=""
                                                    variant="outlined"
                                                />
                                            </span>
                                            <Button
                                                onClick={this.onSearch}
                                                variant="contained"
                                                className={classes.button}>
                                                {''}
                                            </Button>
                                            {this.state.searchMode &&
                                                <Button
                                                    onClick={() => window.location.reload()}
                                                    className={classes.cancelButton}
                                                    variant="contained"
                                                    color="secondary">
                                                    {''}
                                                </Button>}
                                        </div>
                                        <div>
                                            <Button
                                                color="primary"
                                                variant="contained"
                                                style={{
                                                    textTransform: 'capitalize',
                                                    fontSize: '12px'
                                                }}
                                            >
                                                {'  '}
                                            </Button>
                                        </div>
                                    </div>
                                </div>
                                <Paper elevation={2} className={classes.paper}>
                                    <Table className={classes.table} size="medium" aria-label="a dense table">
                                        <TableHead>
                                            <TableRow>
                                                {headCells.map(headCell => (
                                                    <TableCell
                                                        className={classes.tableCellStyle}
                                                        align="center"
                                                        key={headCell.id}
                                                        style={{ width: headCell.width }}
                                                    >
                                                        {headCell.label}
                                                    </TableCell>
                                                ))}
                                            </TableRow>
                                        </TableHead>
                                        <TableBody>
                                            {this.state.orders
                                                .slice(
                                                    this.state.page * this.state.rowsPerPage,
                                                    this.state.page * this.state.rowsPerPage
                                                    + this.state.rowsPerPage).map((ordy) => (
                                                        <TableRow
                                                            key={ordy._id}
                                                            style={{ cursor: 'pointer' }}
                                                            onClick={() => this.onSelectOrder(ordy._id)}
                                                        >
                                                            <TableCell component="th" scope="row" align="center">
                                                                {ordy.shop.name}
                                                            </TableCell>
                                                            <TableCell align="center">
                                                                {new Date(ordy.date).toLocaleDateString() + " " + new Date(ordy.date).toLocaleTimeString()}
                                                            </TableCell>
                                                            <TableCell align="center">
                                                                {ordy.items.map((el) =>
                                                                    <li style={{ lineHeight: 2 }}>
                                                                        {
                                                                        (el.product ? el.product.name_ru : "noname") + "  ---  "
                                                                        }
                                                                        {
                                                                        (el.product ? el.product.price : '0') + ', '
                                                                        }
                                                                        {
                                                                        (el.product ? el.quantity : '0') + ' .' || "0 ."
                                                                        }
                                                                    </li>
                                                                )
                                                                }
                                                            </TableCell>
                                                            <TableCell align="center">
                                                                <Status
                                                                    status={ordy.status}
                                                                />
                                                            </TableCell>
                                                            <TableCell align="center">
                                                                {ordy.sum + ' '}
                                                            </TableCell>
                                                            <TableCell align="center">
                                                                <div style={{
                                                                    whiteSpace: 'pre-line',
                                                                    fontSize: '13px'
                                                                }}
                                                                >
                                                                    {ordy.address.text}
                                                                </div>
                                                            </TableCell>
                                                            <TableCell align="center">
                                                                {ordy.delivery.status}
                                                            </TableCell>
                                                            <TableCell align="center">
                                                                {ordy.oplata.type === 'cash' ?
                                                                    '' : ' '}
                                                            </TableCell>
                                                            <TableCell align="center">
                                                                {ordy.oplata.status === 'not-paid' ?
                                                                    <CloseIcon
                                                                        style={{
                                                                            color: 'brown'
                                                                        }}
                                                                    /> :
                                                                    <DoneIcon
                                                                        style={{
                                                                            color: 'forestgreen'
                                                                        }}
                                                                    />
                                                                }
                                                            </TableCell>
                                                        </TableRow>
                                                    ))}
                                        </TableBody>
                                    </Table>
                                    <TablePagination
                                        className={classes.paginator}
                                        rowsPerPageOptions={[5, 10, 25]}
                                        component="div"
                                        count={this.state.orders.length}
                                        rowsPerPage={this.state.rowsPerPage}
                                        page={this.state.page}
                                        onChangePage={this.handleChangePage}
                                        onChangeRowsPerPage={this.handleChangeRowsPerPage}
                                        labelRowsPerPage={'   :'}
                                    />
                                    <div style={{ float: 'left', alignSelf: 'flex-start', marginTop: "-3.5%" }}>
                                        <Button
                                            onClick={this.uploadModalTrue} variant="contained"
                                        >
                                             
                                </Button>
                                        {/* <a href=""> </a> */}
                                    </div>
                                </Paper>
                            </>
                        )}
                </div>
            </>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(withStyles(styles)(OrdersList));