import axios from 'axios';
import * as actionTypes from './actionTypes';
import { backendUrl } from '../../config/config';

export const authStart = () => {
    return {
        type: actionTypes.AUTH_START
    }
};

export const authFail = (error) => {
    return {
        type: actionTypes.AUTH_FAILED,
        error: error
    }
};

export const logInSuccess = ( token, status ) => {

    localStorage.setItem( 'token-admin-panel-24rolls', token );

    return {
        type: actionTypes.LOG_IN_SUCCESS,
        token: token,
        status: status
    }
}

export const logout = () => {
    localStorage.removeItem('token-admin-panel-24rolls');
    return {
        type: actionTypes.AUTH_LOGOUT
    }
}

export const logIn = ( data ) => {
    return dispatch => {
        dispatch(authStart());

        axios.post(backendUrl + '/admin/login', {
            email: data.email,
            password: data.password
        })
            .then(res => {
                console.log(res, res.data);
                const token = res.data.token;
                const status = res.data.status;
                dispatch(logInSuccess(token, status));
            })
            .catch(err => {
                console.log(err, err.response);
                const message = err.response ? err.response.data.message || err.response.message : err.toString();
                dispatch(authFail(message));
            });
    }
};

export const resetToken = (token) => {
    return {
        type: actionTypes.RESET_TOKEN,
        token: token
    }
};

export const setAdminStatus = (status) => {
    return {
        type: actionTypes.SET_ADMIN_STATUS,
        status: status
    }
}