import React from 'react';
import axios from 'axios';
import { connect } from 'react-redux';
import { backendUrl } from '../../../../config/config';
import { withStyles } from '@material-ui/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import DeleteIcon from '@material-ui/icons/Delete';

import Seo from '../../../SEO/SEO';

const inputStyles = {
    width: '90%',
    minHeight: '40px',
    height: '25px',
    border: '1px solid #d1d6da',
    borderRadius: '4px',
    color: '#555555',
    backgroundColor: '#fff',
    lineHeight: 1.428571429,
    fontSize: '16px',
    overflow: 'auto',
    boxShadow: 'inset 0 1px 1px rgba(0,0,0,0.075)',
    resize: 'vertical',
    padding: '8px'
};

const style = theme => ({
    element: {
        margin: '20px 0'
    },
    button: {
        marginTop: '25px'
    },
    cancelButton: {
        marginTop: '10px'
    }
});

class EditGoods extends React.Component {

    state = {
        form: {
            name: {
                value: '',
                valid: true,
              },
              category: {
                  value: '',
                  valid: true
              },
              filter: {
                  value: '',
                  valid: true
              },
              weight: {
                  value: '',
                  number: true,
                  valid: true
              },
              price: {
                  value: '',
                  number: true,
                  valid: true
              },
              percent: {
                  value: 0,
                  number: true,
                  valid: true
              },
              description: {
                  value: '',
                  valid: true
              },
              alt: {
                  value: '',
                  valid: true
              },
              route: {
                  value: '',
                  valid: true
              },
              article: {
                value: '',
                valid: true,
                touched: false,
             },
        },
        seo: {
            title: '',
            description: '',
            keywords: ''
        },
        seo_dp:{
            title: '',
            description: '',
            keywords: ''
        },
        seo_zp:{
            title: '',
            description: '',
            keywords: ''
        },
        formIsValid: true,
        error: null,
        loading: true,
        categories: null,
        filters: [''],
        file: null,
        imagePreviewUrl: null,
        image: '',
        userId: null,
        currentCities: [],
        cities: [''],
        activeCity: ''
    }

    componentDidMount() {

        const userId = this.props.match.params.id;
        this.setState({ userId: userId });

        axios.get(backendUrl + '/admin/shop/', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
             }
        })
        .then(res => {
            this.setState({
                cities: this.state.cities.concat(res.data),
                activeCity: res.data[0].name
            });
        })
        .catch(err => {
            this.setState({
                error: true,
                loading: false
            })
        });

        axios.get(backendUrl + '/admin/product/categories', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
             }
        })
        .then(res => {
            console.log(res, res.data);
            this.setState({
                categories: res.data.message
             });

             axios.get(backendUrl + `/admin/product/id/${userId}`, {
                headers: {
                    'Authorization': `Bearer ${this.props.token}`
                 }
            })
            .then(res => {
                console.log(res);
                const updatedCities = this.state.cities.filter(el => {
                    let valid = true;
                    res.data.shop.forEach(shop => {
                        if (el._id === shop._id) {
                            valid = false;
                        }
                    });
                    return valid;
                })
                this.setState({
                    image: res.data.image,
                    form: {
                        ...this.state.form,
                        category: {
                            ...this.state.form.category,
                            value: res.data.categoryId.name_ru
                        },
                        filter: {
                            ...this.state.form.filter,
                            value: res.data.filter ? res.data.filter.name_ru : ''
                        },
                        name: {
                            ...this.state.form.name,
                            value: res.data.name_ru
                        },
                        weight: {
                            ...this.state.form.weight,
                            value: res.data.weight
                        },
                        price: {
                            ...this.state.form.price,
                            value: res.data.price
                        },
                        percent: {
                            ...this.state.form.percent,
                            value: res.data.percent
                        },
                        description: {
                            ...this.state.form.description,
                            value: res.data.description_ru
                        },
                        alt: {
                            ...this.state.form.alt,
                            value: res.data.alt
                        },
                        route: {
                            ...this.state.form.route,
                            value: res.data.route
                        },
                        article: {
                            value: res.data.article,
                            valid: true,
                            touched: false,
                        },
                    },
                    seo: {
                        title: res.data.seo_title,
                        description: res.data.seo_description,
                        keywords: res.data.seo_keywords
                    },
                    seo_dp:{
                        title: res.data.seo_title_dp,
                        description: res.data.seo_description_dp,
                        keywords: res.data.seo_keywords_dp
                    },
                    seo_zp:{
                        title: res.data.seo_title_zp,
                        description: res.data.seo_description_zp,
                        keywords: res.data.seo_keywords_zp
                    },
                    currentCities: res.data.shop,
                    cities: updatedCities
                });

                axios.get(backendUrl + '/admin/product/filters', {
                    headers: {
                        'Authorization': `Bearer ${this.props.token}`
                     }
                })
                .then(res => {
                    console.log(res, res.data);
                    const updatedFilters = this.state.filters.concat(res.data.fils);
                    this.setState({
                        filters: updatedFilters
                     });
                })
                .catch(err => {
                    console.log(err, err.data);
                    this.setState({ error: true });
                });

            })
            .catch(err => {
                console.log(err, err.response);
                const message = err.response ? err.response.data.message || err.response.data.error : err.toString();
                this.setState({ error: message });
            })

        })
        .catch(err => {
            console.log(err, err.data);
            const message = err.response ? err.response.data.message || err.response.data.error : err.toString();
            this.setState({ error: message });
        })
        .finally(()=>this.setState({loading: false}));
    }

    onInputChangedHandler = (event, name) => {
        const updatedForm = {
            ...this.state.form
        };
        updatedForm[name].value = event.target.value;
        updatedForm[name].touched = true;
        updatedForm[name].valid = event.target.value !== '';
        if (updatedForm[name].number) {
            console.log(event.target.value);
            updatedForm[name].valid = Number(event.target.value) >= 0 && event.target.value ;
        }

        let formIsValid = true;
        for (let elem in updatedForm) {
            if (!updatedForm[elem].valid && updatedForm[elem].valid !== undefined) {
              formIsValid = false;
            }
          };
        this.setState({ form: updatedForm, formIsValid: formIsValid });
    }

    onSubmitForm = () => {
        this.setState({ loading: true });

        const category = this.state.categories.find((el) => {
            if (el.name_ru === this.state.form.category.value ) {
                return true;
            }
            return false;
        });
        const categoryId = category._id;

        const filter = this.state.filters.find((el) => {
            if ( el.name_ru === this.state.form.filter.value ) {
                return true;
            }
            return false;
        });
        const filterId = filter ? filter._id : '';


        const shops = this.state.currentCities.map(el => el._id);

        if (!this.state.file) {
            axios.put(backendUrl + `/admin/product/noImage/${this.state.userId}`, {
                name_ru: this.state.form.name.value,
                categoryId: categoryId,
                filters: filterId,
                description_ru: this.state.form.description.value,
                price: this.state.form.price.value,
                percent: this.state.form.percent.value,
                weight: this.state.form.weight.value,
                alt: this.state.form.alt.value,
                route: this.state.form.route.value,
                shop: JSON.stringify(shops),
                article: this.state.form.article.value,
                'seo_title': this.state.seo.title,
                'seo_description': this.state.seo.description,
                'seo_keywords': this.state.seo.keywords,
                'seo_title_dp': this.state.seo_dp.title,
                'seo_description_dp': this.state.seo_dp.description,
                'seo_keywords_dp': this.state.seo_dp.keywords,
                'seo_title_zp': this.state.seo_zp.title,
                'seo_description_zp': this.state.seo_zp.description,
                'seo_keywords_zp': this.state.seo_zp.keywords
            }, {
                headers: {
                    'Authorization': `Bearer ${this.props.token}`
                 }
            })
            .then(res => {
                console.log(res.data);
                this.setState({ error: null });
                this.props.history.push('/storage/goods?success-changed=true');
            })
            .catch(err => {
                console.log(err, err.response);
                const message = err.response ? err.response.data.message || err.response.data.error : err.toString();
                this.setState({ error: message });
            })
            .finally(() => {
                this.setState({ loading: false });
            });
        } else {

            let formData = new FormData();
            formData.append('name_ru', this.state.form.name.value);
            formData.append('description_ru', this.state.form.description.value);
            formData.append('price', this.state.form.price.value);
            formData.append('percent', this.state.form.percent.value);
            formData.append('weight', this.state.form.weight.value);
            formData.append('alt', this.state.form.alt.value);
            formData.append('route', this.state.form.route.value);
            formData.append('shop', JSON.stringify(shops));
            formData.append('categoryId', categoryId);
            formData.append('filters', filterId);
            formData.append('image', this.state.file);
            formData.append('article', this.state.form.article.value);
            formData.append('seo_title', this.state.seo.title);
            formData.append('seo_description', this.state.seo.description);
            formData.append('seo_keywords', this.state.seo.keywords);
            formData.append('seo_title_zp', this.state.seo_zp.title);
            formData.append('seo_description_zp', this.state.seo_zp.description);
            formData.append('seo_keywords_zp', this.state.seo_zp.keywords);
            formData.append('seo_title_dp', this.state.seo_dp.title);
            formData.append('seo_description_dp', this.state.seo_dp.description);
            formData.append('seo_keywords_dp', this.state.seo_dp.keywords);

            axios.put(backendUrl + `/admin/product/${this.state.userId}`, formData, {
                headers: {
                    'Authorization': `Bearer ${this.props.token}`
                 }
            })
            .then(res => {
                console.log(res.data);
                this.setState({ error: null });
                this.props.history.push('/storage/goods?success-changed=true');
            })
            .catch(err => {
                console.log(err, err.response);
                const message = err.response ? err.response.data.message || err.response.data.error : err.toString();
                this.setState({ error: message });
            })
            .finally(() => {
                this.setState({ loading: false });
            });
        }
    }

    onCategoryChangedHandler = (event) => {
        this.setState((prevState) => ({
            form: {
                ...prevState.form,
                category: {
                    ...this.state.form.category,
                    value: event.target.value,
                    valid: true
                }
            }
        }))
      }

      onDeleteCity = (id) => {
        const currentCities = [ ...this.state.currentCities ].filter(el => el._id !== id);
        const deletedShop = [ ...this.state.currentCities ].find(el => el._id === id );

        let updatedShops = [...this.state.cities];
        updatedShops.push(deletedShop);
        this.setState({
            cities: updatedShops,
            currentCities
        });
     }

     onAddShop = () => {
        const currentShops = [ ...this.state.currentCities ];
        const addedShop = this.state.cities.find(el => el.name === this.state.activeCity);
        currentShops.push(addedShop);

        const updatedCities = [ ...this.state.cities ].filter(el => el.name !== this.state.activeCity);

        this.setState({
            cities: updatedCities,
            currentCities: currentShops,
            activeCity: ''
        });
    }

    onFilterChangedHandler = (event) => {
        this.setState((prevState) => ({
            form: {
                ...prevState.form,
                filter: {
                    ...this.state.form.filter,
                    value: event.target.value,
                    valid: true
                }
            }
        }))
    }

    onSelectIconHandler = (event) => {
        const reader = new FileReader();
        reader.onloadend = () => {
            this.setState({
                file: file,
                imagePreviewUrl: reader.result
            })
        };

        const file = event.target.files[0];
        if (file) { reader.readAsDataURL(file); }
        else { this.setState({ file: null, imagePreviewUrl: null }) }
    }

    onExportData = (obj) => {
        this.setState({
            seo: {
                title: obj.title,
                description: obj.description,
                keywords: obj.keywords
            }
        });
    }
    onExportData_zp = (obj) => {
        this.setState({
            seo_zp: {
                title: obj.title,
                description: obj.description,
                keywords: obj.keywords
            }
        });
    }
    onExportData_dp = (obj) => {
        this.setState({
            seo_dp: {
                title: obj.title,
                description: obj.description,
                keywords: obj.keywords
            }
        });
    }

    render() {
        console.log(this.state);
        const { classes } = this.props;

        return (
            <>
            {!this.state.categories && !this.state.loading ?
                <p>    !</p>
                :
                this.state.loading ? 
                <div className="create-new-admin__content">
                    <CircularProgress />
                </div> 
                : 
            <>
            <div className="create-new-admin__content" style={{ padding: '20px 0'}}>
                <h1>
                    {' '}
                </h1>
                <p>{'  400, 600, 1000       !    0.   0!         '}</p>
                    { this.state.error && <p className="log-in_error-field" >{this.state.error}</p> }
                    <TextField 
                    onChange={(event) => this.onInputChangedHandler(event, 'name')}
                    value={this.state.form.name.value}
                    className={classes.element}
                    label="" 
                    variant="outlined"
                     />
                     <TextField
                        select
                        className={classes.element}
                        label=""
                        value={this.state.form.category.value}
                        onChange={this.onCategoryChangedHandler}
                        SelectProps={{
                            native: true,
                        }}
                        helperText=" "
                        variant="outlined"
                        >
                        {this.state.categories.map((option, index) => (
                            <option key={index} value={option.name_ru}>
                            {option.name_ru}
                            </option>
                        ))}
                    </TextField>
                    <div style={{display:'flex'}}>
                            <div style={{
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center'
                            }}>
                                <h5>{':'}</h5>
                                <ul style={{
                                    listStyleType: 'none',
                                    display: 'flex',
                                    flexDirection: 'column',
                                    justifyContent: 'center',
                                    backgroundColor: '#aaa',
                                    padding: '0 10px',
                                    borderRadius: '5px' 
                                }}>
                                            {this.state.currentCities.map((el) => (
                                                <li key={el._id}>
                                                    <div style={{
                                                        display: 'flex',
                                                        alignItems: 'center',
                                                        justifyContent: 'center'
                                                    }}>
                                                            <p>{el.name}</p>
                                                        <DeleteIcon 
                                                        onClick={() => this.onDeleteCity(el._id)}
                                                        style={{ 
                                                            color: 'brown', cursor: 'pointer'
                                                        }}/>
                                                    </div>
                                                </li>
                                            ))}
                                </ul>
                            </div>
                            <div style={{
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center',
                                marginLeft: '50px'
                            }}>
                                <h5>{' '}</h5>
                                <TextField
                                    select
                                    value={this.state.activeCity}
                                    onChange={(e) => {
                                        this.setState({
                                            activeCity: e.target.value
                                        })
                                    }}
                                    SelectProps={{
                                        native: true,
                                    }}
                                    helperText=" "
                                    variant="outlined"
                                    >
                                    {this.state.cities.map((option, index) => (
                                        <option key={index} value={option.name}>
                                            {option.name}
                                        </option>
                                    ))}
                                </TextField>
                                <Button 
                                onClick={this.onAddShop}
                                color="primary"
                                variant="outlined"
                                disabled={this.state.activeCity === ''}
                                style={{margin: '20px 0'}}>
                                    {''}
                                </Button>
                            </div>
                    </div>
                    <TextField
                        select
                        label=""
                        value={this.state.form.filter.value}
                        onChange={this.onFilterChangedHandler}
                        SelectProps={{
                            native: true,
                        }}
                        helperText=" "
                        variant="outlined"
                        >
                        {this.state.filters.map((option, index) => (
                            <option key={index} value={option.name_ru}>
                            {option.name_ru}
                            </option>
                        ))}
                    </TextField>
                     <TextField 
                    onChange={(event) => this.onInputChangedHandler(event, 'weight')}
                    value={this.state.form.weight.value}
                    className={classes.element}
                    label="()" 
                    type="number"
                    variant="outlined"
                     />
                     <TextField 
                    onChange={(event) => this.onInputChangedHandler(event, 'price')}
                    value={this.state.form.price.value}
                    className={classes.element}
                    label="()" 
                    type="number"
                    variant="outlined"
                     />
                     <TextField 
                    onChange={(event) => this.onInputChangedHandler(event, 'percent')}
                    value={this.state.form.percent.value}
                    className={classes.element}
                    label=" " 
                    type="number"
                    variant="outlined"
                     />
                     <TextField 
                    onChange={(event) => this.onInputChangedHandler(event, 'alt')}
                    value={this.state.form.alt.value}
                    className={classes.element}
                    label="alt " 
                    variant="outlined"
                     />
                     <p>{' html :'}</p>
                        <textarea 
                        value={this.state.form.article.value}
                        onChange={(e) => this.onInputChangedHandler(e, 'article')}
                        style={{
                            ...inputStyles,
                            height: '600px'
                        }} 
                        />
                     <label style={{margin: '10px 0'}}>
                             
                    </label>
                    <input 
                    onChange={(event) => this.onSelectIconHandler(event)}
                    type="file"
                    accept="image/*"
                    />
                    {this.state.imagePreviewUrl &&
                    <>
                        <Button
                        onClick={() => this.setState({ file: null, imagePreviewUrl: null })}
                        className={classes.cancelButton}
                        size="small"
                        variant="contained"
                        color="secondary">
                            {''}
                        </Button>
                        <img 
                        style={{ marginTop: '20px', maxWidth: '100%' }}
                        alt="icon"
                        src={this.state.imagePreviewUrl}
                        />
                        <p>    :</p>
                        <div className="food-block-container"> 
                            <div 
                            className="list_header"
                            >
                                <img 
                                style={{ marginTop: '10px' }}
                                alt="pic"
                                src={this.state.imagePreviewUrl}
                                />
                            </div>
                        </div>
                    </>}
                     <p>
                         route (    ,    ):
                     </p>
                     <TextField 
                        onChange={(event) => this.onInputChangedHandler(event, 'route')}
                        value={this.state.form.route.value}
                        className={classes.element}
                        label="route " 
                        variant="outlined"
                     />
                    <p style={{margin:'0'}}>{''}</p>
                    <textarea 
                    value={this.state.form.description.value}
                    onChange={(e)=>this.onInputChangedHandler(e, 'description')}
                    style={{ 
                        width: '300px',
                        minHeight: '70px',
                        resize: 'vertical',
                        borderRadius: '5px'
                         }} />
                    <p>{'     SEO        !'}</p>
                    <h1></h1>
                    {this.state.seo &&
                    <Seo 
                        seo={this.state.seo}
                        onExportData={this.onExportData}
                    />}
                    <h1></h1>
                    {this.state.seo_dp &&
                    <Seo 
                        seo={this.state.seo_dp}
                        onExportData={this.onExportData_dp}
                    />}
                    <h1></h1>
                    {this.state.seo_zp &&
                    <Seo 
                        seo={this.state.seo_zp}
                        onExportData={this.onExportData_zp}
                    />}
                    <Button
                    onClick={this.onSubmitForm}
                    disabled={!this.state.formIsValid || !this.state.currentCities[0]}
                    variant="contained" 
                    color="primary"
                    className={classes.button}
                    >
                        {''}
                    </Button>
            </div>
            </>
            }
            </>
        )
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(withStyles(style)(EditGoods));