import React from 'react';
import axios from 'axios';
import { backendUrl } from '../../../config/config';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { checkValidaty } from '../../../tools/checkValidaty';
import CircularProgress from '@material-ui/core/CircularProgress';

const styles = theme => ({
    paper: {
      marginTop: theme.spacing(8),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    avatar: {
      margin: theme.spacing(1),
      backgroundColor: theme.palette.secondary.main,
    },
    form: {
      width: '100%',
      marginTop: theme.spacing(1),
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
    },
    link: {
      color: '#3f51b5',
      '&:hover': {
        textDecoration: 'underline'
      }
    },
    circular: {
      marginLeft: '175px'
    }
  });

class RestorePassword extends React.Component {

    state = {
        controls: {
            password: {
                value: '',
                validation: {
                  required: true,
                  minLength: 6
                },
                valid: false,
                touched: false
              },
              confirmedPassword: {
                value: '',
                validation: {
                  required: true,
                  minLength: 6
                },
                valid: false,
                touched: false
              }
          },
          formIsValid: false,
          modalOpened: false,
          token: null,
          tokenIsValid: false,
          loading: false,
          error: null
    }

    componentDidMount() {
      const token = this.props.match.params.token;
      this.setState({ token: token });
      axios.get(backendUrl + `/admin/reset/toUser/${token}`)
        .then(res => {
          console.log(res, res.data);
          this.setState({ tokenIsValid: true, error: null });
        })
        .catch(err => {
          console.log(err, err.response);
          const message = err.response ? err.response.data.error || err.response.data.message : err.toString();
          this.setState({ tokenIsValid: false, error: message });
        });
    }

    onInputChangedHandler = (event, controlName) => {
        const updatedControls = {
          ...this.state.controls,
          [controlName]: {
            ...this.state.controls[controlName],
            value: event.target.value,
            touched: true,
            valid: checkValidaty(event.target.value, this.state.controls[controlName].validation)
          }
        };
        if ( controlName === 'confirmedPassword' ) {
            const valid = updatedControls.confirmedPassword.value === updatedControls.password.value;
            updatedControls.confirmedPassword.valid = updatedControls.confirmedPassword.valid && valid;
        }
        let formIsValid = true;
        for (let elem in updatedControls) {
          if (!updatedControls[elem].valid) {
            formIsValid = false;
          }
        }
        this.setState({ controls: updatedControls, formIsValid: formIsValid });
      }

      onSubmitHandler = (event) => {
        event.preventDefault();

        this.setState({ loading: true });
        if (!this.state.tokenIsValid) {
          this.setState({ error: '-   !' });
        } else {
            axios.post(backendUrl + '/admin/save/reseted', {
              password: this.state.controls.password.value,
              resetLink: this.state.token
            })
              .then(res => {
                console.log(res, res.data);
                this.setState({ loading: false });
                this.props.history.push('/log-in/?password_reseted_success?true');
              })
              .catch(err => {
                console.log(err, err.response);
                const message = err.response ? err.response.data.message|| err.message : err.toString();
                this.setState({ error: message, loading: false });
              })
        }
      }

    render() {

      console.log(this.state.error);

        const { classes } = this.props;

        return (
            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <div className={classes.paper}>

                {this.state.error && 
                <p className="log-in_error-field" >
                    {this.state.error}
                </p>}

                <form className={classes.form} noValidate>
                    <TextField
                    onChange={(event) => this.onInputChangedHandler(event, 'password')}
                    value={this.state.controls.password.value}
                    error={!this.state.controls.password.valid && this.state.controls.password.touched}
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    label=" "
                    type="password"
                    autoComplete="current-password"
                    />
                    <TextField
                    onChange={(event) => this.onInputChangedHandler(event, 'confirmedPassword')}
                    value={this.state.controls.confirmedPassword.value}
                    error={!this.state.controls.confirmedPassword.valid && this.state.controls.confirmedPassword.touched}
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    label=" "
                    type="password"
                    autoComplete="current-password"
                    />
                    {this.state.loading ?
                    <CircularProgress className={classes.circular} /> :
                    <Button
                    disabled={!this.state.formIsValid}
                    onClick={(e) => this.onSubmitHandler(e)}
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                    >
                      {' '}
                    </Button>}
                </form>
                </div>
            </Container>
        );
    }
}

export default withStyles(styles)(RestorePassword);