import React from 'react';
import axios from 'axios';
import { backendUrl } from '../../../../../../config/config';
import { connect } from 'react-redux';
import { CircularProgress } from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button';
import ErrorComponent from '../../../../../Errors/Error/Error';
import Modal from '../../../../../UI/Modal/Modal';    

import './shares.scss';

class AddShare extends React.Component {

    state = {
        loading: true,
        error: false,
        shares: [],
        modalForSuccessAdd: false,
        deleteMode: false,
        deleteShare: []
    }

    componentDidMount() {

        axios.get(backendUrl + '/admin/action/get', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res);
            this.setState({ shares: res.data });
        })
        .catch(err => {
            console.log(err);
        })
        .finally(() => {
            this.setState({ loading: false });
            if (this.props.location.state && this.props.location.state.successAdd) {
                this.setState({ modalForSuccessAdd : true });
                this.props.location.state = null;
            }
        });
    }

    onDeleteShareHandler = (id) => {
        this.setState({ loading: true, deleteMode: false });

        axios.delete(backendUrl + `/admin/action/${id}`, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res);
            window.location.reload();
        })
        .catch(err => {
            console.log(err);
        })
        .finally(() => this.setState({ loading: false }));
    }

    render() {

        const modalForSuccessAddedContent = (
            <>
                <p style={{textAlign: 'center'}}>
                    {'   '}
                </p>
            </>
        );

        const modalForDeleteMode = (
            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center'}} >
                <p style={{textAlign: 'center', margin: 0}}>
                    {'    '}
                </p>
                <p style={{textAlign: 'center', fontWeight: 700, margin: 0 }}>
                    {this.state.deleteShare[1]}
                </p>
                <Button 
                onClick={ () => this.onDeleteShareHandler(this.state.deleteShare[0]) }
                style={{ marginTop: '20px' }}
                color="primary"
                variant="outlined">
                    {''}
                </Button>
            </div>
        )

        console.log(this.props);
        console.log(this.state);

        return (
            <>  
            <Modal
            show={this.state.deleteMode}
            modalClosed={() => this.setState({
                deleteMode: false,
                deleteShare: []
            })}>
                {modalForDeleteMode}
            </Modal>
            <Modal
            show={this.state.modalForSuccessAdd}
            modalClosed={() => {
                this.setState({ modalForSuccessAdd: false });
                this.props.history.push('/editor/shares/all');
            }}
            >
                {modalForSuccessAddedContent}
            </Modal>
            {this.state.loading && !this.state.error && <CircularProgress />}
            {this.state.error && !this.state.loading && <ErrorComponent />}
            {!this.state.error && !this.state.loading &&
            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <h1>{''}</h1>
                <div className="masonry-layout">
                    {
                        this.state.shares[0] && this.state.shares.map(action =>
                            <div key={action._id} style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end'}}>
                            <IconButton
                            onClick={
                                () => this.setState({ deleteMode: true, deleteShare: [ action._id, action.title] })
                            } 
                            style={{padding: '5px'}}>
                                <DeleteIcon style={{ color: '#890000'}}/>
                            </IconButton>
                            <div className="masonry-layout__panel" key={action._id}>
                                <div className="masonry-layout__panel__header">
                                    <img src={backendUrl + `/${action.image}`} alt={action.alt}/>
                                </div>
                                <div className="masonry-layout__panel__content">
                                    <h4>{action.title}</h4>
                                    <p>{action.description}</p>
                                </div>
                            </div>
                            </div>
                        )
                    }
                    {!this.state.shares[0] && <p>{'   ,    '}</p>}
                </div>
            </div>
            }
            </>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(AddShare);