import React from 'react';
import axios from 'axios';
import { backendUrl } from '../../../../../../config/config';
import { connect } from 'react-redux';

import AddBlog from './AddBlog/AddBlog';
import ErrorComponent from '../../../../../Errors/Error/Error';
import { CircularProgress } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import MainBlog from './MainBlog/MainBlog';
import pureImg from '../../../../../../assets/page/Main/pureImg.jpg';

import '../SharesSlider/SharesSlider.scss';

class BlogSlider extends React.Component {

    state = {
        loading: false,
        error: false,
        slider: [],

        blogs: null,

        edit: null,
        addMode: false,

        editedArea: {
            label: '',
            description: '',
            blog: null
        }
    }

    componentDidMount() {
        this.setState({ loading: true });

        axios.get(backendUrl + '/posts/', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res.data);
            this.setState({ error: false, blogs: res.data });
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({
                error: true,
                loading: false
            })
        })

        axios.get(backendUrl + '/admin/sliders/6025098384cf183349a6cd2c', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => { 
            console.log(res);
            let slider = res.data.blogs.map((el, index) => ({
                ...el,
                id: index + 1
            }));
            while (slider.length < 4 ) {
                slider.push({
                    id: slider.length + 1,
                    image: pureImg,
                });
            }
            this.setState({
                slider: slider,
                error: false,
                loading: false
            });
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({
                error: true,
                loading: false
            })
        });
    }

    onAddGood = (product) => {
        this.setState({ 
            addMode: false,
            editedArea: {
                ...this.state.editedArea,
                good: product
            }
         });
    }

    editShare = () => {
        let newSlider = this.state.slider;
        newSlider = newSlider.map(el => {
            if (el.id === this.state.edit) {
                return {
                    ...el,
                    label: this.state.editedArea.label,
                    description: this.state.editedArea.description,
                    post: this.state.editedArea.blog
                }
            } else {
                return el;
            }
        });
        this.setState({
            edit: null,
            slider: newSlider,
            editedArea: {
                label: '',
                description: '',
                blog: null
            }
        });
    }

    onEditSlider = () => {
        this.setState({ loading: false });

        const blogs = this.state.slider.map(el => ({
                label: el.label,
                description: el.description,
                post: el.post._id
        }));
        console.log(blogs);

        axios.put(backendUrl + '/admin/sliders/blogs/6025098384cf183349a6cd2c', {
            blogs: blogs
        }, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                window.location.reload();
            })
            .catch(err => {
                console.log(err, err.response);
                this.setState({error: true, loading: false});
            });
    }

    onSelectImage = (el) => {
        this.setState({ addMode: false });
        this.setState({
            editedArea: {
                ...this.state.editedArea,
                blog: el
            }
        });
    }

    render() {

        console.log(this.state);

        const sliderIsValid = this.state.slider.filter(el => el.post).length === 4 ;

        return (
            <div style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                padding: '30px 0'
            }}>
                {this.state.loading && !this.state.error && <CircularProgress /> }
                {!this.state.loading && this.state.error && <ErrorComponent /> }
                {!this.state.loading && !this.state.error && 
                this.state.blogs && 
                <>
                    <AddBlog 
                        posts={this.state.blogs}
                        show={this.state.addMode}
                        onSelectImage={this.onSelectImage}
                        modalClosed={()=>this.setState({addMode: false})}
                    />
                    <h3>{'  '}</h3>
                    <h5>{' :'}</h5>
                    <div style={{width: '70%'}}>
                        <MainBlog slider={this.state.slider} />
                    </div>
                    <h5>{' :'}</h5>
                    <ul className="action-slider-ul">
                        {this.state.slider.map(el => (
                            <li 
                            className="action-slider-ul_label-li" 
                            onClick={() => this.setState({ edit: el.id })}
                            >
                                <h5> {el.id} </h5>
                            </li>
                        ))}
                        <li 
                        className={
                            this.state.edit ? 
                            "action-slider-ul_label-edit label-opened" :
                            "action-slider-ul_label-edit label-closed"
                        }
                        >
                            <h3>{` ${this.state.edit} `}</h3>
                            <p>{'  :'}</p>
                            <textarea 
                                value={this.state.editedArea.label}
                                onChange={(e) => {
                                    this.setState({
                                        editedArea: {
                                            ...this.state.editedArea,
                                            label: e.target.value
                                        }
                                    })
                                }}
                            />
                            <p>{'  :'}</p>
                            <textarea 
                                value={this.state.editedArea.description}
                                onChange={(e) => {
                                    this.setState({
                                        editedArea: {
                                            ...this.state.editedArea,
                                            description: e.target.value
                                        }
                                    })
                                }}
                            />
                            <button onClick={
                                () => this.setState((prevState) => ({
                                    addMode: !prevState.addMode
                                }))
                            }>
                                {'   '}
                            </button>
                            <p>{' :'}</p>
                            {
                               !this.state.editedArea.blog ? 
                               <p style={{marginTop:'0px'}}>{'     ...'}</p> :
                               <img src={backendUrl + '/' + this.state.editedArea.blog.image} />
                            }
                            <button 
                            onClick={this.editShare}
                            disabled={
                                this.state.editedArea.label === '' || this.state.editedArea.description === '' || !this.state.editedArea.blog
                            }
                            >
                                {' '}
                            </button>
                        </li>
                    </ul>
                </>}
                <Button
                    color="primary"
                    variant="contained"
                    onClick={this.onEditSlider}
                    disabled={!sliderIsValid}
                >
                    {' '}
                </Button>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(BlogSlider);