import React from 'react';
import axios from 'axios';
import { backendUrl } from '../../../config/config';
import { NavLink } from 'react-router-dom';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import CircularProgress from '@material-ui/core/CircularProgress';

import { checkValidaty } from '../../../tools/checkValidaty';
import Modal from '../../UI/Modal/Modal';

import '../auth.scss';

const styles = theme => ({
    paper: {
      marginTop: theme.spacing(8),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    avatar: {
      margin: theme.spacing(1),
      backgroundColor: theme.palette.secondary.main,
    },
    form: {
      width: '100%',
      marginTop: theme.spacing(1),
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
    },
    link: {
      color: '#3f51b5',
      '&:hover': {
        textDecoration: 'underline'
      }
    },
    circular: {
      marginLeft: '175px'
    }
  });

class ForgotPassword extends React.Component {

    state = {
        controls: {
            email: {
              value: '',
              validation: {
                required: true,
                isEmail: true
              },
              valid: false,
              touched: false
            }
          },
          formIsValid: false,
          modalOpened: false,
          error: null,
          loading: false
    }

    onInputChangedHandler = (event, controlName) => {
        const updatedControls = {
          ...this.state.controls,
          [controlName]: {
            ...this.state.controls[controlName],
            value: event.target.value,
            touched: true,
            valid: checkValidaty(event.target.value, this.state.controls[controlName].validation)
          }
        }
        let formIsValid = true;
        for (let elem in updatedControls) {
          if (!updatedControls[elem].valid) {
            formIsValid = false;
          }
        }
        this.setState({ controls: updatedControls, formIsValid: formIsValid });
      }

      onCloseModal = () => {
          this.setState({ modalOpened: false });
      }

      onSubmitHandler = (event) => {
        event.preventDefault();
        const defaultState = {
          controls: {
              email: {
                value: '',
                validation: {
                  required: true,
                  isEmail: true
                },
                valid: false,
                touched: false
              }
            },
            formIsValid: false,
            modalOpened: false,
            loading: false,
            error: null
          };
        this.setState({
          ...defaultState,
          loading: true
        })
        
        axios.post(backendUrl + '/admin/recover', {
          email: this.state.controls.email.value
        })
        .then(res => {
          console.log(res, res.data);
          this.setState(defaultState);
          this.setState({ modalOpened: true, loading: false });
        })
        .catch(err => {
          console.log(err, err.response);
          const message = err.response ? err.response.data.error || err.response.data.message : err.toString();
          this.setState({ error: message, loading: false });
        });
      }


    render() {

        const { classes } = this.props;

        const modalContent = (
            <p style={{ textAlign: 'center' }}> {'     '} </p>
        )

        return (
        <>
            <Modal
            show={this.state.modalOpened}
            modalClosed={this.onCloseModal}>
                {modalContent}
            </Modal>
            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    {' '}
                </Typography>

                {this.state.error && 
                <p className="log-in_error-field" >
                    {this.state.error}
                </p>}

                <form className={classes.form} noValidate>
                    <TextField
                    onChange={(event) => this.onInputChangedHandler(event, 'email')}
                    value={this.state.controls.email.value}
                    error={!this.state.controls.email.valid && this.state.controls.email.touched}
                    helperText={!this.state.controls.email.valid && this.state.controls.email.touched ? 'enter correct email' : null}
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    id="email"
                    label="  "
                    autoComplete="email"
                    autoFocus
                    />
                    {this.state.loading ?
                    <CircularProgress className={classes.circular} />
                    :
                    <Button
                    disabled={!this.state.formIsValid}
                    onClick={(e) => this.onSubmitHandler(e)}
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                    >
                    {''}
                    </Button>}
                    <Grid container>
                    <Grid item xs>
                    <NavLink to="/log-in" variant="body2" className={classes.link}>
                        {'  '}
                    </NavLink>
                    </Grid>
                </Grid>
                </form>
                </div>
            </Container>
          </>
        );
    }
}

export default withStyles(styles)(ForgotPassword);