import React from 'react';
import { connect } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { authList, notAuthList } from './list';
import Button from '@material-ui/core/Button';


const Links = ( props ) => {

    const linkStyle = {
        fontSize: '11px'
    }; 

    let content;
    if (props.isAuth) {
        content = authList.map((el, index) => {
            return (
                <NavLink 
                to={el.to} 
                key={index}
                style={linkStyle}>
                    <Button 
                    style={{fontSize: '11px'}}
                    color={el.to === '/logout' ? 'secondary' : 'inherit'}
                    variant={el.to === '/logout' ? 'contained' : null}>
                         {el.label} 
                    </Button>
                </NavLink>
            );
        });
    } else {
        content = notAuthList.map((el, index) => {
            return (
                <NavLink 
                to={el.to} 
                key={index}
                style={linkStyle}>
                    <Button 
                    color='inherit'> 
                        {el.label} 
                    </Button>
                </NavLink>
            );
        })
    }

    return content ;

};

const mapStateToProps = state => ({
    isAuth: state.auth.token !== null
});

export default connect(mapStateToProps)(Links);