import * as actionTypes from '../actions/actionTypes';

const initialState = {
    token: null,
    status: null,
    loading: false,
    error: null
};

const reducer = (state = initialState, action) => {
    switch ( action.type ) {
        case actionTypes.AUTH_START:
            return {
                ...state,
                token: null,
                loading: true,
                error: null
            };
        case actionTypes.AUTH_FAILED:
            return {
                ...state,
                token: null,
                loading: false,
                error: action.error
            };
        case actionTypes.AUTH_LOGOUT:
            return {
                ...state,
                token: null,
                status: null
            }
        case actionTypes.RESET_TOKEN:
            return {
                ...state,
                token: action.token
            };
        case actionTypes.LOG_IN_SUCCESS:
            return {
                ...state,
                loading: false,
                error: null,
                token: action.token,
                status: action.status
            }
        case actionTypes.SET_ADMIN_STATUS:
            return {
                ...state,
                status: action.status
            }
        default: return state;
    }
};

export default reducer;