import React from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { backendUrl } from '../../../../../../config/config';
import { connect } from 'react-redux';
import ErrorComponent from '../../../../../Errors/Error/Error';
import Modal from '../../../../../UI/Modal/Modal';
import SingleComment from '../Comments/SingleComment/SingleComment';

import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import Button from '@material-ui/core/Button';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import DoneIcon from '@material-ui/icons/Done';
import DeleteIcon from '@material-ui/icons/Delete';
import { CircularProgress } from '@material-ui/core';

class Comments extends React.Component {

    state = {
        loading: true,
        error: false,
        comments: [],
        deleteComm: null,
        editedComm: null,
        modalDelete: false,
        modalPass: false
    }

    componentDidMount() {
        axios.get(backendUrl + '/admin/comment/', {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res);
            this.setState({ error: false, comments: res.data });
        })
        .catch(err => {
            console.log(err, err.response);
            this.setState({ error: true });
        })
        .finally(() => this.setState({loading: false}));
    }

    onPassComment = () => {
        this.setState({ loading: true, modalPass: false });

        axios.put(backendUrl + `/admin/comment/${this.state.editedComm}`, {}, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
            .then(res => {
                console.log(res);
                window.location.reload();
            })
            .catch(err => {
                console.log(err, err.response);
                this.setState({ 
                    loading: false, 
                    error: true,
                    editedComm: null
                 });
            })
    }

    onDeleteComment = () => {
        this.setState({ loading: true, modalDelete: false });

        axios.delete(backendUrl + `/admin/comment/${this.state.deleteComm}`, {
            headers: {
                'Authorization': `Bearer ${this.props.token}`
            }
        })
        .then(res => {
            console.log(res);
            window.location.reload();
        })
        .catch(err => {
            console.log(err);
            this.setState({ 
                loading: false,
                error: true,
                deleteComm: null
            });
        })
    }

    render() {

        let newComments = [];
        let moderatedComments = []
        if (this.state.comments[0]) {
            newComments = this.state.comments.filter(el => el.status === 'New');
            moderatedComments = this.state.comments.filter(el => el.status !== 'New');
        };

        return (
            <div style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                padding: '30px 0'
            }}>
                <Modal
                    show={this.state.modalPass}
                    modalClosed={() => this.setState({
                        modalPass: false,
                        editedComm: null
                    })}
                >
                    <div style={{textAlign:'center'}}>
                        <p>{'     ?'}</p>
                        <Button
                            onClick={this.onPassComment}
                            color="primary"
                            variant="outlined"
                        >
                            {''}
                        </Button>
                    </div>
                </Modal>
                <Modal
                    show={this.state.modalDelete}
                    modalClosed={()=>this.setState({
                        modalDelete: false,
                        deleteComm: null
                    })}
                >
                    <div style={{textAlign:'center'}}>
                        <p>{'     ?'}</p>
                        <Button
                            onClick={this.onDeleteComment}
                            color="primary"
                            variant="outlined"
                        >
                            {''}
                        </Button>
                    </div>
                </Modal>
                { this.state.loading && !this.state.error && <CircularProgress /> }
                { this.state.error && <ErrorComponent /> }
                { this.state.comments &&
                    <>
                        <h3>{' '}</h3>
                        <TableContainer component={Paper} style={{width: '90%'}}>
                            <Table size="small" aria-label="a dense table">
                                <TableHead>
                                <TableRow>
                                    <TableCell align="left">
                                        {'  '}
                                    </TableCell>
                                    <TableCell align="left">
                                        {''}
                                    </TableCell>
                                    <TableCell align="center">
                                        {''}
                                    </TableCell>
                                    <TableCell align="center">
                                        {''}   
                                    </TableCell>
                                </TableRow>
                                </TableHead>
                                <TableBody>
                                {newComments.map((row) => (
                                    <TableRow key={row._id}>
                                    <TableCell align="left">
                                        <Link
                                            style={{textDecoration:'underline'}} 
                                            to={`/editor/blog/edit/${row.postId._id}`}
                                        > 
                                            {''} 
                                        </Link>
                                    </TableCell>
                                    <TableCell style={{width: '400px'}}>
                                        <SingleComment
                                            data={row.date} 
                                            content={row.content}
                                            name={row.userId.name} 
                                        />
                                    </TableCell>
                                    <TableCell align="center">
                                        <DoneIcon 
                                            onClick={() => this.setState({
                                                modalPass: true,
                                                editedComm: row._id
                                            })}
                                            style={{cursor:'pointer',color:'forestgreen'}}
                                        />
                                    </TableCell>
                                    <TableCell align="center">
                                        <DeleteIcon 
                                            onClick={()=>this.setState({
                                                modalDelete: true,
                                                deleteComm: row._id
                                            })}
                                            style={{cursor:'pointer',color:'brown'}}
                                        />
                                    </TableCell>
                                    </TableRow>
                                ))}
                                </TableBody>
                            </Table>
                        </TableContainer>


                        <h3 style={{marginTop:'40px'}}>{'  '}</h3>
                        <TableContainer component={Paper} style={{width: '90%'}}>
                            <Table size="small" aria-label="a dense table">
                                <TableHead>
                                <TableRow>
                                    <TableCell align="left">
                                        {'  '}
                                    </TableCell>
                                    <TableCell align="left">
                                        {''}
                                    </TableCell>
                                    <TableCell align="center">
                                        {''}   
                                    </TableCell>
                                </TableRow>
                                </TableHead>
                                <TableBody>
                                {moderatedComments.map((row) => (
                                    <TableRow key={row._id}>
                                    <TableCell align="left">
                                        <Link
                                            style={{textDecoration:'underline'}} 
                                            to={row.postId ? `/editor/blog/edit/${row.postId._id}` : '/'}
                                        > 
                                            {''} 
                                        </Link>
                                    </TableCell>
                                    <TableCell style={{width: '400px'}}>
                                        <SingleComment
                                            data={row.date} 
                                            content={row.content}
                                            name={row.userId.name} 
                                        />
                                    </TableCell>
                                    <TableCell align="center">
                                        <DeleteIcon 
                                            onClick={()=>this.setState({
                                                modalDelete: true,
                                                deleteComm: row._id
                                            })}
                                            style={{cursor:'pointer',color:'brown'}}
                                        />
                                    </TableCell>
                                    </TableRow>
                                ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </>
                }
            </div>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(Comments);