import React, { useEffect, useState } from 'react';
import { backendUrl } from '../../../../config/config';
import axios from 'axios';
import { connect } from 'react-redux';

import Seo from '../../../SEO/SEO';
import ErrorComponent from '../../../Errors/Error/Error';
import { CircularProgress } from '@material-ui/core';

const SeoEdit = (props) => {
    
    const [ loading, setLoading ] = useState(false);
    const [ error, setError ] = useState(false);
    const [ seo, setSeo ] = useState(null);
    const [ seoId, setSeoId ] = useState(null);

    useEffect(() => {
      //  window.location.reload();
        const name = props.name;
        setLoading(true);
        setError(false);

        axios.get(backendUrl + `/admin/seo/name/${name}`, {
            headers: {
                'Authorization': `Bearer ${props.token}`
            }
        })
            .then(res => {
                console.log(res);
                setSeo({
                    title: res.data.title,
                    keywords: res.data.keywords,
                    description: res.data.description,
                    image: res.data.image,
                    alt: res.data.alt
                });
                setSeoId(res.data._id);
                setError(false);
            })
            .catch(err => {
                console.log(err, err.response);
                setError(true);
            })
            .finally(() => {
                setLoading(false);
            });
    }, [props]);

    const onExportData = (obj) => {
        setLoading(true);

        if (obj.uploadedFile.file) {

            let formData = new FormData();

            formData.append('title', obj.title);
            formData.append('description', obj.description);
            formData.append('keywords', obj.keywords);
            formData.append('image', obj.uploadedFile.file);
            formData.append('alt', obj.alt);

            axios.put(backendUrl + `/admin/seo/${seoId}`, formData, {
                headers: {
                    'Authorization': `Bearer ${props.token}`
                }
            })
            .then(res => {
                console.log(res);
                props.history.push('/pages');
            })
            .catch(err => {
                console.log(err, err.response);
                setError(true);
                setLoading(false);
            });

        } else {

            axios.put(backendUrl + `/admin/seo/no/image/${seoId}`,{
                title: obj.title,
                description: obj.description,
                keywords: obj.keywords,
                alt: obj.alt
            }, {
                headers: {
                    'Authorization': `Bearer ${props.token}`
                } 
            })
                .then(res => {
                    console.log(res);
                    props.history.push('/pages');
                })
                .catch(err => {
                    console.log(err);
                    setError(true);
                    setLoading(false)
                })
        }

    }


    return (
        <div style={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            padding: '30px 0'
        }}>
            { error && !loading && <ErrorComponent /> }
            { !error && loading && <CircularProgress /> }
            { !error && !loading && seo &&
            <> 
             <h1>{'SEO   '}</h1>
             <Seo 
                image
                seo={seo}
                onExportData={onExportData}
             />
            </>
            }
        </div>
    );
};

const mapStateToProps = state => ({
    token: state.auth.token
});

export default connect(mapStateToProps)(SeoEdit);